<template>
  <div class="ivu-description-list" :class="{ 'ivu-description-list-vertical': layout === 'vertical' }">
    <div class="ivu-description-list-title" v-if="title || $slots.title">
      <slot name="title">{{ title }}</slot>
    </div>
    <Row :gutter="gutter">
      <slot></slot>
    </Row>
  </div>
</template>
<script>
  import { Row } from 'view-ui-plus'

  export default {
    name: 'DescriptionList',
    components: { Row },
    provide() {
      return {
        DescriptionListInstance: this
      }
    },
    props: {
      layout: {
        validator(value) {
          return ['horizontal', 'vertical'].includes(value)
        },
        default: 'horizontal'
      },
      title: {
        type: String,
        default: undefined
      },
      gutter: {
        type: Number,
        default: 32
      },
      col: {
        validator(value) {
          return [1, 2, 3, 4].includes(value)
        },
        default: 3
      }
    }
  }
</script>
