import axios from 'axios'
import { Message, Notice } from 'view-ui-plus'
import store from '@/store'
import config from '@/config'
import router from '@/router'
import { storage } from '@/utils/storage'
import { ACCESS_TOKEN, pushLog } from '@/store/mutation-types'

const axiosInstance = axios.create({
  baseURL: config.routerBase + config.apiBaseURL,
  timeout: 1000 * 5
})

axiosInstance.interceptors.request.use(
  config => {
    const token = storage.get(ACCESS_TOKEN)
    token && (config.headers['Access-Token'] = token)
    return config
  },
  error => Promise.reject(error)
)

axiosInstance.interceptors.response.use(
  response => {
    const data = response.data
    const code = data.code
    if (!code) return data
    switch (code) {
      case 200:
        return data.data
      case 401:
        Message.error({ content: data.msg, duration: config.modalDuration })
        router.replace({ name: 'login' })
        break
      case 404:
        router.replace({ name: '404' })
        break
      case 500:
        if (data.msg === '验证码错误') {
          router.replace({ name: 'login' })
          Message.error({ content: data.msg, duration: config.modalDuration })
          break
        }
        throwError(`${data.msg}: ${response.config.url}`)
        break
      default:
        throwError(`${data.msg}: ${response.config.url}`)
        break
    }
  },
  error => {
    const { baseURL, url } = error.response.config
    if (error && error.response) {
      switch (error.response.status) {
        case 400:
          error.message = '请求错误'
          break
        case 401:
          error.message = '未授权，请登录'
          break
        case 403:
          error.message = '拒绝访问'
          break
        case 404:
          error.message = `请求地址出错: ${baseURL}${url}`
          break
        case 408:
          error.message = '请求超时'
          break
        case 500:
          error.message = '服务器内部错误'
          break
        case 501:
          error.message = '服务未实现'
          break
        case 502:
          error.message = '网关错误'
          break
        case 503:
          error.message = '服务不可用'
          break
        case 504:
          error.message = '网关超时'
          break
        case 505:
          error.message = 'HTTP版本不受支持'
          break
        default:
          break
      }
    }
    handleError(error)
    return Promise.reject(error)
  }
)

const throwError = error => {
  const errorMsg = new Error(error)
  handleError(errorMsg)
  throw errorMsg
}

const handleError = error => {
  errorPrompt(error)
  if (!config.catchErrorLog) return
  const { baseURL, url } = error.config
  store.dispatch(pushLog, {
    message: '数据请求异常',
    sources: 'axios',
    type: 'error',
    meta: { error, apiURL: baseURL + url }
  })
}

export const errorPrompt = error => {
  if (config.errorModalType === 'Message') {
    Message.error({ content: error.message, duration: config.modalDuration })
  } else if (config.errorModalType === 'Notice') {
    Notice.error({ title: '系统提示', desc: error.message, duration: config.modalDuration })
  }
}

axiosInstance.install = app => (app.config.globalProperties.$axios = axiosInstance)

export default axiosInstance
