<script>
  import { h, Fragment, Comment, Text } from 'vue'
  import { Divider } from 'view-ui-plus'

  const spaceSize = {
    small: 8,
    middle: 16,
    large: 24
  }

  export default {
    name: 'Space',
    props: {
      size: {
        type: [String, Number, Array],
        validator: value => {
          const sizes = ['small', 'middle', 'large']
          if (typeof value === 'string') {
            return sizes.includes(value)
          }
          if (Array.isArray(value)) {
            return value.length && value.every(i => typeof i === 'number' || sizes.includes(i))
          }
          return true
        },
        default: 'small'
      },
      direction: {
        type: String,
        validator: value => ['horizontal', 'vertical'].includes(value),
        default: 'horizontal'
      },
      align: {
        type: String,
        validator: value => ['start', 'end', 'center', 'baseline', 'stretch'].includes(value)
      },
      wrap: {
        type: Boolean,
        default: false
      },
      split: {
        type: Boolean,
        default: false
      },
      type: {
        type: String,
        validator: value => ['inline-flex', 'flex'].includes(value),
        default: 'inline-flex'
      }
    },
    computed: {
      mergedAlign() {
        if (!this.align) {
          if (this.direction === 'horizontal') return 'center'
          if (this.type === 'flex') return 'stretch'
        }
        return this.align
      },
      classes() {
        return [
          'ivu-space',
          `ivu-space-${this.direction}`,
          {
            'ivu-space-flex': this.type === 'flex',
            'ivu-space-wrap': this.wrap,
            [`ivu-space-${this.mergedAlign}`]: this.mergedAlign
          }
        ]
      },
      itemStyle() {
        let style = {}
        if (['number', 'string'].includes(typeof this.size)) {
          style = {
            [this.direction === 'vertical' ? 'marginBottom' : 'marginRight']: this.getSize(this.size)
          }
        }
        if (Array.isArray(this.size) && this.size.length) {
          style = {
            [this.direction === 'vertical' ? 'marginBottom' : 'marginRight']: this.getSize(this.size[0])
          }
        }
        return style
      },
      wrapStyle() {
        const style = {}
        if (this.size.length > 1) {
          // FIXME: gap兼容性不好，慎用
          style.rowGap = this.getSize(this.size[1])
        }
        return style
      }
    },
    methods: {
      getSize(size) {
        return `${typeof size === 'string' ? spaceSize[size] : size ?? 8}px`
      },
      filterEmpty(children = []) {
        const res = []
        children.forEach(child => {
          if (Array.isArray(child)) {
            res.push(...child)
          } else if (child.type === Fragment) {
            res.push(...child.children)
          } else {
            res.push(child)
          }
        })
        return res.filter(c => !this.isEmptyElement(c))
      },
      isEmptyElement(c) {
        return c && (c.type === Comment || (c.type === Fragment && c.children.length === 0) || (c.type === Text && c.children.trim() === ''))
      }
    },
    render() {
      const items = this.filterEmpty(this.$slots.default() ?? [])
      const len = items.length

      if (len === 0) return null

      let split = null

      if (this.split && !this.$slots.split) {
        split = h(Divider, { type: 'vertical' })
      } else if (this.$slots.split) {
        split = this.$slots.split()
      }

      return h(
        'div',
        { class: this.classes, style: this.wrapStyle },
        items.map((child, index) => {
          const item = h(
            'div',
            {
              class: 'ivu-space-item',
              style: index + 1 < len ? this.itemStyle : {}
            },
            [child]
          )

          if (split && index + 1 < len) {
            return [item, h('div', { class: 'ivu-space-split' }, [split])]
          }

          return item
        })
      )
    }
  }
</script>
