<template>
  <Icon v-if="!!type" :type="iconType" :size="iconSize" :custom="iconCustom" v-bind="$attrs" />
</template>

<script>
  import { Icon } from 'view-ui-plus'

  const separator = ' '

  export default {
    name: 'XIcon',
    components: {
      Icon
    },
    props: {
      type: {
        type: String,
        default: ''
      },
      size: {
        type: [String, Number],
        default: undefined
      }
    },
    computed: {
      isCustom() {
        return this.type.includes(separator)
      },
      iconType() {
        return (!this.isCustom && this.type) || undefined
      },
      iconSize() {
        return this.size || (this.isCustom && 12) || undefined
      },
      iconCustom() {
        if (this.type.includes(separator)) {
          const [iconPrefix, iconSuffix] = this.type.split(separator)
          return `${iconPrefix} ${iconPrefix}-${iconSuffix}`
        }
        return undefined
      }
    }
  }
</script>
