import store from '@/store'
import config from '@/config'
import { errorPrompt } from '@/utils/axios'
import { pushLog } from '@/store/mutation-types'

export default {
  install(Vue) {
    if (!config.catchErrorLog) return
    Vue.config.errorHandler = (error, vm, payload) => {
      errorPrompt({ message: config.catchErrorMsg })
      Vue.nextTick(() =>
        store.dispatch(pushLog, {
          message: `${payload}: ${error.message}`,
          sources: 'vueHandler',
          type: 'error',
          meta: { error }
        })
      )
    }
    Vue.config.warnHandler = (msg, vm, trace) => {
      // `trace` 是组件的继承关系追踪
      Vue.nextTick(() =>
        store.dispatch(pushLog, {
          message: `${msg} ${trace}`,
          sources: 'vueHandler',
          type: 'warn',
          meta: { msg }
        })
      )
    }
  }
}
