<template>
  <div class="ivu-verify-point">
    <div class="verify-img-panel" :style="imgStyleOuter">
      <img ref="canvas" :src="`data:image/png;base64,${pointBackImgBase}`" @click="bindingClick ? canvasClick($event) : undefined" />
      <div class="verify-refresh" @click="refresh" v-show="showRefresh">
        <Icon type="reload" />
      </div>
      <div :key="index" :style="pointIndexStyle(tempPoint)" v-for="(tempPoint, index) in tempPoints">
        {{ index + 1 }}
      </div>
    </div>
    <div class="verify-bar-area" :style="barAreaStyle">
      <span>{{ text }}</span>
    </div>
  </div>
</template>
<script type="text/babel">
  /**
   * VerifyPoint
   * @description 点选
   * */
  import { Icon } from 'view-ui-plus'
  import { resetSize } from '../utils/util'
  import { aesEncrypt, reqGet, reqCheck } from '../utils/ase'

  export default {
    name: 'VerifyPoint',
    components: { Icon },
    inject: ['Verify'],
    props: {
      // 弹出式pop，固定fixed
      mode: {
        type: String,
        default: 'fixed'
      },
      // 间隔
      vSpace: {
        type: Number,
        default: 5
      },
      imgSize: {
        type: Object,
        default: () => ({ width: '310px', height: '155px' })
      },
      barSize: {
        type: Object,
        default: () => ({ width: '310px', height: '40px' })
      }
    },
    data() {
      return {
        captchaType: 'clickWord',
        secretKey: '', // 后端返回的ase加密秘钥
        checkNum: 3, // 默认需要点击的字数
        fontPos: [], // 选中的坐标信息
        checkPosArr: [], // 用户点击的坐标
        num: 1, // 点击的记数
        pointBackImgBase: '', // 后端获取到的背景图片
        poinTextList: [], // 后端返回的点击字体顺序
        backToken: '', // 后端返回的token值
        setSize: {
          imgHeight: 0,
          imgWidth: 0,
          barHeight: 0,
          barWidth: 0
        },
        tempPoints: [],
        text: '',
        barAreaColor: undefined,
        barAreaBorderColor: undefined,
        showRefresh: true,
        bindingClick: true
      }
    },
    computed: {
      resetSize() {
        return resetSize
      },
      imgStyleOuter() {
        return {
          width: this.setSize.imgWidth,
          height: this.setSize.imgHeight,
          backgroundSize: `${this.setSize.imgWidth} ${this.setSize.imgHeight}`,
          marginBottom: `${this.vSpace}px`
        }
      },
      barAreaStyle() {
        return {
          width: this.setSize.imgWidth,
          color: this.barAreaColor,
          borderColor: this.barAreaBorderColor,
          lineHeight: this.barSize.height
        }
      },
      pointIndexStyle() {
        return tempPoint => ({
          backgroundColor: '#1abd6c',
          color: '#fff',
          zIndex: 9999,
          width: '20px',
          height: '20px',
          textAlign: 'center',
          lineHeight: '20px',
          borderRadius: '50%',
          position: 'absolute',
          top: `${tempPoint.y - 10}px`,
          left: `${tempPoint.x - 10}px`
        })
      }
    },
    watch: {
      // type变化则全面刷新
      type: {
        immediate: true,
        handler() {
          this.init()
        }
      }
    },
    mounted() {
      // 禁止拖拽
      this.$el.onselectstart = () => false
    },
    methods: {
      init() {
        // 加载页面
        this.fontPos.splice(0, this.fontPos.length)
        this.checkPosArr.splice(0, this.checkPosArr.length)
        this.num = 1
        this.getPicture()
        this.$nextTick(() => {
          this.setSize = this.resetSize(this) // 重新设置宽度高度
          this.Verify.$emit('on-ready', this)
        })
      },
      canvasClick(e) {
        this.checkPosArr.push(this.getMousePos(this.$refs.canvas, e))
        // eslint-disable-next-line eqeqeq
        if (this.num === this.checkNum) {
          this.num = this.createPoint(this.getMousePos(this.$refs.canvas, e))
          // 按比例转换坐标值
          this.checkPosArr = this.pointTransform(this.checkPosArr, this.setSize)
          // 等创建坐标执行完
          setTimeout(() => {
            // const flag = this.comparePos(this.fontPos, this.checkPosArr);
            // 发送后端请求
            const captchaVerification = this.secretKey
              ? aesEncrypt(`${this.backToken}---${JSON.stringify(this.checkPosArr)}`, this.secretKey)
              : `${this.backToken}---${JSON.stringify(this.checkPosArr)}`
            const data = {
              captchaType: this.captchaType,
              pointJson: this.secretKey ? aesEncrypt(JSON.stringify(this.checkPosArr), this.secretKey) : JSON.stringify(this.checkPosArr),
              token: this.backToken
            }
            reqCheck(data).then(res => {
              if (res.repCode === '0000') {
                this.barAreaColor = '#4cae4c'
                this.barAreaBorderColor = '#5cb85c'
                this.text = '验证成功'
                this.bindingClick = false
                this.Verify.$emit('on-success', { captchaVerification })
              } else {
                this.Verify.$emit('on-fail', this)
                this.barAreaColor = '#d9534f'
                this.barAreaBorderColor = '#d9534f'
                this.text = '验证失败'
                setTimeout(() => {
                  this.refresh()
                }, 700)
              }
            })
          }, 400)
        }
        if (this.num < this.checkNum) {
          this.num = this.createPoint(this.getMousePos(this.$refs.canvas, e))
        }
      },

      // 获取坐标
      getMousePos(obj, e) {
        const x = e.offsetX
        const y = e.offsetY
        return { x, y }
      },
      // 创建坐标点
      createPoint(pos) {
        this.tempPoints.push({ ...pos })
        return ++this.num
      },
      refresh() {
        this.tempPoints.splice(0, this.tempPoints.length)
        this.barAreaColor = '#000'
        this.barAreaBorderColor = '#ddd'
        this.bindingClick = true
        this.fontPos.splice(0, this.fontPos.length)
        this.checkPosArr.splice(0, this.checkPosArr.length)
        this.num = 1
        this.getPicture()
        this.showRefresh = true
      },

      // 请求背景图片和验证图片
      getPicture() {
        const data = { captchaType: this.captchaType }
        reqGet(data)
          .then(res => {
            if (res.repCode === '0000') {
              this.pointBackImgBase = res.repData.originalImageBase64
              this.backToken = res.repData.token
              this.secretKey = res.repData.secretKey
              this.poinTextList = res.repData.wordList
              this.text = `请依次点击【${this.poinTextList.join('，')}】`
            } else {
              this.text = res.repMsg
            }
          })
          .catch(error => this.Verify.$emit('on-error', error))
      },
      // 坐标转换函数
      pointTransform(pointArr, imgSize) {
        return pointArr.map(p => {
          const x = Math.round((310 * p.x) / parseInt(imgSize.imgWidth))
          const y = Math.round((155 * p.y) / parseInt(imgSize.imgHeight))
          return { x, y }
        })
      }
    }
  }
</script>
<style lang="less">
  .ivu-verify-point {
    position: relative;
    overflow: hidden;

    .verify-img-panel {
      position: relative;
      overflow: hidden;

      img {
        width: 100%;
        height: 100%;
        display: block;
      }

      .verify-refresh {
        position: absolute;
        top: 0;
        right: 2px;
        z-index: 2;
        padding: 4px;
        cursor: pointer;

        .anticon {
          color: #fff;
          font-size: 16px;
        }
      }
    }

    .verify-bar-area {
      position: relative;
      text-align: center;
      border: 1px solid #ddd;
      border-radius: 2px;
    }
  }
</style>
