<template>
  <div :class="[prefixCls, device]">
    <div :class="`${prefixCls}-header`">
      <div :class="`${prefixCls}-header-logo`">
        <SvgLogo />
      </div>
      <article :class="`${prefixCls}-typography`">
        <h1>{{ $config.titleSuffix }}</h1>
        <div :class="`${prefixCls}-typography`">{{ $config.description }}</div>
      </article>
    </div>

    <router-view />
    <Copyright />
  </div>
</template>

<script>
  import { SvgLogo } from '@/components'
  import { Copyright } from './components'
  import { mixinDevice } from '@/utils/mixin'

  export default {
    name: 'LoginLayout',
    components: { Copyright, SvgLogo },
    mixins: [mixinDevice],
    data() {
      return {
        prefixCls: `${this.$config.cssPrefix}login`
      }
    }
  }
</script>
