<template>
  <div class="bytemd-wrapper">
    <Editor :locale="zhHans" :value="content" :plugins="plugins" v-bind="$attrs" @change="handleChange" />
  </div>
</template>

<script>
  import { defineAsyncComponent } from 'vue'

  export default {
    name: 'ByteMdEditor',
    emits: ['update:modelValue'],
    components: {
      Editor: defineAsyncComponent(() => import(/* webpackChunkName: "async-bytemd" */ '@bytemd/vue-next').then(res => res.Editor))
    },
    props: {
      modelValue: String
    },
    data() {
      return {
        zhHans: null,
        plugins: [],
        content: this.modelValue
      }
    },
    watch: {
      modelValue(newValue) {
        this.content = newValue
      }
    },
    async mounted() {
      await import(/* webpackChunkName: "async-bytemd" */ 'bytemd/dist/index.css')
      await import(/* webpackChunkName: "async-bytemd" */ 'bytemd/locales/zh_Hans.json').then(res => (this.zhHans = res.default))
      await import(/* webpackChunkName: "async-bytemd" */ '@bytemd/plugin-gfm').then(res => this.plugins.push(res.default))
    },
    methods: {
      handleChange(content) {
        this.content = content
        this.$emit('update:modelValue', content)
      }
    }
  }
</script>
<style lang="less">
  .bytemd-wrapper {
    .bytemd {
      &.bytemd-fullscreen {
        z-index: 2000;
      }

      .bytemd-toolbar-right > .bytemd-toolbar-icon:last-child {
        display: none;
      }
    }
  }
</style>
