<template>
  <div ref="containerRef" class="ivu-watermark">
    <slot></slot>
  </div>
</template>

<script>
  export default {
    name: 'Watermark',
    props: {
      width: {
        type: Number,
        default: undefined
      },
      height: {
        type: Number,
        default: undefined
      },
      gap: {
        type: Array,
        default: () => [100, 100]
      },
      content: {
        type: [String, Array],
        default: undefined
      },
      image: {
        type: String,
        default: undefined
      },
      zIndex: {
        type: Number,
        default: 2000
      },
      rotate: {
        type: Number,
        default: -22
      },
      font: {
        type: Object,
        default: () => ({
          color: 'rgba(0,0,0,.15)',
          fontSize: 16,
          fontWeight: 'normal',
          fontStyle: 'normal',
          fontFamily: 'sans-serif'
        })
      },
      offset: {
        type: Array,
        default: () => []
      }
    },
    data() {
      return {
        containerRef: null,
        watermarkRef: null,
        observer: null,
        reRender: 0,
        baseSize: 2,
        fontGap: 3
      }
    },
    watch: {
      reRender() {
        this.destroyWatermark()
        this.renderWatermark()
      },
      content() {
        this.destroyWatermark()
        this.renderWatermark()
      }
    },
    mounted() {
      this.containerRef = this.$refs.containerRef
      this.observer = new MutationObserver(records => {
        for (const record of records) {
          for (const node of record.removedNodes) {
            if (node === this.watermarkRef) {
              this.reRender++
              break
            }
          }
          if (record.target === this.watermarkRef) {
            this.reRender++
            break
          }
        }
      })
      this.observer &&
        this.observer.observe(this.containerRef, {
          childList: true,
          attributes: true,
          subtree: true
        })
      this.renderWatermark()
    },
    beforeUnmount() {
      this.observer && this.observer.disconnect()
    },
    methods: {
      getPixelRatio() {
        return window.devicePixelRatio || 1
      },
      getMarkSize(ctx) {
        let defaultWidth = 120
        let defaultHeight = 64

        const {
          width,
          height,
          content,
          image,
          font: { fontSize, fontFamily },
          fontGap
        } = this

        if (!image && ctx.measureText) {
          ctx.font = `${Number(fontSize)}px ${fontFamily}`
          const contents = Array.isArray(content) ? content : [content]
          const widths = contents.map(item => ctx.measureText(item).width)
          defaultWidth = Math.ceil(Math.max(...widths))
          defaultHeight = Number(fontSize) * contents.length + (contents.length - 1) * fontGap
        }
        return [width ?? defaultWidth, height ?? defaultHeight]
      },
      rotateWatermark(ctx, rotateX, rotateY, rotate) {
        ctx.translate(rotateX, rotateY)
        ctx.rotate((Math.PI / 180) * Number(rotate))
        ctx.translate(-rotateX, -rotateY)
      },
      getStyleStr(style) {
        return Object.keys(style)
          .map(key => {
            const toLowercaseSeparator = key => key.replace(/([A-Z])/g, '-$1').toLowerCase()
            return `${toLowercaseSeparator(key)}: ${style[key]};`
          })
          .join(' ')
      },
      getMarkStyle() {
        const { zIndex, gap, offset } = this
        const markStyle = {
          zIndex,
          position: 'absolute',
          top: 0,
          left: 0,
          width: '100%',
          height: '100%',
          userSelect: 'none',
          pointerEvents: 'none',
          backgroundRepeat: 'repeat'
        }

        const [gapX, gapY] = gap
        const gapXCenter = gapX / 2
        const gapYCenter = gapY / 2
        const offsetLeft = offset?.[0] ?? gapXCenter
        const offsetTop = offset?.[1] ?? gapYCenter

        /** Calculate the style of the offset */
        let positionLeft = offsetLeft - gapXCenter
        let positionTop = offsetTop - gapYCenter
        if (positionLeft > 0) {
          markStyle.left = `${positionLeft}px`
          markStyle.width = `calc(100% - ${positionLeft}px)`
          positionLeft = 0
        }
        if (positionTop > 0) {
          markStyle.top = `${positionTop}px`
          markStyle.height = `calc(100% - ${positionTop}px)`
          positionTop = 0
        }
        markStyle.backgroundPosition = `${positionLeft}px ${positionTop}px`

        return markStyle
      },
      appendWatermark(base64Url, markWidth) {
        if (this.containerRef && this.watermarkRef) {
          const {
            gap: [gapX, gapY],
            baseSize
          } = this
          this.watermarkRef.setAttribute(
            'style',
            this.getStyleStr({
              ...this.getMarkStyle(),
              backgroundImage: `url('${base64Url}')`,
              backgroundSize: `${(gapX + markWidth) * baseSize}px`
            })
          )
          this.containerRef?.append(this.watermarkRef)
        }
      },
      drawText(canvas, ctx, drawX, drawY, drawWidth, drawHeight, alternateRotateX, alternateRotateY, alternateDrawX, alternateDrawY, markWidth) {
        const { rotate } = this
        this.fillTexts(ctx, drawX, drawY, drawWidth, drawHeight)
        /** Fill the interleaved text after rotation */
        ctx.restore()
        this.rotateWatermark(ctx, alternateRotateX, alternateRotateY, rotate)
        this.fillTexts(ctx, alternateDrawX, alternateDrawY, drawWidth, drawHeight)
        this.appendWatermark(canvas.toDataURL(), markWidth)
      },
      fillTexts(ctx, drawX, drawY, drawWidth, drawHeight) {
        const {
          content,
          fontGap,
          font: { color, fontSize, fontStyle, fontWeight, fontFamily }
        } = this
        const ratio = this.getPixelRatio()
        const mergedFontSize = Number(fontSize) * ratio
        ctx.font = `${fontStyle} normal ${fontWeight} ${mergedFontSize}px/${drawHeight}px ${fontFamily}`
        ctx.fillStyle = color
        ctx.textAlign = 'center'
        ctx.textBaseline = 'top'
        ctx.translate(drawWidth / 2, 0)
        const contents = Array.isArray(content) ? content : [content]
        contents?.forEach((item, index) => {
          ctx.fillText(item ?? '', drawX, drawY + index * (mergedFontSize + fontGap * ratio))
        })
      },
      renderWatermark() {
        if (!this.content) return
        const canvas = document.createElement('canvas')
        const ctx = canvas.getContext('2d')
        if (ctx && !this.watermarkRef) {
          this.watermarkRef = document.createElement('div')
        }

        const { image, rotate, gap, baseSize } = this
        const [gapX, gapY] = gap
        const ratio = this.getPixelRatio()
        const [markWidth, markHeight] = this.getMarkSize(ctx)
        const canvasWidth = (gapX + markWidth) * ratio
        const canvasHeight = (gapY + markHeight) * ratio
        canvas.setAttribute('width', `${canvasWidth * baseSize}px`)
        canvas.setAttribute('height', `${canvasHeight * baseSize}px`)

        const drawX = (gapX * ratio) / 2
        const drawY = (gapY * ratio) / 2
        const drawWidth = markWidth * ratio
        const drawHeight = markHeight * ratio
        const rotateX = (drawWidth + gapX * ratio) / 2
        const rotateY = (drawHeight + gapY * ratio) / 2
        /** Alternate drawing parameters */
        const alternateDrawX = drawX + canvasWidth
        const alternateDrawY = drawY + canvasHeight
        const alternateRotateX = rotateX + canvasWidth
        const alternateRotateY = rotateY + canvasHeight

        ctx.save()
        this.rotateWatermark(ctx, rotateX, rotateY, rotate)

        if (image) {
          const img = new Image()
          img.onload = () => {
            ctx.drawImage(img, drawX, drawY, drawWidth, drawHeight)
            /** Draw interleaved pictures after rotation */
            ctx.restore()
            this.rotateWatermark(ctx, alternateRotateX, alternateRotateY, rotate)
            ctx.drawImage(img, alternateDrawX, alternateDrawY, drawWidth, drawHeight)
            this.appendWatermark(canvas.toDataURL(), markWidth)
          }
          img.onerror = () =>
            this.drawText(
              canvas,
              ctx,
              drawX,
              drawY,
              drawWidth,
              drawHeight,
              alternateRotateX,
              alternateRotateY,
              alternateDrawX,
              alternateDrawY,
              markWidth
            )
          img.crossOrigin = 'anonymous'
          img.referrerPolicy = 'no-referrer'
          img.src = image
        } else {
          this.drawText(
            canvas,
            ctx,
            drawX,
            drawY,
            drawWidth,
            drawHeight,
            alternateRotateX,
            alternateRotateY,
            alternateDrawX,
            alternateDrawY,
            markWidth
          )
        }
      },
      destroyWatermark() {
        if (this.watermarkRef) {
          this.watermarkRef.remove()
          this.watermarkRef = null
        }
      }
    }
  }
</script>
<style lang="less">
  .ivu-watermark {
    position: relative;
  }
</style>
