import { isIE } from '@/utils/util'

// 判断环境不是 prod 或者 preview 是 true 时，加载 mock 服务
if (process.env.NODE_ENV !== 'production' || process.env.VUE_APP_PREVIEW === 'true') {
  if (isIE()) {
    console.error('[admin-pro] ERROR: `mockjs` NOT SUPPORT `IE` PLEASE DO NOT USE IN `production` ENV.')
  }
  // 使用同步加载依赖
  // 防止 vuex 中的 getUserInfo 早于 mock 运行，导致无法 mock 请求返回结果
  // console.log('[admin-pro] mock mounting')
  const Mock = require('mockjs2')
  require('./services/auth')
  require('./services/user')
  require('./services/manage')
  require('./services/other')
  require('./services/article')
  Mock.mock(/\/save_error_logger/, 'success')
  Mock.setup({
    timeout: 100 // setter delay time
  })
  // console.log('[admin-pro] mock mounted')
}
