<template>
  <div :class="prefixCls" ref="wrap" v-resize="calcVisibleCount">
    <Menu :class="`${prefixCls}-head`" ref="menu" mode="horizontal" :activeName="activeName">
      <template v-for="menu in expandedMenu">
        <MenuHeadSubmenu v-if="menu.children && menu.children.length" topLevel :menu="menu" :key="menu.path" />
        <MenuHeadItem v-else :menu="menu" :key="menu.path" />
      </template>
      <MenuHeadSubmenu ref="more" v-if="collapseMenu.children.length" :class="collapseClass" topLevel :arrowIcon="false" :menu="collapseMenu" />
    </Menu>
  </div>
</template>

<script>
  import mixins from '../MenuSide/mixins'
  import MenuHeadItem from './MenuHeadItem'
  import MenuHeadSubmenu from './MenuHeadSubmenu'

  export default {
    name: 'MenuHead',
    mixins: [mixins],
    components: {
      MenuHeadItem,
      MenuHeadSubmenu
    },
    props: {
      menus: {
        type: Array,
        default: () => []
      },
      theme: {
        type: String,
        default: 'light'
      },
      iconSize: {
        type: Number,
        default: 16
      }
    },
    provide() {
      return {
        prefixCls: this.prefixCls
      }
    },
    data() {
      return {
        visibleCount: 5
      }
    },
    computed: {
      activeName() {
        const matched = this.$route.matched.filter(item => !item.meta.hideInMenu)
        let currentRoute
        if (matched.length > 1) {
          for (const route of matched) {
            currentRoute = this.menus.find(item => item.path === route.path)
            if (currentRoute) break
          }
        }
        return currentRoute && currentRoute.path
      },
      expandedMenu() {
        return this.menus.slice(0, this.visibleCount)
      },
      collapseMenu() {
        return {
          name: '...',
          path: '...',
          meta: { title: '...' },
          children: this.menus.slice(this.visibleCount)
        }
      },
      collapseClass() {
        const { prefixCls } = this
        return [
          `${prefixCls}-head-collapse`,
          {
            [`${prefixCls}-head-collapse-selected`]: this.collapseSelected
          }
        ]
      },
      collapseSelected() {
        const matchedPath = this.$route.matched[0].path
        const collapseMenu = this.menus.slice(this.visibleCount)
        return collapseMenu.some(item => item.path === matchedPath)
      }
    },
    mounted() {
      this.calcVisibleCount()
    },
    methods: {
      calcVisibleCount() {
        this.$nextTick(() => {
          const wrapWidth = this.$refs.wrap.offsetWidth
          const moreWidth = this.$refs.more?.$el?.offsetWidth ?? 0
          this.visibleCount = Math.floor((wrapWidth - moreWidth) / 150)
        })
      }
    }
  }
</script>
