import {
  setMenuCollapse,
  setDevice,
  setSiderTheme,
  setHeaderTheme,
  setLayoutMode,
  setColorWeak,
  setColorGrey,
  setHeaderFix,
  setHeaderStick,
  setSiderFix,
  setHeaderWidth,
  setContentWidth,
  setHideHeader,
  setTabsNav,
  setTabsNavStyle,
  setTabsNavList,
  keepAliveRemove,
  keepAlivePush,
  setTabsNavIcon,
  setTabSortable,
  setTabsNavFix,
  setTabsReload,
  setDynamicTitle,
  setCurrentTitle,
  setMenuAccordion,
  setFullContent,
  setCopyright,
  setWatermark,
  setResponsiveLayout,
  setShowSiderCollapse,
  setShowReload,
  setBreadcrumbNav,
  setBreadcrumbNavIcon,
  setShowSearch,
  setRouteAnimate,
  setShowLog,
  setShowFullscreen,
  setShowNotice,
  setDialogType,
  setPageBreadcrumb,
  setPageDescription
} from '@/store/mutation-types'
import { triggerWindowResizeEvent, updateColorWeak, updateColorGrey } from '@/utils/util'
import { DEVICE_TYPE } from '@/utils/constants'
import { storage } from '@/utils/storage'
import config from '@/config'

export default {
  state: {
    ...config,
    menuCollapse: false,
    device: 'desktop',
    menuBadge: [
      {
        path: '/dashboard/monitor',
        count: 1
      },
      {
        path: '/system',
        text: 'New',
        type: 'warning'
      },
      {
        path: '/example',
        color: 'lime'
      },
      {
        path: '/example/version',
        color: 'lime'
      }
    ],
    tabsNavList: [],
    cacheNavList: [],
    currentTitle: '',
    fullContent: false
  },
  mutations: {
    [setMenuCollapse](state, payload) {
      state.menuCollapse = payload
      storage.set(setMenuCollapse, payload)
    },
    [setDevice](state, device) {
      state.device = state.responsiveLayout ? device : DEVICE_TYPE.desktop
    },
    [setSiderTheme](state, siderTheme) {
      state.siderTheme = siderTheme
      storage.set(setSiderTheme, siderTheme)
    },
    [setHeaderTheme](state, headerTheme) {
      state.headerTheme = headerTheme
      storage.set(setHeaderTheme, headerTheme)
    },
    [setLayoutMode](state, layoutMode) {
      state.layoutMode = layoutMode
      triggerWindowResizeEvent()
      storage.set(setLayoutMode, layoutMode)
    },
    [setHeaderFix](state, fixed) {
      if (!fixed) {
        state.autoHideHeadert = fixed
      }
      state.headerFix = fixed
      storage.set(setHeaderFix, fixed)
    },
    [setHeaderStick](state, fixed) {
      state.headerStick = fixed
      storage.set(setHeaderStick, fixed)
    },
    [setSiderFix](state, fixed) {
      state.siderFix = fixed
      storage.set(setSiderFix, fixed)
    },
    [setHideHeader](state, payload) {
      state.headerHide = payload
      storage.set(setHideHeader, payload)
    },
    [setHeaderWidth](state, payload) {
      state.headerWidth = payload
      storage.set(setHeaderWidth, payload)
    },
    [setContentWidth](state, payload) {
      state.contentWidth = payload
      storage.set(setContentWidth, payload)
      // fix: 内容宽度改变，图表宽度不改变
      triggerWindowResizeEvent()
    },
    [setColorWeak](state, payload) {
      storage.set(setColorWeak, payload)
      state.colorWeak = payload
      updateColorWeak(payload)
    },
    [setColorGrey](state, payload) {
      storage.set(setColorGrey, payload)
      state.colorGrey = payload
      updateColorGrey(payload)
    },
    [setTabsNav](state, payload) {
      state.tabsNav = payload
      storage.set(setTabsNav, payload)
      if (!payload) state.cacheNavList = []
    },
    [setTabsNavStyle](state, payload) {
      state.tabsNavStyle = payload
      storage.set(setTabsNavStyle, payload)
    },
    [setTabsNavList](state, tabsNavList) {
      let tabList = []
      if (tabsNavList) {
        tabList = [...tabsNavList]
      } else {
        tabList = storage.get(setTabsNavList) || []
      }
      // FIXME: 清空缓存退出后登录重定向之前的页面 Tab不显示或者顺序不对
      const { homeRouteName } = config
      if (tabList[0] && tabList[0].name !== homeRouteName) {
        const firstTab = tabList.shift()
        tabList.push(firstTab)
      }
      const homeTagIndex = tabList.findIndex(item => item.name === homeRouteName)
      if (homeTagIndex > 0) {
        const homeTag = tabList.splice(homeTagIndex, 1)[0]
        tabList.unshift(homeTag)
      }
      state.tabsNavList = tabList
      const cacheNavList = tabList.filter(item => item.meta && item.meta.keepAlive).map(item => item.name)
      state.cacheNavList = state.tabsNav ? ['ParentView', ...cacheNavList] : []
      storage.set(setTabsNavList, [...tabList])
    },
    [keepAliveRemove](state, payload) {
      state.cacheNavList = state.cacheNavList.filter(item => item !== payload)
    },
    [keepAlivePush](state, payload) {
      const cacheNavList = state.cacheNavList
      cacheNavList.push(payload)
      state.cacheNavList = cacheNavList
    },
    [setTabsNavIcon](state, payload) {
      state.tabsNavIcon = payload
      storage.set(setTabsNavIcon, payload)
    },
    [setTabSortable](state, payload) {
      state.tabSortable = payload
      storage.set(setTabSortable, payload)
    },
    [setTabsNavFix](state, payload) {
      state.tabsNavFix = payload
      storage.set(setTabsNavFix, payload)
    },
    [setTabsReload](state, payload) {
      state.tabsReload = payload
      storage.set(setTabsReload, payload)
    },
    [setDynamicTitle](state, payload) {
      state.dynamicTitle = payload
      storage.set(setDynamicTitle, payload)
      this.commit(setCurrentTitle, state.currentTitle)
    },
    [setCurrentTitle](state, currentTitle) {
      state.currentTitle = currentTitle
      const { dynamicTitle, titleSuffix } = state
      window.document.title = dynamicTitle && currentTitle ? `${currentTitle} - ${titleSuffix}` : titleSuffix
    },
    [setShowReload](state, payload) {
      state.showReload = payload
      storage.set(setShowReload, payload)
    },
    [setBreadcrumbNav](state, payload) {
      state.showBreadcrumb = payload
      storage.set(setBreadcrumbNav, payload)
    },
    [setBreadcrumbNavIcon](state, payload) {
      state.showBreadcrumbIcon = payload
      storage.set(setBreadcrumbNavIcon, payload)
    },
    [setMenuAccordion](state, payload) {
      state.menuAccordion = payload
      storage.set(setMenuAccordion, payload)
    },
    [setFullContent](state, payload) {
      state.fullContent = payload
      triggerWindowResizeEvent()
      storage.set(setFullContent, payload)
    },
    [setCopyright](state, payload) {
      state.copyright = payload
      storage.set(setCopyright, payload)
    },
    [setWatermark](state, payload) {
      state.watermark = payload
      storage.set(setWatermark, payload)
    },
    [setRouteAnimate](state, value) {
      state.routeAnimate = value
      storage.set(setRouteAnimate, value)
    },
    [setShowSiderCollapse](state, payload) {
      state.showSiderCollapse = payload
      storage.set(setShowSiderCollapse, payload)
    },
    [setResponsiveLayout](state, payload) {
      const cls = `${state.cssPrefix}body-limit-width`
      payload ? document.body.classList.remove(cls) : document.body.classList.add(cls)
      state.responsiveLayout = payload
      storage.set(setResponsiveLayout, payload)
    },
    [setShowSearch](state, payload) {
      state.showSearch = payload
      storage.set(setShowSearch, payload)
    },
    [setShowLog](state, payload) {
      state.showLog = payload
      storage.set(setShowLog, payload)
    },
    [setShowFullscreen](state, payload) {
      state.showFullscreen = payload
      storage.set(setShowFullscreen, payload)
    },
    [setShowNotice](state, payload) {
      state.showNotice = payload
      storage.set(setShowNotice, payload)
    },
    [setDialogType](state, value) {
      state.dialogType = value
      storage.set(setDialogType, value)
    },
    [setPageBreadcrumb](state, value) {
      state.pageBreadcrumb = value
      storage.set(setPageBreadcrumb, value)
    },
    [setPageDescription](state, value) {
      state.pageDescription = value
      storage.set(setPageDescription, value)
    },
    setAllMenuBadge(state, value) {
      state.menuBadge = value
    },
    setMenuBadge(state, value) {
      const path = value.path
      const badge = value.badge
      const menuBadge = [...state.menuBadge]
      const findIndex = menuBadge.findIndex(item => item.path === path)
      if (findIndex > -1) {
        menuBadge[findIndex] = badge
        state.menuBadge = menuBadge
      } else {
        state.menuBadge.push(badge)
      }
    },
    removeMenuBadge(state, value) {
      const findIndex = state.menuBadge.findIndex(item => item.path === value)
      findIndex > -1 && state.menuBadge.splice(findIndex, 1)
    }
  },
  actions: {
    setTabsNavList({ state, rootState, commit, dispatch, getters }, tabsNavList) {
      const formatTabList = tabsNavList.map(tabNav => {
        const { name, path, fullPath, meta, hash, params, query } = tabNav
        return {
          name,
          path,
          fullPath,
          meta,
          hash,
          params,
          query
        }
      })
      getters.openTabs.forEach(item => {
        // FIXME:2023/05/27 判断不允许关闭的页签是否存在，不存在则加入
        const hasOpenTab = formatTabList.some(tab => tab.fullPath === item.fullPath)
        if (!hasOpenTab) formatTabList.push(item)
      })
      commit(setTabsNavList, formatTabList)
    }
  }
}
