<template>
  <span :class="[prefixCls, `${BasicLayout.prefixCls}-header-tool-item`]">
    <Dropdown :trigger="isMobile ? 'click' : 'hover'" :class="isMobile && `${prefixCls}-mobile`" @on-click="handleDropMenu">
      <span :class="`${prefixCls}-username`">
        <Avatar size="small" :src="userInfo.avatar" />
        <span v-if="!isMobile">{{ userInfo.name }}</span>
      </span>
      <template #list>
        <DropdownMenu>
          <DropdownItem name="UserProfile">
            <Icon type="ios-contact-outline" />
            <span>个人中心</span>
          </DropdownItem>
          <DropdownItem name="UserSetting">
            <Icon type="ios-settings-outline" />
            <span>账户设置</span>
          </DropdownItem>
          <DropdownItem name="storage">
            <Icon type="md-trash" />
            <span>清空缓存</span>
          </DropdownItem>
          <Divider />
          <DropdownItem name="logout">
            <Icon type="ios-log-out" />
            <span>退出登录</span>
          </DropdownItem>
        </DropdownMenu>
      </template>
    </Dropdown>
  </span>
</template>

<script>
  import { mapActions, mapState } from 'vuex'

  export default {
    name: 'HeaderUser',
    inject: ['BasicLayout'],
    props: {
      isMobile: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        prefixCls: `${this.$config.cssPrefix}header-user`
      }
    },
    computed: {
      ...mapState({
        userInfo: state => state.user.userInfo
      })
    },
    methods: {
      ...mapActions(['logout']),
      handleLogout() {
        this.logout(this)
          .then(() => {
            this.$router.push({ path: '/login' })
          })
          .catch(err => {
            this.$Notice.error({
              title: '系统提示',
              desc: err.toString()
            })
          })
      },
      clearLocalCache() {
        this.$Modal.confirm({
          title: '系统提示',
          content: '清空缓存会退出登录状态，确定继续吗？',
          onOk: () => {
            localStorage.clear()
            location.reload()
          }
        })
      },
      handleDropMenu(name) {
        switch (name) {
          case 'UserProfile':
            this.$router.push({ name })
            break
          case 'UserSetting':
            this.$router.push({ name })
            break
          case 'storage':
            this.clearLocalCache()
            break
          case 'logout':
            this.handleLogout()
            break
          default:
            break
        }
      }
    }
  }
</script>
