<template>
  <div ref="miniArea" v-height="height"></div>
</template>

<script>
  import { shallowRef } from 'vue'

  export default {
    name: 'MiniArea',
    props: {
      height: {
        type: Number,
        required: true
      },
      options: {
        type: Object,
        required: true,
        validator: value => !!value.xData && !!value.yData && !!value.color
      }
    },
    data() {
      return {
        miniArea: null
      }
    },
    async mounted() {
      this.handleSetAreaChart()
    },
    methods: {
      async handleSetAreaChart() {
        const $echarts = await import(/* webpackChunkName: "async-echarts" */ 'echarts')
        this.miniArea = shallowRef(this.miniArea ?? $echarts.init(this.$refs.miniArea))
        this.miniArea.setOption({
          xAxis: {
            type: 'category',
            show: false,
            boundaryGap: false,
            data: this.options.xData
          },
          yAxis: {
            type: 'value',
            show: false
          },
          series: [
            {
              data: this.options.yData,
              type: 'line',
              smooth: true,
              tooltip: true,
              symbol: 'none',
              areaStyle: { normal: { opacity: 0.2 } }
            }
          ],
          // color: ['#1495EB', '#00CC66', '#F9D249', '#ff9900', '#9860DF'],
          color: this.options.color,
          tooltip: { trigger: 'axis' },
          grid: {
            left: -60,
            right: -20,
            bottom: -20,
            top: 0,
            containLabel: true
          }
        })
      },
      handleResize() {
        this.miniArea && this.miniArea.resize()
      }
    },
    beforeDestroy() {
      this.miniArea && this.miniArea.dispose()
      this.miniArea = null
    }
  }
</script>
