import component from './component'
import directive from '@/directives'
import vueFilter from '@/utils/filter'
import catchError from './catch-error'
import viewDesign from './view-ui-plus'
import thirdPlugin from './third-plugin'

import $axios from '@/utils/axios'
import $config from '@/config'
import $console from '@/utils/console'
import $storage from '@/utils/storage'

export default {
  install(app) {
    app.use(thirdPlugin) // 第三方插件
    app.use(viewDesign) // 按需加载 View Design
    app.use(catchError) // 异常处理记录
    app.use(component) // 自定义组件
    app.use(directive) // 自定义指令
    app.use(vueFilter) // 自定义filter

    app.use($axios) // axios封装实例
    app.use($config) // 应用全局配置
    app.use($console) // $console插件
    app.use($storage) // $storage插件
  }
}
