<template>
  <div :id="config.id" :style="rootStyle"></div>
</template>

<script>
  /**
   * @see http://v2.h5player.bytedance.com/config/#%E5%BF%85%E9%80%89%E9%85%8D%E7%BD%AE
   */
  export default {
    name: 'VueXgplayer',
    props: {
      config: {
        type: Object,
        default: () => {}
      },
      rootStyle: {
        type: Object,
        default: () => {}
      }
    },
    data() {
      return {
        player: null,
        defaultConfig: {
          id: 'mse',
          url: '',
          playbackRate: [0.5, 0.75, 1, 1.25, 1.5, 1.75, 2], // 倍速播放
          pip: true, // 画中画
          loop: false, // 循环播放
          fluid: true, // 流式布局
          download: false, // 视频下载
          autoplay: false, // 自动播放
          videoInit: true, // 初始化显示视频首帧
          cssFullscreen: false // 样式全屏
        }
      }
    },
    computed: {
      mergeConfig() {
        return { ...this.defaultConfig, ...this.config }
      }
    },
    mounted() {
      this.init()
    },
    activated() {
      this.init()
    },
    beforeUpdate() {
      this.init()
    },
    beforeUnmount() {
      this.player && typeof this.player.destroy === 'function' && this.player.destroy()
      this.player = null
    },
    methods: {
      async init() {
        const xgplayer = await import(/* webpackChunkName: "async-xgplayer" */ 'xgplayer')
        const Player = xgplayer.default
        if (this.config.url && !this.player) {
          this.player = new Player(this.mergeConfig)
          this.$emit('player', this.player)
        }
      }
    }
  }
</script>
<style lang="less">
  .xgplayer-skin-default {
    .xgplayer-start,
    .xgplayer-replay {
      z-index: inherit !important;
    }
  }
</style>
