/**
 * iView Admin Pro 业务配置
 */
const Vue = require('vue')
const iViewUIPkg = require('view-ui-plus/package')
const packageJson = require('../../package.json')

const isProduction = process.env.NODE_ENV === 'production'

const paramsSetting = {
  // 配置显示在浏览器标签的title
  titleSuffix: 'iView Admin Plus',
  // 项目描述信息
  description: '基于Vue 3.x、View UI Plus 企业级中台前端/设计解决方案',
  // 默认打开的首页路由name值，默认为Workplace
  homeRouteName: 'Workplace',
  // 全局css前缀,需同步修改less: @admin-prefix
  cssPrefix: 'ivu-admin-',
  // 路由模式，可选值为 history 或 hash
  routerMode: 'hash',
  // 应用的基路径:VueRouter的一个配置选项，用于指定应用的基础URL路径，即路由URL的前缀
  routerBase: '/',
  // 应用的基路径:VueCLI中的一个配置选项，用于指定构建后的静态资源文件（如JS、CSS、图片等）在服务器上的访问路径
  publicPath: isProduction ? '/admin-iview/' : '/',
  // api请求基础路径
  apiBaseURL: process.env.VUE_APP_API_BASE_URL,
  // 部署时间
  deployTime: process.env.VUE_APP_DEPLOY_TIME,
  // 侧边栏展开宽度
  siderExpandedWidth: 200,
  // 侧边栏折叠宽度
  siderCollapseWidth: 48,
  // 顶栏的高度
  layoutHeaderHeight: 48,
  // token在本地存储的时间，默认24小时
  tokenExpire: 1000 * 60 * 60 * 24,
  // 对话框持续时间，单位秒
  modalDuration: 3,
  // 错误对话框类型: Message | Notice
  errorModalType: 'Message',
  // 验证码类型: blockPuzzle(块拼图),clickWord(点击单词),pictureCode(图片验证码), random为随机验证码
  captchaType: 'blockPuzzle',
  // 页面切换时，是否显示模拟的进度条
  showProgressBar: true,
  // 退出登录是否二次确认
  logoutConfirm: true,
  // 设为false不会收集错误信息，方便排查错误
  catchErrorLog: isProduction,
  // 捕获异常后给用户的提示信息
  catchErrorMsg: '系统异常，请稍后重试',
  // 相同路由，不同参数间进行切换，是否强力更新
  sameRouteForceUpdate: true,
  // Vue-ls 插件配置项 (localStorage/sessionStorage)
  storageOptions: {
    namespace: '__iView__Admin_Plus_', // key prefix more see: https://github.com/RobinCK/vue-ls
    name: 'ls', // name variable Vue.[ls] or this.[$ls],
    storage: 'local' // storage name session, local, memory
  }
}
const headerSetting = {
  // 折叠侧边栏按钮, 移动端强制开启
  showSiderCollapse: true,
  // 重载页面按钮
  showReload: true,
  // 开启面包屑导航
  showBreadcrumb: true,
  // 显示面包屑图标
  showBreadcrumbIcon: true,
  // 路由菜单搜索
  showSearch: true,
  // 告警日志入口
  showLog: true,
  // 显示全屏按钮
  showFullscreen: true,
  // 消息通知中心
  showNotice: true
}
const tabNavSetting = {
  // 开启多页签
  tabsNav: true,
  // 页签显示风格: dot / card
  tabsNavStyle: 'dot',
  // 显示页签图标
  tabsNavIcon: true,
  // 拖拽页签排序
  tabSortable: true,
  // 固定多页签栏
  tabsNavFix: true,
  // 双击页签，是否重载当前页面
  tabsReload: true
}
const pageHeaderSetting = {
  // 面包屑导航
  pageBreadcrumb: true,
  // 显示页面描述
  pageDescription: true
}
const layoutSetting = {
  // 导航模式 ['sideMenu', 'mixMenu'] 两种布局
  layoutMode: 'sideMenu',
  // 顶栏布局： 流式 fluid | 固定 fixed [topMenu时有效]
  headerWidth: 'fluid',
  // 内容区布局： 流式 fluid | 固定 fixed [topMenu时有效]
  contentWidth: 'fixed',
  // 侧栏主题 ['dark', 'light'] 两种主题
  siderTheme: 'light',
  // 顶栏主题 ['dark', 'light'] 两种主题
  headerTheme: 'light',
  // 固定左侧菜单栏
  siderFix: true,
  // 固定 Header
  headerFix: true,
  // 置顶 Header
  headerStick: true,
  // 下滑隐藏 Header
  headerHide: false
}
const othersSetting = {
  // 使用titleSuffix作为Logo，false使用图片Logo
  showTextLogo: true,
  // 侧边栏开启手风琴模式
  menuAccordion: true,
  // 路由动画
  // options: null / slider-right / slide-bottom / zoom-in / zoom-out / fade
  routeAnimate: 'slide-bottom',
  // 响应式布局, 不开启设备布局均为deskTop
  responsiveLayout: true,
  // 开启动态标题
  dynamicTitle: true,
  // 底部版权
  copyright: true,
  // 全局水印
  watermark: isProduction,
  // 色盲模式
  colorWeak: false,
  // 灰色模式
  colorGrey: false,
  // 对话框类型: modal | drawer
  dialogType: 'modal',
  //  是否显示Setting配置tip提示
  settingTip: isProduction,
  // 开发中的功能默认提示语
  developMsg: '探索中，敬请期待！',
  // admin版本号
  adminVersion: packageJson.version,
  vueJsVersion: Vue.version,
  iViewVersion: iViewUIPkg.version
}

const appConfig = {
  ...paramsSetting,
  ...headerSetting,
  ...tabNavSetting,
  ...layoutSetting,
  ...othersSetting,
  ...pageHeaderSetting,
  install: app => (app.config.globalProperties.$config = appConfig)
}

module.exports = appConfig
