<template>
  <Tooltip :content="tipContent">
    <span :class="`${BasicLayout.prefixCls}-header-tool-item`" @click="handleOpenLog">
      <Badge :count="logCount" :dot="showDot" :offset="siteDot">
        <Icon custom="i-icon i-icon-record" :size="iconSize" />
      </Badge>
    </span>
  </Tooltip>
</template>

<script>
  export default {
    name: 'HeaderLog',
    inject: ['BasicLayout'],
    props: {
      iconSize: {
        type: [String, Number],
        default: 18
      }
    },
    computed: {
      logCount() {
        return this.$store.getters.logCount
      },
      errCount() {
        return this.$store.getters.errCount
      },
      showDot() {
        return !!this.logCount && !this.errCount
      },
      siteDot() {
        return this.showDot ? [18, 2] : [14, 0]
      },
      tipContent() {
        if (this.logCount) {
          const logContent = `${this.logCount} 条日志`
          const errContent = ` | 包含 ${this.errCount} 个异常`
          return (this.errCount && logContent + errContent) || logContent
        }
        return '没有日志或异常'
      }
    },
    methods: {
      handleOpenLog() {
        this.$router.push({ name: 'log' })
      }
    }
  }
</script>
