<template>
  <Breadcrumb :class="prefixCls">
    <BreadcrumbItem v-for="(item, index) in breadcrumbList" :key="`${item.name}-${index}`">
      <component :is="isComponent(item, index)" :to="toRoutePath(item, index)">
        <XIcon :type="item.meta.icon" v-if="showIcon && item.meta.icon" size="16" />
        <span>{{ item.meta.title }}</span>
      </component>
    </BreadcrumbItem>
  </Breadcrumb>
</template>

<script>
  export default {
    name: 'HeaderBreadcrumb',
    data() {
      return {
        prefixCls: `${this.$config.cssPrefix}breadcrumb`
      }
    },
    computed: {
      homeRoute() {
        return this.$store.getters.homeRoute
      },
      showIcon() {
        return this.$store.state.app.showBreadcrumbIcon
      },
      routeMatched() {
        return this.$route.matched.filter(item => !item.meta.hideInBread)
      },
      breadcrumbList() {
        return [this.homeRoute, ...this.routeMatched]
      }
    },
    methods: {
      isComponent(route, index) {
        const isLastItem = index === this.breadcrumbList.length - 1
        const isHomeBread = this.routeMatched.some(item => item.name === this.homeRoute.name)
        const renderDomEl = this.routeMatched.some(item => item.path === route.redirect)
        return isLastItem || isHomeBread || renderDomEl ? 'span' : 'router-link'
      },
      toRoutePath(route, index) {
        const isLastItem = index === this.breadcrumbList.length - 1
        return (!isLastItem && { path: route.path || '/' }) || undefined
      }
    }
  }
</script>
