import { Modal } from 'view-ui-plus'
import config from '@/config'

const analyseScriptStr = text => {
  const scriptReg = /<script.*src=["'](?<src>[^"']+)/gm
  scriptReg.lastIndex = 0 // 正则分析页面所有url地址
  const result = [] // 保存分析数据到一个数组里
  let match
  while ((match = scriptReg.exec(text))) {
    result.push(match.groups.src)
  }
  return result.filter(item => item.endsWith('.js'))
}

/**
 * 请求服务端首页HTML
 * @returns {Promise<Array>}
 */
const getServerScripts = async () => {
  const serverHTML = await fetch(`${config.publicPath}?timestamp=${Date.now()}`)
    .then(res => res.text())
    .catch(err => err.toString())
  return analyseScriptStr(serverHTML)
}

/**
 * 查询本地document
 * @returns {Promise<Array>}
 */
const getNativeScripts = async () => {
  const scriptCollection = document.getElementsByTagName('script')
  const scriptElementList = []
  for (const item of scriptCollection) {
    scriptElementList.push(item.outerHTML)
  }
  return analyseScriptStr(scriptElementList.join())
}

const needRefresh = async () => {
  const serverStrList = await getServerScripts()
  const nativeStrList = await getNativeScripts()
  if (!serverStrList.length) return false
  if (nativeStrList.length !== serverStrList.length) return true
  return nativeStrList.some(item => !serverStrList.includes(item))
}

const autoRefresh = () => {
  if (process.env.NODE_ENV !== 'production') return
  const duration = 1000 * 10
  setInterval(async () => {
    if (await needRefresh()) {
      Modal.confirm({
        title: '检测到新版本',
        content: '将重载页面更新资源，请确认是否继续？',
        onOk: () => location.reload(),
        onCancel: () => Modal.remove()
      })
    }
  }, duration)
}
autoRefresh()
