<template>
  <Watermark :content="(watermark && $config.titleSuffix) || undefined" :gap="[150, 150]">
    <Layout :class="[device, prefixCls]">
      <Sider
        v-show="showSider"
        :class="siderClasses"
        :width="siderExpandedWidth"
        :collapsedWidth="siderCollapseWidth"
        :modelValue="menuCollapse"
        :collapsible="true"
        :hideTrigger="true"
      >
        <HeaderLogo mode="sider" :collapsed="menuCollapse" :theme="siderTheme" />
        <MenuSide :menus="siderMenus" :theme="siderTheme" :collapsed="menuCollapse" :activeName="activePath" :accordion="menuAccordion" />
      </Sider>
      <Layout :class="insideClasses">
        <Transition name="fade-quick">
          <Header v-show="showHeader" :class="headerClasses">
            <div :class="headerInsideClass">
              <HeaderLogo
                v-if="isMobile || isTopMenu || (headerFix && headerStick) || hideSider"
                mode="header"
                :theme="headerTheme"
                :showTitle="!isMobile"
              />
              <div :class="`${prefixCls}-header-tool`" v-if="isMobile || !isTopMenu">
                <HeaderCollapse v-if="showSiderCollapse && !hideSider" />
                <HeaderReload v-if="!isMobile && showReload" />
              </div>
              <HeaderBreadcrumb v-if="isDesktop && isSideMenu && showBreadcrumb" />
              <div :class="`${prefixCls}-header-nav`">
                <MenuHead v-if="!isMobile && !isSideMenu" :menus="headerMenus" />
              </div>
              <div :class="`${prefixCls}-header-tool`">
                <HeaderSearch v-if="showSearch" :isMobile="isMobile" />
                <HeaderLog v-if="showLog" />
                <HeaderFullScreen v-if="isDesktop && showFullscreen" />
                <HeaderNotice v-if="showNotice" />
                <HeaderUser :isMobile="isMobile" />
                <HeaderSetting />
              </div>
            </div>
          </Header>
        </Transition>
        <Content :class="contentClasses">
          <Transition name="fade-quick">
            <TabsNav v-if="tabsNav" v-show="showHeader" :hideSider="hideSider" />
          </Transition>
          <div :class="`${prefixCls}-content-main`">
            <router-view v-slot="{ Component, route }">
              <Transition :name="routeAnimate" mode="out-in">
                <keep-alive :include="cacheNavList">
                  <component :is="Component" :key="route.path" v-if="loadRouter" />
                </keep-alive>
              </Transition>
            </router-view>
          </div>
        </Content>
        <Copyright />
      </Layout>
      <Drawer
        v-if="isMobile"
        placement="left"
        :width="siderExpandedWidth"
        :className="drawerClasses"
        :transfer="false"
        :closable="false"
        :modelValue="!menuCollapse"
        @on-close="handleCollapse"
      >
        <HeaderLogo mode="sider" :collapsed="menuCollapse" :theme="siderTheme" />
        <MenuSide
          :menus="siderMenus"
          :theme="siderTheme"
          :collapsed="false"
          :activeName="activePath"
          :accordion="menuAccordion"
          @on-select="handleCollapse"
        />
      </Drawer>
      <Consult />
    </Layout>
  </Watermark>
</template>

<script>
  import { mapMutations } from 'vuex'
  import { deepCopy } from 'view-ui-plus/src/utils/assist'
  import { Watermark } from '@/components'
  import { mixinState, mixinDevice } from '@/utils/mixin'
  import { setMenuCollapse, keepAliveRemove, keepAlivePush } from '@/store/mutation-types'
  import {
    Consult,
    MenuSide,
    MenuHead,
    Copyright,
    HeaderLog,
    HeaderLogo,
    HeaderUser,
    HeaderReload,
    HeaderSearch,
    HeaderNotice,
    HeaderSetting,
    HeaderCollapse,
    HeaderBreadcrumb,
    HeaderFullScreen
  } from './components'

  export default {
    name: 'BasicLayout',
    mixins: [mixinState, mixinDevice],
    components: {
      Consult,
      MenuSide,
      MenuHead,
      Copyright,
      Watermark,
      HeaderLog,
      HeaderLogo,
      HeaderUser,
      HeaderReload,
      HeaderSearch,
      HeaderNotice,
      HeaderSetting,
      HeaderCollapse,
      HeaderBreadcrumb,
      HeaderFullScreen
    },
    provide() {
      return {
        BasicLayout: this
      }
    },
    data() {
      return {
        prefixCls: `${this.$config.cssPrefix}layout`,
        scrollArea: document.getElementById('app'),
        oldScrollTop: 0,
        loadRouter: true,
        headerVisible: true
      }
    },
    watch: {
      $route(newVal, oldVal) {
        newVal.name === oldVal.name && this.sameRouteForceUpdate && this.handleReload()
      }
    },
    computed: {
      activePath() {
        return this.$route.path
      },
      showHeader() {
        return !(this.headerFix && this.headerHide && !this.headerVisible)
      },
      headerMenus() {
        const menuList = deepCopy(this.$store.getters.menuList)
        const mainMenu = menuList.map(item => ({
          ...item,
          children: undefined
        }))
        return this.isTopMenu ? menuList : mainMenu
      },
      siderMenus() {
        const menuList = this.$store.getters.menuList
        const subMenus = this.$store.getters.subMenus(this)
        return !this.isMobile && this.isMixMenu ? subMenus : menuList
      },
      hideSider() {
        return this.isMixMenu && !this.siderMenus.length
      },
      showSider() {
        return !this.isMobile && !this.isTopMenu && !this.hideSider
      },
      siderClasses() {
        const { prefixCls } = this
        return [
          `${prefixCls}-sider`,
          {
            [`${prefixCls}-sider-fix`]: this.siderFix,
            [`${prefixCls}-sider-dark`]: this.siderTheme === 'dark'
          }
        ]
      },
      headerClasses() {
        const { prefixCls } = this
        return [
          `${prefixCls}-header`,
          `${prefixCls}-header-${this.headerTheme}`,
          {
            [`${prefixCls}-header-fix`]: this.headerFix,
            [`${prefixCls}-header-fix-expanded`]: !this.isMobile && !this.isTopMenu && this.headerFix && !this.menuCollapse,
            [`${prefixCls}-header-fix-collapse`]: !this.isMobile && !this.isTopMenu && this.headerFix && this.menuCollapse,
            [`${prefixCls}-header-stick`]: (!this.isTopMenu && this.headerFix && this.headerStick) || this.hideSider
          }
        ]
      },
      headerInsideClass() {
        const { prefixCls } = this
        return [
          `${prefixCls}-header-inside`,
          {
            [`${prefixCls}-header-top-menu`]: this.isTopMenu,
            [`${prefixCls}-header-top-menu-${this.headerWidth}`]: this.isTopMenu
          }
        ]
      },
      insideClasses() {
        const { prefixCls } = this
        return [
          `${prefixCls}-inside`,
          {
            [`${prefixCls}-inside-fix-with-sider`]: !this.isMobile && !this.isTopMenu && this.siderFix && !this.menuCollapse,
            [`${prefixCls}-inside-fix-with-sider-collapse`]: !this.isMobile && !this.isTopMenu && this.siderFix && this.menuCollapse,
            [`${prefixCls}-inside-with-hide-sider`]: this.hideSider
          }
        ]
      },
      contentClasses() {
        const { prefixCls } = this
        return [
          `${prefixCls}-content`,
          {
            [`${prefixCls}-content-with-tabs`]: this.tabsNav,
            [`${prefixCls}-content-with-tabs-fix`]: this.tabsNav && this.tabsNavFix,
            [`${prefixCls}-content-fix-with-header`]: this.headerFix && !this.fullContent,
            [`${prefixCls}-content-${this.contentWidth}`]: this.isDesktop && this.isTopMenu && !this.fullContent,
            [`${prefixCls}-content-fullscreen`]: this.fullContent
          }
        ]
      },
      drawerClasses() {
        const { prefixCls } = this
        let classes = `${prefixCls}-drawer`
        this.siderTheme === 'dark' && (classes += ` ${prefixCls}-drawer-dark`)
        return classes
      }
    },
    methods: {
      ...mapMutations([setMenuCollapse, keepAliveRemove, keepAlivePush]),
      handleReload() {
        this.$Loading.start()
        const routeName = this.$route.name
        const isInclude = this.cacheNavList.includes(routeName)
        if (isInclude) this[keepAliveRemove](routeName)
        this.loadRouter = false
        this.$nextTick(() => {
          this.loadRouter = true
          this.$Loading.finish()
          if (isInclude) this[keepAlivePush](routeName)
        })
      },
      handleCollapse() {
        this[setMenuCollapse](!this.menuCollapse)
      },
      handleScroll() {
        if (this.headerHide && !this.ticking) {
          this.ticking = true
          const scrollTop = this.scrollArea.scrollTop
          requestAnimationFrame(() => {
            if (this.oldScrollTop > scrollTop) {
              this.headerVisible = true
            } else if (scrollTop > 100 && this.headerVisible) {
              this.headerVisible = false
            } else if (scrollTop < 100 && !this.headerVisible) {
              this.headerVisible = true
            }
            this.oldScrollTop = scrollTop
            this.ticking = false
          })
        }
      }
    },
    mounted() {
      this.scrollArea.addEventListener('scroll', this.handleScroll, {
        passive: true
      })
    },
    beforeUnmount() {
      this.scrollArea.removeEventListener('scroll', this.handleScroll, true)
    }
  }
</script>
