import { deepCopy } from 'view-ui-plus/src/utils/assist'
import { getHomeRoute } from '@/utils/util'
import { filterMenus, findOpenTabs, filterSubMenus } from '@/utils/routeConvert'

export default {
  // app
  settingEqual: state => (var1, var2) => state.app[`${var1}`] === var2,

  // log
  logCount: state => state.log.logList.length,
  errCount: state => state.log.logList.filter(item => item.type === 'error').length,

  // auth
  menuList: state => filterMenus(deepCopy(state.auth.addRouters)),
  subMenus: (state, getters) => vm => filterSubMenus(vm, getters.menuList),
  homeRoute: (state, getters) => getHomeRoute(getters.menuList, state.app.homeRouteName),
  openTabs: (state, getters) =>
    findOpenTabs(getters.menuList).map(item => {
      const { name, path, meta, fullPath, hash, params, query } = item
      return {
        name,
        path,
        meta,
        fullPath: fullPath || path,
        hash,
        params,
        query
      }
    })
}
