<template>
  <component
    :is="wrapComponentType"
    class-name="ivu-verify-center"
    title="请完成安全验证"
    v-model="visible"
    :width="modalWidth"
    :centered="true"
    :mask-closable="false"
    footer-hide
    @on-cancel="closeModal"
  >
    <component
      ref="instance"
      :is="componentType"
      :arith="arith"
      :barSize="barSize"
      :blockSize="blockSize"
      :explain="explain"
      :figure="figure"
      :imgSize="imgSize"
      :mode="mode"
      :vSpace="vSpace"
    />
  </component>
</template>
<script>
  /**
   * Verify 验证码组件
   * @description 分发验证码使用
   * */
  import { Modal } from 'view-ui-plus'
  import VerifySlide from './components/VerifySlide'
  import VerifyPoint from './components/VerifyPoint'

  export default {
    name: 'Vue2Verify',
    components: {
      Modal,
      VerifySlide,
      VerifyPoint
    },
    props: {
      captchaType: {
        type: String,
        required: true,
        validator: value => ['blockPuzzle', 'clickWord'].includes(value)
      },
      figure: {
        type: Number,
        default: undefined
      },
      arith: {
        type: Number,
        default: undefined
      },
      mode: {
        type: String,
        default: 'pop'
      },
      vSpace: {
        type: Number,
        default: 5
      },
      explain: {
        type: String,
        default: undefined
      },
      imgSize: {
        type: Object,
        default: () => ({ width: '310px', height: '155px' })
      },
      blockSize: {
        type: Object,
        default: () => {}
      },
      barSize: {
        type: Object,
        default: () => {}
      }
    },
    provide() {
      return { Verify: this }
    },
    data() {
      return {
        visible: false
      }
    },
    computed: {
      instance() {
        return this.$refs.instance || {}
      },
      modalWidth() {
        return parseInt(this.imgSize.width) + 34
      },
      wrapComponentType() {
        return this.mode === 'pop' ? 'Modal' : 'span'
      },
      componentType() {
        return this.captchaType === 'blockPuzzle' ? 'VerifySlide' : 'VerifyPoint'
      }
    },
    methods: {
      refresh() {
        if (this.instance.refresh) {
          this.instance.refresh()
        }
      },
      closeModal() {
        this.refresh()
      },
      show() {
        this.visible = this.mode === 'pop'
      }
    }
  }
</script>
<style lang="less">
  .ivu-verify-center {
    display: flex;
    align-items: center;
    justify-content: center;

    .ivu-modal {
      top: 0;
    }
  }

  /* ---------------------------- */
  /*常规验证码*/
  .verify-code {
    font-size: 20px;
    text-align: center;
    cursor: pointer;
    margin-bottom: 5px;
    border: 1px solid #ddd;
  }

  .cerify-code-panel {
    height: 100%;
    overflow: hidden;
  }

  .verify-code-area {
    float: left;
  }

  .verify-input-area {
    float: left;
    width: 60%;
    padding-right: 10px;
  }

  .verify-change-area {
    line-height: 30px;
    float: left;
  }

  .varify-input-code {
    display: inline-block;
    width: 100%;
    height: 25px;
  }

  .verify-change-code {
    color: #337ab7;
    cursor: pointer;
  }

  .verify-btn {
    width: 200px;
    height: 30px;
    background: #337ab7;
    color: #ffffff;
    border: none;
    margin-top: 10px;
  }
</style>
