<template>
  <div ref="chartBar" v-height="height"></div>
</template>

<script>
  import { shallowRef } from 'vue'

  export default {
    name: 'ChartBar',
    props: {
      height: {
        type: Number,
        required: true
      },
      options: {
        type: Object,
        required: true,
        validator: value => !!value.xData && !!value.yData && !!value.color
      }
    },
    data() {
      return {
        chartBar: null
      }
    },
    mounted() {
      this.handleSetVisitChart()
    },
    methods: {
      async handleSetVisitChart() {
        const $echarts = await import(/* webpackChunkName: "async-echarts" */ 'echarts')
        this.chartBar = shallowRef(this.chartBar ?? $echarts.init(this.$refs.chartBar))
        this.chartBar.setOption({
          xAxis: {
            type: 'category',
            axisLine: { lineStyle: { color: '#D7DDE4' } },
            axisTick: { alignWithLabel: true, lineStyle: { color: '#D7DDE4' } },
            axisLabel: { textStyle: { color: '#7F8B9C' } },
            splitLine: { show: false, lineStyle: { color: '#F5F7F9' } },
            data: this.options.xData
          },
          yAxis: {
            type: 'value',
            axisLine: { show: false },
            axisTick: { show: false },
            axisLabel: { textStyle: { color: '#7F8B9C' } },
            splitLine: { show: true, lineStyle: { color: '#F5F7F9' } }
          },
          series: [
            {
              data: this.options.yData,
              type: 'bar',
              tooltip: true,
              smooth: true,
              symbol: 'none',
              barWidth: '50%',
              itemStyle: {
                normal: {
                  barBorderRadius: [3, 3, 0, 0],
                  color: new $echarts.graphic.LinearGradient(0, 0, 0, 1, [
                    { offset: 0, color: '#69cdff' },
                    { offset: 0.5, color: '#3eb3f7' },
                    { offset: 1, color: '#1495eb' }
                  ])
                }
              }
            }
          ],
          color: this.options.color,
          grid: { left: 16, right: 25, bottom: 5, top: 20, containLabel: true },
          tooltip: { trigger: 'axis' }
        })
      },
      handleResize() {
        this.chartBar && this.chartBar.resize()
      }
    },
    beforeDestroy() {
      this.chartBar && this.chartBar.dispose()
      this.chartBar = null
    }
  }
</script>
