<template>
  <div class="ivu-exception">
    <div class="ivu-exception-img">
      <div class="ivu-exception-img-element" :style="{ 'background-image': 'url(' + imgPath + ')' }"></div>
    </div>
    <div class="ivu-exception-content">
      <h1>
        <slot name="title">{{ titleText }}</slot>
      </h1>
      <div class="ivu-exception-content-desc">
        <slot name="desc">{{ descText }}</slot>
      </div>
      <div class="ivu-exception-content-actions">
        <slot name="actions">
          <Button type="primary" @click="backHome">{{ backText }}</Button>
          <Button type="primary" @click="backPrev" v-if="prevBtn">返回上一页（{{ second }}s）</Button>
        </slot>
      </div>
    </div>
  </div>
</template>
<script>
  import { Button } from 'view-ui-plus'
  import typeConfig from './typeConfig.js'

  export default {
    name: 'Exception',
    components: { Button },
    props: {
      type: {
        validator(value) {
          return ['403', '404', '500', 403, 404, 500].includes(value)
        },
        default: '404'
      },
      title: {
        type: String
      },
      desc: {
        type: String
      },
      img: {
        type: String
      },
      imgColor: {
        type: Boolean,
        default: false
      },
      backText: {
        type: String,
        default: '返回首页'
      },
      redirect: {
        type: String,
        default: '/'
      },
      prevBtn: {
        type: Boolean,
        default: true
      }
    },
    data() {
      return {
        second: 5,
        timer: null
      }
    },
    computed: {
      imgPath() {
        return this.img ? this.img : this.imgColor ? typeConfig[this.type].imgColor : typeConfig[this.type].img
      },
      titleText() {
        return this.title ? this.title : typeConfig[this.type].title
      },
      descText() {
        return this.desc ? this.desc : typeConfig[this.type].desc
      }
    },
    mounted() {
      if (this.prevBtn) {
        this.timer = setInterval(() => {
          if (this.second === 1) this.backPrev()
          else this.second--
        }, 1000)
      }
    },
    beforeUnmount() {
      if (this.prevBtn) {
        clearInterval(this.timer)
      }
    },
    methods: {
      backHome() {
        this.$router.push({ path: this.redirect })
      },
      backPrev() {
        this.$router.go(-1)
      }
    }
  }
</script>
