/**
 * 触发 window.resize
 */
export function triggerWindowResizeEvent() {
  const event = document.createEvent('HTMLEvents')
  event.initEvent('resize', true, true)
  event.eventType = 'message'
  window.dispatchEvent(event)
}

export function handleScrollHeader(callback) {
  let timer = 0

  let beforeScrollTop = window.pageYOffset
  callback = callback || function () {}
  window.addEventListener(
    'scroll',
    event => {
      clearTimeout(timer)
      timer = setTimeout(() => {
        let direction = 'up'
        const afterScrollTop = window.pageYOffset
        const delta = afterScrollTop - beforeScrollTop
        if (delta === 0) {
          return false
        }
        direction = delta > 0 ? 'down' : 'up'
        callback(direction)
        beforeScrollTop = afterScrollTop
      }, 50)
    },
    false
  )
}

export function isIE() {
  const bw = window.navigator.userAgent
  const compare = s => bw.indexOf(s) >= 0
  const ie11 = (() => 'ActiveXObject' in window)()
  return compare('MSIE') || ie11
}

/**
 * Remove loading animate
 * @param id parent element id or class
 * @param timeout
 */
export const removeLoadingAnimate = (id = '', timeout = 1500) => {
  if (id === '') {
    return
  }
  setTimeout(() => {
    document.body.removeChild(document.getElementById(id))
  }, timeout)
}

// 判断参数是否是其中之一
export const oneOf = (value, validList) => {
  for (let i = 0; i < validList.length; i++) {
    if (value === validList[i]) {
      return true
    }
  }
  return false
}

/**
 * @param {Array} routes 路由列表数组
 * @param {String} homeRouteName homeRouteName
 * @description 用于找到路由列表中name为home的对象
 */
export const getHomeRoute = (routes, homeRouteName) => {
  let i = -1
  const len = routes.length
  let homeRoute = {}
  while (++i < len) {
    const item = routes[i]
    if (item.children && item.children.length) {
      const res = getHomeRoute(item.children, homeRouteName)
      if (res.name) return res
    } else if (item.name === homeRouteName) homeRoute = item
  }
  return { ...homeRoute, meta: { ...homeRoute.meta, icon: 'md-home', title: '首页' } }
}

export const debounce = (fn, duration = 500) => {
  let timerId
  return function (...args) {
    clearTimeout(timerId)
    timerId = setTimeout(() => {
      fn.apply(this, args)
    }, duration)
  }
}

export const updateColorWeak = colorWeak => {
  const bodyStyle = document.body.style
  const attrsList = bodyStyle.filter.split(' ')
  const otherAttr = attrsList.filter(item => !item.includes('invert')).join(' ')
  bodyStyle.filter = colorWeak ? `${otherAttr} invert(80%)` : otherAttr
}

export const updateColorGrey = colorGrey => {
  const bodyStyle = document.body.style
  const attrsList = bodyStyle.filter.split(' ')
  const otherAttr = attrsList.filter(item => !item.includes('grayscale')).join(' ')
  bodyStyle.filter = colorGrey ? `${otherAttr} grayscale(95%)` : otherAttr
}

export const formatSeconds = seconds => {
  let secondTime = 0 // 秒
  let minuteTime = 0 // 分
  let hourTime = 0 // 小时
  let dayTime = 0 // 天

  if (seconds < 60) {
    secondTime = seconds
  } else {
    // 获取分钟，除以60取整数，得到整数分钟
    minuteTime = Math.floor(seconds / 60)
    // 获取秒数，秒数取佘，得到整数秒数
    secondTime = Math.floor(seconds % 60)
    // 如果分钟大于60，将分钟转换成小时
    if (minuteTime >= 60) {
      // 获取小时，获取分钟除以60，得到整数小时
      hourTime = Math.floor(minuteTime / 60)
      // 获取小时后取佘的分，获取分钟除以60取佘的分
      minuteTime = Math.floor(minuteTime % 60)
      if (hourTime >= 24) {
        // 获取天数， 获取小时除以24，得到整数天
        dayTime = Math.floor(hourTime / 24)
        // 获取小时后取余小时，获取分钟除以24取余的分；
        hourTime = Math.floor(hourTime % 24)
      }
    }
  }
  let result = `${hourTime}小时${minuteTime >= 10 ? minuteTime : `0${minuteTime}`}分${secondTime >= 10 ? secondTime : `0${secondTime}`}秒`
  if (dayTime > 0) {
    result = `${dayTime}天${result}`
  }
  return result
}

export const hadRunTime = passTime => {
  const nowDateTime = parseInt((new Date().getTime() / 1000).toString())
  const passDateTime = parseInt((new Date(passTime).getTime() / 1000).toString())
  const runTime = nowDateTime - passDateTime
  return formatSeconds(runTime)
}

export const getRandomId = (prefix = 'antd') => {
  const random = Math.floor(9000 * Math.random())
  return `${prefix}_${random}${String(Date.now())}`
}
