<template>
  <div class="ivu-verify-slide">
    <!-- 图片部分 -->
    <div class="verify-img-panel" :style="imgStyleOuter">
      <img :src="`data:image/png;base64,${backImgBase}`" />
      <div class="verify-refresh" @click="refresh" v-show="showRefresh">
        <Icon custom="i-icon i-icon-refresh" :size="24" />
      </div>
      <transition name="tips">
        <span :class="passFlag ? 'suc-bg' : 'err-bg'" class="verify-tips" v-if="tipWords">{{ tipWords }}</span>
      </transition>
    </div>

    <!-- 滑块部分 -->
    <div :style="areaStyleOuter" class="verify-bar-area">
      <span>{{ text }}</span>
      <div :style="slideBarStyle" class="verify-slide-bar">
        <div :style="moveBlockStyle" @mousedown="start" @touchstart="start" class="verify-move-block">
          <Icon :type="iconType" :size="24" />
          <div :style="subBlockOuter" class="verify-sub-block">
            <img :src="`data:image/png;base64,${blockBackImgBase}`" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script type="text/babel">
  /**
   * VerifySlide
   * @description 滑块
   * */
  import { Icon } from 'view-ui-plus'
  import { resetSize } from '../utils/util'
  import { aesEncrypt, reqGet, reqCheck } from '../utils/ase'

  export default {
    name: 'VerifySlide',
    components: { Icon },
    inject: ['Verify'],
    props: {
      // 弹出式pop，固定fixed
      mode: {
        type: String,
        default: 'fixed'
      },
      vSpace: {
        type: Number,
        default: 5
      },
      explain: {
        type: String,
        default: '向右滑动完成验证'
      },
      imgSize: {
        type: Object,
        default: () => ({ width: '310px', height: '155px' })
      },
      blockSize: {
        type: Object,
        default: () => ({ width: '50px', height: '50px' })
      },
      barSize: {
        type: Object,
        default: () => ({ width: '310px', height: '40px' })
      }
    },
    data() {
      return {
        captchaType: 'blockPuzzle',
        secretKey: '', // 后端返回的加密秘钥 字段
        passFlag: '', // 是否通过的标识
        backImgBase: '', // 验证码背景图片
        blockBackImgBase: '', // 验证滑块的背景图片
        backToken: '', // 后端返回的唯一token值
        startMoveTime: '', // 移动开始的时间
        endMovetime: '', // 移动结束的时间
        tipsBackColor: '', // 提示词的背景颜色
        tipWords: '',
        text: '',
        setSize: {
          imgHeight: 0,
          imgWidth: 0,
          barHeight: 0,
          barWidth: 0
        },
        top: 0,
        left: 0,
        moveBlockLeft: undefined,
        leftBarWidth: undefined,
        // 移动中样式
        moveBlockBackgroundColor: undefined,
        leftBarBorderColor: '#ddd',
        status: false, // 鼠标状态
        isEnd: false, // 是够验证完成
        showRefresh: true,
        transitionLeft: '',
        transitionWidth: '',
        iconType: 'ios-arrow-forward'
      }
    },
    computed: {
      barArea() {
        return this.$el.querySelector('.verify-bar-area')
      },
      resetSize() {
        return resetSize
      },
      imgStyleOuter() {
        return {
          width: this.setSize.imgWidth,
          height: this.setSize.imgHeight,
          marginBottom: `${this.vSpace}px`
        }
      },
      areaStyleOuter() {
        return {
          width: this.setSize.imgWidth,
          height: this.barSize.height,
          lineHeight: this.barSize.height
        }
      },
      slideBarStyle() {
        return {
          width: this.leftBarWidth ? this.leftBarWidth : this.barSize.height,
          height: this.barSize.height,
          borderColor: this.leftBarBorderColor,
          transaction: this.transitionWidth
        }
      },
      moveBlockStyle() {
        return {
          width: this.barSize.height,
          height: this.barSize.height,
          backgroundColor: this.moveBlockBackgroundColor,
          left: this.moveBlockLeft,
          transition: this.transitionLeft
        }
      },
      subBlockOuter() {
        return {
          width: `${Math.floor((parseInt(this.setSize.imgWidth) * 47) / 310)}px`,
          height: this.setSize.imgHeight,
          top: `-${parseInt(this.setSize.imgHeight) + this.vSpace}px`,
          backgroundSize: `${this.setSize.imgWidth} ${this.setSize.imgHeight}`
        }
      }
    },
    mounted() {
      this.init()
      // 禁止拖拽
      this.$el.onselectstart = () => false
    },
    methods: {
      init() {
        this.text = this.explain
        this.getPicture()
        this.$nextTick(() => {
          const { imgWidth, imgHeight, barWidth, barHeight } = this.resetSize(this) // 重新设置宽度高度

          this.setSize.imgWidth = imgWidth
          this.setSize.barWidth = barWidth
          this.setSize.imgHeight = imgHeight
          this.setSize.barHeight = barHeight
          this.Verify.$emit('on-ready', this)
        })

        const that = this

        window.removeEventListener('touchmove', e => that.move(e))
        window.removeEventListener('mousemove', e => that.move(e))

        // 鼠标松开
        window.removeEventListener('touchend', () => that.end())
        window.removeEventListener('mouseup', () => that.end())

        window.addEventListener('touchmove', e => that.move(e))
        window.addEventListener('mousemove', e => that.move(e))

        // 鼠标松开
        window.addEventListener('touchend', () => that.end())
        window.addEventListener('mouseup', () => that.end())
      },

      // 鼠标按下
      start(e) {
        e = e || window.event
        let x
        if (!e.touches) {
          // 兼容PC端
          x = e.clientX
        } else {
          // 兼容移动端
          // eslint-disable-next-line no-redeclare
          x = e.touches[0].pageX
        }
        this.startLeft = Math.floor(x - this.barArea.getBoundingClientRect().left)
        this.startMoveTime = +new Date() // 开始滑动的时间
        if (!this.isEnd) {
          this.text = ''
          this.moveBlockBackgroundColor = '#337ab7'
          this.leftBarBorderColor = '#337AB7'
          e.stopPropagation()
          this.status = true
        }
      },
      // 鼠标移动
      move(e) {
        e = e || window.event
        if (this.status && !this.isEnd) {
          let x
          if (!e.touches) {
            // 兼容PC端
            x = e.clientX
          } else {
            // 兼容移动端
            // eslint-disable-next-line no-redeclare
            x = e.touches[0].pageX
          }
          // eslint-disable-next-line camelcase
          const bar_area_left = this.barArea.getBoundingClientRect().left
          // eslint-disable-next-line camelcase
          let move_block_left = x - bar_area_left // 小方块相对于父元素的left值
          // eslint-disable-next-line camelcase
          if (move_block_left >= this.barArea.offsetWidth - parseInt(parseInt(this.blockSize.width) / 2) - 2) {
            // eslint-disable-next-line camelcase
            move_block_left = this.barArea.offsetWidth - parseInt(parseInt(this.blockSize.width) / 2) - 2
          }
          // eslint-disable-next-line camelcase
          if (move_block_left <= 0) {
            // eslint-disable-next-line camelcase
            move_block_left = parseInt(parseInt(this.blockSize.width) / 2)
          }
          // 拖动后小方块的left值
          // eslint-disable-next-line camelcase
          this.moveBlockLeft = `${move_block_left - this.startLeft}px`
          // eslint-disable-next-line camelcase
          this.leftBarWidth = `${move_block_left - this.startLeft}px`
        }
      },

      // 鼠标松开
      end() {
        this.endMovetime = +new Date()
        const _this = this
        // 判断是否重合
        if (this.status && !this.isEnd) {
          let moveLeftDistance = parseInt((this.moveBlockLeft || '').replace('px', ''))
          moveLeftDistance = (moveLeftDistance * 310) / parseInt(this.setSize.imgWidth)
          const data = {
            captchaType: this.captchaType,
            pointJson: this.secretKey
              ? aesEncrypt(JSON.stringify({ x: moveLeftDistance, y: 5.0 }), this.secretKey)
              : JSON.stringify({ x: moveLeftDistance, y: 5.0 }),
            token: this.backToken
          }
          reqCheck(data).then(res => {
            if (res.repCode === '0000') {
              this.moveBlockBackgroundColor = '#5cb85c'
              this.leftBarBorderColor = '#5cb85c'
              this.iconType = 'md-checkmark'
              this.showRefresh = false
              this.isEnd = true
              this.passFlag = true
              this.tipWords = `${((this.endMovetime - this.startMoveTime) / 1000).toFixed(2)}s验证成功`
              const captchaVerification = this.secretKey
                ? aesEncrypt(
                    `${this.backToken}---${JSON.stringify({
                      x: moveLeftDistance,
                      y: 5.0
                    })}`,
                    this.secretKey
                  )
                : `${this.backToken}---${JSON.stringify({
                    x: moveLeftDistance,
                    y: 5.0
                  })}`
              setTimeout(() => {
                this.tipWords = ''
                this.Verify.$emit('on-cancel')
                this.Verify.$emit('on-success', { captchaVerification })
              }, 1000)
            } else {
              this.moveBlockBackgroundColor = '#d9534f'
              this.leftBarBorderColor = '#d9534f'
              this.iconType = 'md-close'
              this.passFlag = false
              setTimeout(() => {
                _this.refresh()
              }, 1000)
              this.Verify.$emit('on-fail', this)
              this.tipWords = '验证失败'
              setTimeout(() => {
                this.tipWords = ''
              }, 1000)
            }
          })
          this.status = false
        }
      },

      refresh() {
        this.showRefresh = true

        this.transitionLeft = 'left .3s'
        this.moveBlockLeft = 0

        this.leftBarWidth = undefined
        this.transitionWidth = 'width .3s'

        this.leftBarBorderColor = '#ddd'
        this.moveBlockBackgroundColor = '#fff'
        this.iconType = 'ios-arrow-forward'
        this.isEnd = false

        this.getPicture()
        setTimeout(() => {
          this.transitionWidth = ''
          this.transitionLeft = ''
          this.text = this.explain
        }, 300)
      },

      // 请求背景图片和验证图片
      getPicture() {
        const data = { captchaType: this.captchaType }
        reqGet(data)
          .then(res => {
            if (res.repCode === '0000') {
              this.backImgBase = res.repData.originalImageBase64
              this.blockBackImgBase = res.repData.jigsawImageBase64
              this.backToken = res.repData.token
              this.secretKey = res.repData.secretKey
            } else {
              this.tipWords = res.repMsg
            }
          })
          .catch(error => this.Verify.$emit('on-error', error))
      }
    }
  }
</script>
<style lang="less">
  .ivu-verify-slide {
    position: relative;
    overflow: hidden;

    .verify-img-panel {
      position: relative;
      overflow: hidden;
      box-sizing: content-box;
      border-left: 1px solid transparent;
      border-right: 1px solid transparent;

      img {
        width: 100%;
        height: 100%;
        display: block;
      }

      .suc-bg {
        background: rgba(92, 184, 92, 0.5);
      }

      .err-bg {
        background: rgba(217, 83, 79, 0.5);
      }

      .verify-refresh {
        position: absolute;
        top: 0;
        right: 2px;
        z-index: 2;
        padding: 4px;
        cursor: pointer;

        .anticon {
          color: #fff;
          font-size: 16px;
        }
      }
    }

    .verify-bar-area {
      position: relative;
      box-sizing: content-box;
      text-align: center;
      border: 1px solid #ddd;
      border-radius: 2px;

      .verify-move-block {
        position: absolute;
        top: 0;
        left: 0;
        background: #fff;
        cursor: pointer;
        box-sizing: content-box;
        box-shadow: 0 0 1px #888;
        border-radius: 1px;

        .verify-sub-block {
          position: absolute;
          text-align: center;
          z-index: 3;

          img {
            width: 100%;
            height: 100%;
            display: block;
          }
        }

        .anticon {
          font-size: 16px;
        }
      }

      .verify-slide-bar {
        position: absolute;
        top: -1px;
        left: -1px;
        background: #f0fff0;
        cursor: pointer;
        box-sizing: content-box;
        border: 1px solid #ddd;
      }
    }
  }

  .verify-tips {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 30px;
    line-height: 30px;
    color: #fff;
  }

  .tips-enter,
  .tips-leave-to {
    bottom: -30px;
  }

  .tips-enter-active,
  .tips-leave-active {
    transition: bottom 0.5s;
  }
</style>
