<template>
  <span>
    <XIcon class="ivu-mr-4" v-if="showIcon" :type="iconType" :size="iconSize" />
    <span>{{ time }}</span>
  </span>
</template>

<script>
  import dayjs from 'dayjs'

  export default {
    name: 'NowTime',
    props: {
      format: {
        type: String,
        default: 'YYYY年MM月DD日 HH:mm:ss'
      },
      onlyTime: {
        type: Boolean,
        default: false
      },
      showIcon: {
        type: Boolean,
        default: false
      },
      iconType: {
        type: String,
        default: 'md-time'
      },
      iconSize: {
        type: Number,
        default: 16
      }
    },
    data() {
      return {
        time: 'Loading...'
      }
    },
    created() {
      this.initTime()
    },
    methods: {
      initTime() {
        if (this.onlyTime) {
          setInterval(() => (this.time = dayjs().format('HH:mm:ss')), 1000)
          return
        }
        this.time = this.getFormatNowTime()
        setInterval(() => (this.time = this.getFormatNowTime()), 1000)
      },
      getFormatNowTime() {
        const weekList = ['日', '一', '二', '三', '四', '五', '六']
        return `${dayjs().format(this.format)} 星期${weekList[dayjs().day()]}`
      }
    }
  }
</script>
