import { createRouter, createWebHistory, createWebHashHistory, createMemoryHistory } from 'vue-router'
import { routerMode, routerBase } from '@/config'
import { constantRoutes, asyncRoutes } from '@/router/routes'

const router = createRouter({
  routes: [...constantRoutes, ...asyncRoutes], // FIXME:登录有问题先全部写入路由
  history:
    routerMode === 'history'
      ? createWebHistory(routerBase)
      : routerMode === 'hash'
      ? createWebHashHistory(routerBase)
      : createMemoryHistory(routerBase),
  scrollBehavior: () => document.getElementById('app').scrollTo(0, 0)
})

export default router
