const color = (type = 'default') => {
  const typeMap = {
    default: '#515a6e',
    primary: '#2d8cf0',
    success: '#19be6b',
    warning: '#ff9900',
    error: '#ed4014'
  }
  return typeMap[type]
}

const colorful = array => {
  console.log(`%c${array.map(e => e.text || '').join('%c')}`, ...array.map(e => `color: ${color(e.type)};`))
}

const defaultLog = text => colorful([{ text }])

const $console = {
  capsule: (prefix, suffix, type = 'primary') => {
    const isColor = typeof type === 'string' && type.includes('#')
    console.log(
      `%c ${prefix} %c ${suffix} %c`,
      'background:#35495E; padding: 1px; border-radius: 3px 0 0 3px; color: #fff;',
      `background:${isColor ? type : color(type)}; padding: 1px; border-radius: 0 3px 3px 0; color: #fff;`,
      'background:transparent'
    )
  },
  log: defaultLog,
  default: defaultLog,
  primary: text => {
    colorful([
      {
        text,
        type: 'primary'
      }
    ])
  },
  success: text => {
    colorful([
      {
        text,
        type: 'success'
      }
    ])
  },
  warning: text => {
    colorful([
      {
        text,
        type: 'warning'
      }
    ])
  },
  error: text => {
    colorful([
      {
        text,
        type: 'error'
      }
    ])
  }
}

$console.install = app => (app.config.globalProperties.$console = $console)

export default $console
