import { LoginLayout, BasicLayout, BlankLayout, ParentView } from '@/layouts'

/**
 * iView Admin Pro中meta除了原生参数外可配置的参数:
 * meta: {
 *  title: { String|Number|Function }
 *         显示在侧边栏、面包屑和标签栏的文字
 *         使用'{{ 多语言字段 }}'形式结合多语言使用，例子看多语言的路由配置;
 *         可以传入一个回调函数，参数是当前路由对象，例子看动态路由和带参路由
 *  hideInBread: (false) 设为true后此级路由将不会出现在面包屑中
 *  hideInMenu: (false) 设为true后在左侧菜单不会显示该页面选项
 *  showChild: (false) 设为true后只有一个子路由时菜单显示子路由
 *  hideChildrenInMenu: (false) 设为true后该路由的子路由不会在左侧菜单中
 *  keepAlive: (false) 设为true后页面在切换标签后会缓存，如果不需要缓存，无需设置这个字段，而且需要设置页面组件name属性和路由配置的name一致
 *  permission: (null) 可访问该页面的权限数组，当前路由设置的权限会影响子路由
 *  closable: (true) 设为false后在多页签中不能关闭
 *  icon: (-) 该页面在左侧菜单、面包屑和标签导航处显示的图标，自定义图标请参考具体使用'
 *  beforeFn: (-) 设置该字段，则在关闭当前tab页时会去'@/router/beforeFn.js'里寻找该字段名对应的方法，作为关闭前的钩子函数
 * }
 */
export const asyncRoutes = [
  // dashboard
  {
    path: '/dashboard',
    name: 'dashboard',
    redirect: '/dashboard/workplace',
    component: BasicLayout,
    meta: {
      title: 'Dashboard',
      keepAlive: true,
      icon: 'i-icon-demo dashboard',
      permission: ['dashboard']
    },
    children: [
      {
        path: 'workplace',
        name: 'Workplace',
        meta: {
          title: '工作台',
          subtitle: 'Workplace',
          closable: false,
          keepAlive: true,
          permission: ['dashboard']
        },
        component: () => import(/* webpackChunkName: "dashboard" */ '@/views/dashboard/Workplace')
      },
      {
        path: 'console',
        name: 'Console',
        meta: {
          title: '主控台',
          closable: false,
          keepAlive: true,
          permission: ['dashboard']
        },
        component: () => import(/* webpackChunkName: "dashboard" */ '@/views/dashboard/Console')
      },
      {
        path: 'monitor',
        name: 'Monitor',
        meta: { title: '监控页', keepAlive: true, permission: ['dashboard'] },
        component: () => import(/* webpackChunkName: "dashboard" */ '@/views/dashboard/Monitor')
      },
      {
        path: 'components',
        name: 'Components',
        meta: {
          title: '组件库',
          closable: false,
          keepAlive: true,
          permission: ['dashboard']
        },
        component: () => import(/* webpackChunkName: "dashboard" */ '@/views/dashboard/Components')
      }
    ]
  },

  // system
  {
    path: '/system',
    name: 'system',
    redirect: '/system/user',
    component: BasicLayout,
    meta: {
      title: '系统管理',
      keepAlive: true,
      icon: 'ios-settings',
      permission: ['system']
    },
    children: [
      {
        path: 'user',
        name: 'User',
        meta: { title: '用户管理', keepAlive: true, permission: ['system'] },
        component: () => import(/* webpackChunkName: "system" */ '@/views/system/User')
      },
      {
        path: 'menu',
        name: 'Menu',
        meta: { title: '菜单管理', keepAlive: true, permission: ['system'] },
        component: () => import(/* webpackChunkName: "system" */ '@/views/system/Menu')
      },
      {
        path: 'role',
        name: 'Role',
        meta: { title: '角色管理', keepAlive: true, permission: ['system'] },
        component: () => import(/* webpackChunkName: "system" */ '@/views/system/Role')
      }
    ]
  },

  {
    path: '/tools',
    name: 'tools',
    component: BasicLayout,
    redirect: '/tools/capture-log',
    meta: {
      title: '内置插件',
      icon: 'i-icon-demo tool',
      permission: ['tools']
    },
    children: [
      {
        path: 'capture-log',
        name: 'capture-log',
        meta: { title: '错误捕捉', permission: ['tools'] },
        component: () => import(/* webpackChunkName: "tools" */ '@/views/tools/capture-log')
      },
      {
        path: 'console-log',
        name: 'console-log',
        meta: { title: '日志打印', permission: ['tools'] },
        component: () => import(/* webpackChunkName: "tools" */ '@/views/tools/console-log')
      }
    ]
  },

  {
    path: '/log',
    component: BasicLayout,
    meta: { hideInBread: true, hideInMenu: true, showChild: true },
    children: [
      {
        path: '/log',
        name: 'log',
        meta: { title: '前端日志', icon: 'md-locate', permission: ['tools'] },
        component: () => import(/* webpackChunkName: "tools" */ '@/views/tools/alarm-log')
      }
    ]
  },

  {
    path: '/example',
    name: 'example',
    meta: { title: '常用实例', icon: 'md-apps', permission: ['example'] },
    redirect: '/example/iframe',
    component: BasicLayout,
    children: [
      {
        path: 'iframe',
        name: 'iframe',
        meta: {
          title: '内嵌页面',
          keepAlive: true,
          permission: ['example'],
          frameSrc: 'https://istudy-run.gitee.io/relax-time#/'
        },
        component: () => import(/* webpackChunkName: "example" */ '@/views/iframe/index.vue')
      },
      {
        path: 'https://istudy-run.gitee.io/relax-time#/',
        name: 'relax-time',
        meta: { title: '外链页面', target: '_blank', permission: ['example'] }
      },
      {
        path: 'version',
        name: 'Version',
        meta: { title: '更新日志', keepAlive: true, permission: ['example'] },
        component: () => import(/* webpackChunkName: "example" */ '@/views/version/index.vue')
      }
    ]
  },

  // forms
  {
    path: '/form',
    name: 'form',
    redirect: '/form/base-form',
    component: BasicLayout,
    meta: { title: '表单页面', icon: 'i-icon-demo form', permission: ['form'] },
    children: [
      {
        path: '/form/base-form',
        name: 'BaseForm',
        meta: { title: '基础表单', keepAlive: true, permission: ['form'] },
        component: () => import(/* webpackChunkName: "form" */ '@/views/form/BasicForm')
      },
      {
        path: '/form/step-form',
        name: 'StepForm',
        meta: { title: '分步表单', keepAlive: true, permission: ['form'] },
        component: () => import(/* webpackChunkName: "form" */ '@/views/form/stepForm/StepForm')
      },
      {
        path: '/form/advanced-form',
        name: 'AdvancedForm',
        meta: { title: '高级表单', keepAlive: true, permission: ['form'] },
        component: () => import(/* webpackChunkName: "form" */ '@/views/form/advancedForm/AdvancedForm')
      },
      {
        path: '/form/form-detail',
        name: 'FormDetail',
        meta: { title: '表单详情', keepAlive: true, permission: ['form'] },
        component: () => import(/* webpackChunkName: "form" */ '@/views/form/FormDetail')
      }
    ]
  },

  // list
  {
    path: '/list',
    name: 'list',
    component: BasicLayout,
    redirect: '/list/table-list',
    meta: { title: '列表页面', icon: 'i-icon-demo list', permission: ['list'] },
    children: [
      {
        path: '/list/basic-list',
        name: 'BasicList',
        meta: { title: '基础列表', keepAlive: true, permission: ['list'] },
        component: () => import(/* webpackChunkName: "list" */ '@/views/list/StandardList')
      },
      {
        path: '/list/card',
        name: 'CardList',
        meta: { title: '卡片列表', keepAlive: true, permission: ['list'] },
        component: () => import(/* webpackChunkName: "list" */ '@/views/list/CardList')
      },
      {
        path: '/list/user',
        name: 'UserList',
        meta: { title: '用户列表', keepAlive: true, permission: ['list'] },
        component: () => import(/* webpackChunkName: "list" */ '@/views/list/UserList')
      },
      {
        path: '/list/table-list',
        name: 'TableList',
        meta: { title: '查询表格', keepAlive: true, permission: ['list'] },
        component: () => import(/* webpackChunkName: "list" */ '@/views/list/TableList')
      }
    ]
  },

  // description
  {
    path: '/description',
    name: 'description',
    component: BasicLayout,
    redirect: '/description/basic',
    meta: {
      title: '详情页面',
      icon: 'i-icon-demo profile',
      permission: ['description']
    },
    children: [
      {
        path: 'basic',
        name: 'Basic',
        meta: { title: '基础详情页', permission: ['description'] },
        component: () => import(/* webpackChunkName: "description" */ '@/views/description/Basic')
      },
      {
        path: 'advanced',
        name: 'Advanced',
        meta: { title: '高级详情页', permission: ['description'] },
        component: () => import(/* webpackChunkName: "description" */ '@/views/description/Advanced')
      }
    ]
  },

  // search
  {
    path: '/search',
    name: 'searchList',
    component: BasicLayout,
    redirect: '/search/article',
    meta: {
      title: '搜索页面',
      icon: 'i-icon-demo search',
      keepAlive: true,
      permission: ['search']
    },
    children: [
      {
        path: '/search/article',
        name: 'SearchArticles',
        meta: { title: '搜索列表（文章）', permission: ['search'] },
        component: () => import(/* webpackChunkName: "search" */ '../views/search/Article')
      },
      {
        path: '/search/project',
        name: 'SearchProjects',
        meta: { title: '搜索列表（项目）', permission: ['search'] },
        component: () => import(/* webpackChunkName: "search" */ '../views/search/Projects')
      },
      {
        path: '/search/application',
        name: 'SearchApplications',
        meta: { title: '搜索列表（应用）', permission: ['search'] },
        component: () => import(/* webpackChunkName: "search" */ '../views/search/Applications')
      }
    ]
  },

  // Exception
  {
    path: '/exception',
    name: 'exception',
    component: BasicLayout,
    redirect: '/exception/403',
    meta: {
      title: '异常页面',
      icon: 'i-icon-demo exception',
      permission: ['exception']
    },
    children: [
      {
        path: '/exception/403',
        name: 'Exception403',
        meta: { title: '403', permission: ['exception'] },
        component: () => import(/* webpackChunkName: "exception" */ '@/views/exception/403')
      },
      {
        path: '/exception/404',
        name: 'Exception404',
        meta: { title: '404', permission: ['exception'] },
        component: () => import(/* webpackChunkName: "exception" */ '@/views/exception/404')
      },
      {
        path: '/exception/500',
        name: 'Exception500',
        meta: { title: '500', permission: ['exception'] },
        component: () => import(/* webpackChunkName: "exception" */ '@/views/exception/500')
      }
    ]
  },

  // account
  {
    path: '/user',
    name: 'UserCenter',
    component: BasicLayout,
    redirect: '/user/profile',
    meta: {
      title: '个人中心',
      hideInMenu: true,
      icon: 'i-icon-demo setting',
      keepAlive: true,
      permission: ['user']
    },
    children: [
      {
        path: '/user/profile',
        name: 'UserProfile',
        meta: { title: '个人资料', keepAlive: true, permission: ['user'] },
        component: () => import(/* webpackChunkName: "user" */ '@/views/account/UserProfile')
      },
      {
        path: '/user/settings',
        name: 'UserSetting',
        meta: { title: '账户设置', keepAlive: true, permission: ['user'] },
        component: () => import(/* webpackChunkName: "user" */ '@/views/account/UserSetting')
      }
    ]
  },

  // result
  {
    path: '/result',
    name: 'result',
    component: BasicLayout,
    redirect: '/result/success',
    meta: {
      title: '结果页面',
      icon: 'i-icon-demo result',
      permission: ['result']
    },
    children: [
      {
        path: '/result/success',
        name: 'ResultSuccess',
        meta: { title: '成功', permission: ['result'] },
        component: () => import(/* webpackChunkName: "result" */ '@/views/result/Success')
      },
      {
        path: '/result/fail',
        name: 'ResultFail',
        meta: { title: '失败', permission: ['result'] },
        component: () => import(/* webpackChunkName: "result" */ '@/views/result/Error')
      }
    ]
  },

  {
    path: '/:pathMatch(.*)*',
    meta: { title: '404', hideInMenu: true },
    redirect: '/404'
  }
]

/**
 * 基础路由
 * @type { *[] }
 */
export const constantRoutes = [
  {
    path: '/',
    component: LoginLayout,
    redirect: '/login',
    children: [
      {
        path: 'login',
        name: 'login',
        meta: { title: '登录' },
        component: () => import(/* webpackChunkName: "login" */ '@/views/login/Login')
      },
      {
        path: 'register',
        name: 'register',
        meta: { title: '注册' },
        component: () => import(/* webpackChunkName: "login" */ '@/views/login/Register')
      },
      {
        path: 'register-result',
        name: 'registerResult',
        component: () => import(/* webpackChunkName: "login" */ '@/views/login/RegisterResult')
      }
    ]
  },
  {
    path: '/404',
    component: () => import(/* webpackChunkName: "404" */ '@/views/exception/404')
  }
]
