import Storage from 'vue-ls'
import config from '@/config'

const storageNameSpace = config.namespace
const storageType = {
  Local: 'local',
  Session: 'session'
}

class StorageCache {
  constructor(storage) {
    const mergeOptions = { ...config.storageOptions, storage }
    const { ls } = Storage.useStorage(mergeOptions)
    this.storage = ls
  }

  set(key, value, expire) {
    this.storage.set(key, value, expire)
  }

  get(key, defVal) {
    return this.storage.get(key, defVal)
  }

  remove(key) {
    return this.storage.remove(key)
  }

  clear() {
    this.storage.clear()
  }
}

const storageLocalWithModule = {
  getStorage() {
    return JSON.parse(window.localStorage.getItem(storageNameSpace) || '{}')
  },
  set(key, val, module) {
    if (module) {
      const moduleVal = this.get(module)
      moduleVal[key] = val
      this.set(module, moduleVal)
    } else {
      const storage = this.getStorage()
      storage[key] = val
      window.localStorage.setItem(storageNameSpace, JSON.stringify(storage))
    }
  },
  get(key, module) {
    if (module) {
      const moduleVal = this.get(module)
      if (moduleVal) return moduleVal[key]
    }
    return this.getStorage()[key]
  },
  remove(key, module) {
    const storage = this.getStorage()
    if (module) {
      delete storage[module][key]
    } else {
      delete storage[key]
    }
    window.localStorage.setItem(storageNameSpace, JSON.stringify(storage))
  },
  clear() {
    window.localStorage.clear()
  }
}

const storageLocal = new StorageCache(storageType.Local)
const storageSession = new StorageCache(storageType.Session)

export { storageLocal as storage, storageSession, storageLocalWithModule }

export default {
  install(app) {
    app.config.globalProperties.$storage = storageLocal
  }
}
