<template>
  <span :class="`${BasicLayout.prefixCls}-header-tool-item`" @click="handleCollapse">
    <Icon :custom="`i-icon i-icon-${collapsed ? 'menu-unfold' : 'menu-fold'}`" :size="iconSize" />
  </span>
</template>

<script>
  export default {
    name: 'HeaderCollapse',
    inject: ['BasicLayout'],
    props: {
      iconSize: {
        type: [String, Number],
        default: 18
      }
    },
    computed: {
      collapsed() {
        return this.BasicLayout.menuCollapse
      }
    },
    methods: {
      handleCollapse() {
        this.BasicLayout.handleCollapse()
      }
    }
  }
</script>
