<template>
  <draggable v-model="currentValue" :animation="animation" v-bind="$attrs">
    <slot></slot>
  </draggable>
</template>

<script>
  import draggable from 'vuedraggable'

  export default {
    name: 'VueDraggable',
    emits: ['update:modelValue'],
    components: { draggable },
    props: {
      modelValue: {
        type: Array,
        default: () => []
      },
      animation: {
        type: Number,
        default: 300
      }
    },
    data() {
      return {
        currentValue: this.modelValue
      }
    },
    watch: {
      value(val) {
        this.currentValue = val
      },
      currentValue(val) {
        this.$emit('update:modelValue', val)
      }
    }
  }
</script>
