          <p class="ivu-pb-16 ivu-pt-8">咨询时间：工作日 9:30 - 18:00</p>
          <div :class="`${prefixCls}-wechat-cover`"></div>
        </div>
      </Modal>
    </div>
  </div>
</template>

<script>
  import { Modal, Icon, Tooltip } from 'view-ui-plus'

  export default {
    name: 'Consult',
    components: { Modal, Icon, Tooltip },
    data() {
      return {
        prefixCls: `${this.$config.cssPrefix}consult`,
