import config from '@/config'
import { welcome } from '@/utils/util'
import { storage } from '@/utils/storage'
import { login, getInfo, logout } from '@/api/login'
import { ACCESS_TOKEN } from '@/store/mutation-types'

export default {
  state: {
    token: '',
    welcome: '',
    userRole: [],
    userInfo: {}
  },
  mutations: {
    [ACCESS_TOKEN](state, token) {
      state.token = token
      !token && storage.remove(ACCESS_TOKEN)
    },
    SET_USER_ROLE(state, userRole) {
      state.userRole = userRole
    },
    SET_USER_INFO(state, userInfo) {
      state.userInfo = userInfo
      state.welcome = welcome()
    }
  },
  actions: {
    login({ commit }, userInfo) {
      return new Promise((resolve, reject) => {
        login(userInfo)
          .then(data => {
            storage.set(ACCESS_TOKEN, data.token, config.tokenExpire)
            commit(ACCESS_TOKEN, data.token)
            resolve(data)
          })
          .catch(error => {
            reject(error)
          })
      })
    },
    logout({ commit, state }, vm) {
      return new Promise(resolve => {
        const logoutAction = () => {
          logout(state.token)
            .then(() => {
              commit(ACCESS_TOKEN, '')
              commit('SET_USER_ROLE', [])
              storage.remove(ACCESS_TOKEN)
              resolve()
            })
            .catch(error => {
              console.log('error:', error)
              // @see https://gitee.com/sendya/ant-design-pro-vue/commit/4747677464a363fe2987abd6c61edeb0d240223b
              // resolve()
            })
        }
        if (vm.$config.logoutConfirm) {
          vm.$Modal.confirm({
            title: '系统提示',
            content: '您确定退出登录当前账户吗？打开的标签页和个人设置将会保存。',
            onOk: () => logoutAction()
          })
        } else logoutAction()
      })
    },
    getUserInfo({ commit }) {
      return new Promise((resolve, reject) => {
        getInfo()
          .then(data => {
            if (data.role && data.role.permissions.length) {
              const role = data.role
              role.permissions = data.role.permissions
              role.permissions.forEach(auth => {
                if (auth.actionEntitySet != null && auth.actionEntitySet.length) {
                  auth.actionList = auth.actionEntitySet.map(action => action.action)
                }
              })
              role.permissionList = role.permissions.map(permission => permission.permissionId)
              commit('SET_USER_ROLE', [data.role])
              commit('SET_USER_INFO', data)
            } else {
              reject(new Error('getInfo: userRole must be a non-null array !'))
            }
            resolve(data)
          })
          .catch(error => {
            reject(error)
          })
      })
    }
  }
}
