/**
 * 过滤掉隐藏的菜单
 * @param routes
 * @returns {*}
 */

export const filterMenus = routes => {
  const res = routes.filter(item => {
    if (!item.meta.hideInMenu) {
      item.children && (item.children = filterMenus(item.children))
      return item
    }
  })
  return convertRoutePath(res)
}

/**
 * 转换菜单RoutePath为相对路径
 * @param routes
 * @returns {*}
 */
export const convertRoutePath = routes => {
  const res = routes.map(item => {
    item.children &&
      item.children.forEach(child => {
        if (!child.path.startsWith('/') && !child.path.startsWith('http')) {
          child.path = item.path.replace(/(\w*)[/]*$/, `$1/${child.path}`)
          if (child.children) convertRoutePath([child])
        }
      })
    return { ...item }
  })
  return formatMenus(res)
}

/**
 * 对MenuList结构统一格式化
 * @param menus
 * @returns {*}
 */
export const formatMenus = menus => {
  return menus.map(item => {
    if (manyChild(item)) {
      return {
        path: item.path,
        name: item.name,
        meta: item.meta,
        children: formatMenus(item.children)
      }
    }
    const showChild = !!item.meta.showChild && oneChild(item)
    const newChild = {
      path: getPathOrHref(item, showChild),
      name: (showChild && item.children[0].name) || item.name,
      meta: (showChild && item.children[0].meta) || item.meta
    }
    if (!item.meta.showChild && oneChild(item)) {
      // 有一个子菜单且不显示默认子路由添加Children
      newChild.children = formatMenus(item.children)
    }
    return newChild
  })
}

export const oneChild = item => item.children && item.children.length === 1

export const manyChild = item => item.children && item.children.length > 1

export const getPathOrHref = (item, child0) => (item.meta?.href ? item.meta.href : child0 ? item.children[0].path : item.path)

export const findOpenTabs = (menus, result = []) => {
  menus.forEach(route => {
    if (route.meta && route.meta.closable === false) {
      if (!result.some(item => item.path === route.path)) {
        result.push(route)
      }
    }
    if (route.children && route.children.length) findOpenTabs(route.children, result)
  })
  return result
}

/**
 * 获取当前一级菜单下的子菜单
 * @param vm vue实例
 * @param menuList
 * @returns {*}
 */
export const filterSubMenus = (vm, menuList) => {
  const matched = vm.$route.matched
  const hideMenu = matched.some(item => item.meta.hideInMenu)
  if (hideMenu) return []
  const mainPath = matched[0].path
  const mainMenu = menuList.find(item => item.path === mainPath)
  return mainMenu?.children?.filter(item => !item.meta.hideInMenu) || []
}
