<template>
  <Dropdown :class="dropdownClass" @mouseenter="dropOpened = true" @mouseleave="dropOpened = false" :placement="topLevel ? 'bottom' : 'right-start'">
    <li v-if="topLevel" :class="menuItemClass">
      <MenuHeadTitle :menu="menu" />
      <Icon v-if="arrowIcon" type="ios-arrow-down" />
    </li>
    <DropdownItem v-else>
      <MenuHeadTitle :menu="menu" :selected="routeMatched" />
      <Icon type="ios-arrow-forward" />
    </DropdownItem>
    <template #list>
      <DropdownMenu :style="dropMenuStyle">
        <template v-for="item in menu.children">
          <MenuHeadSubmenu v-if="item.children && item.children.length" :menu="item" :key="item.path" />
          <a v-else class="ivu-link ivu-link-color" :href="linkUrl(item.path)" :target="item.meta.target" :key="item.path">
            <DropdownItem :class="dropdownItemClass(item.path)">
              <MenuHeadTitle :menu="item" />
            </DropdownItem>
          </a>
        </template>
      </DropdownMenu>
    </template>
  </Dropdown>
</template>

<script>
  import { getStyle } from 'view-ui-plus/src/utils/assist'
  import mixins from '../MenuSide/mixins'
  import MenuHeadTitle from './MenuHeadTitle'

  export default {
    name: 'MenuHeadSubmenu',
    mixins: [mixins],
    components: { MenuHeadTitle },
    props: {
      menu: {
        type: Object,
        default: () => {}
      },
      theme: {
        type: String,
        default: 'light'
      },
      topLevel: {
        type: Boolean,
        default: false
      },
      arrowIcon: {
        type: Boolean,
        default: true
      }
    },
    data() {
      return {
        dropWidth: parseFloat(getStyle(this.$el, 'width')),
        dropOpened: false
      }
    },
    watch: {
      dropOpened(val) {
        if (val) {
          this.dropWidth = parseFloat(getStyle(this.$el, 'width'))
        }
      }
    },
    computed: {
      dropdownClass() {
        return {
          [`${this.prefixCls}-head-submenu`]: this.topLevel,
          [`${this.prefixCls}-head-submenu-opened`]: this.dropOpened
        }
      },
      dropMenuStyle() {
        const style = {}
        if (this.dropWidth) style.minWidth = `${this.dropWidth}px`
        return style
      },
      menuItemClass() {
        return [
          'ivu-menu-item',
          {
            'ivu-menu-item-active ivu-menu-item-selected': this.routeMatched
          }
        ]
      },
      dropdownItemClass() {
        return path => {
          const sameCurrentPath = path === this.$route.path
          const matchPatentPath = path === this.$route.matched[0].path
          return {
            [`${this.prefixCls}-head-dropdown-item-selected`]: sameCurrentPath || matchPatentPath
          }
        }
      }
    }
  }
</script>
