<template>
  <div :class="classes" :style="{ zIndex: zIndex }">
    <div :class="`${prefixCls}-box`" :style="boxStyle"></div>
    <Poptip ref="popoverRef" v-bind="popProps" :style="popStyle">
      <template #content v-if="steps && step">
        <div v-if="step.title" :class="`${prefixCls}-title`">
          <slot name="title" :step="step">{{ step.title }}</slot>
        </div>
        <div :class="`${prefixCls}-text`">
          <slot name="text" :step="step">{{ step.description }}</slot>
        </div>
        <slot name="footer">
          <div :class="`${prefixCls}-footer`">
            <div :class="`${prefixCls}-counter`">
              {{ `${(modelValue || 0) + 1}/${steps.length}` }}
            </div>
            <div :class="`${prefixCls}-action`">
              <Space>
                <Button v-if="!isLast" size="small" @click="onFinish">跳过</Button>
                <Button v-if="modelValue !== 0" size="small" @click="onPrev">上一步</Button>
                <Button v-if="!isLast" size="small" type="primary" @click="onNext">下一步</Button>
                <Button v-if="isLast" size="small" type="primary" @click="onFinish">结束</Button>
              </Space>
            </div>
          </div>
        </slot>
      </template>
    </Poptip>
  </div>
</template>

<script>
  import { Button, Poptip } from 'view-ui-plus'
  import Space from '../Space'

  export default {
    name: 'Tour',
    emits: ['update:modelValue', 'close', 'finish'],
    components: { Button, Poptip, Space },
    props: {
      modelValue: Number,
      zIndex: Number,
      steps: {
        type: Array,
        required: true
      },
      mask: {
        type: Boolean,
        default: true
      },
      padding: {
        type: Number,
        default: 6
      }
    },
    data() {
      return {
        prefixCls: `${this.$config.cssPrefix}tour`,
        visible: false,
        step: null,
        isLast: false,
        boxStyle: {},
        popStyle: {},
        showMask: false,
        popProps: {}
      }
    },
    computed: {
      classes() {
        const { prefixCls } = this
        return [
          prefixCls,
          {
            [`${prefixCls}-mask`]: this.showMask,
            [`${prefixCls}-open`]: this.visible
          }
        ]
      }
    },
    watch: {
      modelValue() {
        this.start()
      }
    },
    mounted() {
      this.start()
    },
    methods: {
      start() {
        if (!this.steps || this.modelValue == null || this.modelValue < 0 || this.modelValue >= this.steps.length) {
          this.close()
          return
        }
        this.step = { ...this.steps[this.modelValue], index: this.modelValue }
        this.isLast = this.modelValue === this.steps.length - 1
        const { mask, popoverProps, target, padding } = this.step
        this.showMask = mask ?? this.mask
        const targetEl = typeof target === 'function' ? target() : target
        if (targetEl) {
          targetEl.focus()
          const { width, height } = targetEl.getBoundingClientRect()
          const { top, left } = this.getPosition(targetEl)
          const boxPadding = padding ?? this.padding ?? 0
          this.boxStyle = {
            width: `${width + boxPadding + boxPadding}px`,
            height: `${height + boxPadding + boxPadding}px`,
            top: `${top - boxPadding}px`,
            left: `${left - boxPadding}px`
          }
          this.setPopStyle(top, popoverProps)
        } else {
          this.popStyle = {}
          this.boxStyle = {
            width: '0px',
            height: '0px',
            top: '50%',
            left: '50%'
          }
        }
        this.popProps = this.getPoptipProps(true, !targetEl, popoverProps)
        this.$nextTick(() => this.updatePopper())
        this.visible = true
        this.$emit('change')
      },
      setPopStyle(top, popoverProps = {}) {
        const placement = popoverProps.placement || 'top'
        const isTop = placement.split('-')[0] === 'top'
        this.popStyle = {
          position: 'absolute',
          width: this.boxStyle.width,
          height: this.boxStyle.height,
          top: isTop ? `${top - 24}px` : `${top + 24}px`,
          left: this.boxStyle.left
        }
      },
      getPoptipProps(value, targetEl, props = {}) {
        const { prefixCls } = this
        const classes = [`${prefixCls}-popover`]
        targetEl && classes.push(`${prefixCls}-modal`)
        props && props.popperClass && classes.push(props.popperClass)
        return {
          trigger: 'focus',
          placement: 'top',
          modelValue: value != null && value,
          popperClass: classes.join(' '),
          ...props
        }
      },
      getPosition($el) {
        const { top, left } = $el.getBoundingClientRect()
        const { pageYOffset, pageXOffset } = $el.ownerDocument.defaultView ?? {}
        return {
          top: top + (pageYOffset ?? 0),
          left: left + (pageXOffset ?? 0)
        }
      },
      close() {
        this.visible = false
        this.boxStyle = {}
        this.step = null
        this.showMask = false
        this.popProps = this.getPoptipProps()
        this.$emit('close')
      },
      updateValue(value) {
        this.$emit('update:modelValue', value)
      },
      onPrev() {
        this.modelValue != null && this.steps != null && this.steps.length && this.modelValue > 0 && this.updateValue(this.modelValue - 1)
      },
      onNext() {
        this.modelValue != null &&
          this.steps != null &&
          this.steps.length &&
          this.modelValue < this.steps.length - 1 &&
          this.updateValue(this.modelValue + 1)
      },
      onFinish() {
        this.updateValue(null)
        this.$emit('finish')
      },
      updatePopper() {
        const popoverRef = this.$refs.popoverRef
        if (!popoverRef) return
        const placement = this.popProps.placement
        popoverRef.popperJS?.options && (popoverRef.popperJS.options.placement = placement)
        popoverRef.updatePopper()
      }
    }
  }
</script>
