<template>
  <div :class="[`${prefixCls}-head-title`, selected && `${prefixCls}-head-title-selected`]">
    <span v-if="withIcon" :class="`${prefixCls}-head-title-icon`">
      <XIcon v-if="menu.meta.icon" :type="menu.meta.icon" :size="iconSize" />
      <img v-else :src="menu.meta.img" />
    </span>
    <span :class="`${prefixCls}-head-title-text`">{{ menu.meta.title || '未命名' }}</span>
    <em v-if="menu.meta.subtitle">{{ menu.meta.subtitle }}</em>
    <Badge :class="`${prefixCls}-head-title-badge`" v-if="badgeData" v-bind="badgeData" />
  </div>
</template>

<script>
  import mixins from '../MenuSide/mixins'

  export default {
    name: 'MenuHeadTitle',
    mixins: [mixins],
    props: {
      menu: {
        type: Object,
        default: () => {}
      },
      selected: {
        type: Boolean,
        default: false
      },
      hideIcon: {
        type: Boolean,
        default: false
      },
      iconSize: {
        type: Number,
        default: 16
      }
    },
    computed: {
      withIcon() {
        return (this.menu.meta.icon || this.menu.meta.img) && !this.hideIcon
      }
    }
  }
</script>
