<template>
  <Drawer :width="300" placement="right" :closable="false" :styles="{ padding: '24px' }" v-model="visible">
    <Divider size="small">整体风格</Divider>
    <SettingItem v-if="!isTopMenu">
      <Row :gutter="24">
        <Col v-for="item in siderStyles" :span="12" :key="item.title">
          <Tooltip transfer placement="top" :content="item.title">
            <div :class="themeClasses(item.type, item.theme)" @click="setSiderTheme(item.theme)">
              <ThemeBlock :theme="item.block" />
            </div>
          </Tooltip>
        </Col>
      </Row>
    </SettingItem>
    <SettingItem>
      <Row :gutter="24">
        <Col v-for="item in headerStyles" :span="8" :key="item.title">
          <Tooltip transfer placement="top" :content="item.title">
            <div :class="themeClasses(item.type, item.theme)" @click="setHeaderTheme(item.theme)">
              <ThemeBlock :theme="item.block" />
            </div>
          </Tooltip>
        </Col>
      </Row>
    </SettingItem>

    <Divider size="small">导航模式</Divider>
    <SettingItem>
      <Row :gutter="24">
        <Col v-for="item in navModeList" :span="8" :key="item.title">
          <Tooltip transfer placement="top" :content="item.title">
            <div :class="navModeClasses(item.type)" @click="setLayoutMode(item.type)">
              <ThemeBlock :theme="item.block" />
            </div>
          </Tooltip>
        </Col>
      </Row>
    </SettingItem>

    <Divider size="small">布局设置</Divider>
    <SettingItem
      title="顶栏区域宽度"
      :modelValue="headerWidth"
      :disabled="!isTopMenu || !isDesktop"
      :tipTitle="isDesktop ? `需开启 [ 顶部菜单导航 ]` : `桌面端可配置`"
      :radio-action="true"
      :radio-option="[
        { label: `fixed`, value: `固定` },
        { label: `fluid`, value: `流式` }
      ]"
      @on-change="setHeaderWidth"
    />
    <SettingItem
      title="内容区域宽度"
      :tipTitle="isDesktop ? `需开启 [ 顶部菜单导航 ]` : `桌面端可配置`"
      :disabled="!isTopMenu || !isDesktop"
      :modelValue="contentWidth"
      :radio-action="true"
      :radio-option="[
        { label: `fixed`, value: `固定` },
        { label: `fluid`, value: `流式` }
      ]"
      @on-change="setContentWidth"
    />
    <SettingItem
      title="固定侧栏区域"
      :modelValue="siderFix"
      :disabled="isTopMenu && !isMobile"
      :tipTitle="`需开启 [ 侧边菜单导航 ]`"
      @on-change="setSiderFix"
    />
    <SettingItem title="固定顶栏区域" :modelValue="headerFix" @on-change="setHeaderFix" />
    <SettingItem
      title="置顶顶栏区域"
      :modelValue="headerStick"
      :disabled="isTopMenu || !headerFix"
      :tipTitle="`需开启 [ 侧边/混合菜单 ] + [ 固定顶栏区域 ]`"
      @on-change="setHeaderStick"
    />
    <SettingItem
      title="下滑隐藏顶栏"
      :modelValue="headerHide"
      :disabled="!headerFix"
      :tipTitle="`需开启 [ 固定顶栏区域 ]`"
      @on-change="setHideHeader"
    />
    <Divider size="small">顶栏设置</Divider>
    <SettingItem
      title="侧栏折叠按钮"
      :modelValue="showSiderCollapse"
      :disabled="isTopMenu || isMobile"
      :tipTitle="`需开启 [ 侧边菜单导航 ]`"
      @on-change="setShowSiderCollapse"
    />
    <SettingItem
      title="重载页面按钮"
      :modelValue="showReload"
      :disabled="isTopMenu"
      :tipTitle="`需开启 [ 侧边菜单导航 ]`"
      @on-change="setShowReload"
    />
    <SettingItem
      title="面包屑导航"
      :modelValue="showBreadcrumb"
      :disabled="isTopMenu"
      :tipTitle="`需开启 [ 侧边菜单导航 ]`"
      @on-change="setBreadcrumbNav"
    />
    <SettingItem
      title="面包屑图标"
      :modelValue="showBreadcrumbIcon"
      :disabled="isTopMenu || !showBreadcrumb"
      :tipTitle="`需开启 [ 面包屑导航 ]`"
      @on-change="setBreadcrumbNavIcon"
    />
    <SettingItem title="路由菜单搜索" :modelValue="showSearch" @on-change="setShowSearch" />
    <SettingItem title="前端日志告警" :modelValue="showLog" @on-change="setShowLog" />
    <SettingItem title="全屏切换按钮" :modelValue="showFullscreen" @on-change="setShowFullscreen" />
    <SettingItem title="消息通知中心" :modelValue="showNotice" @on-change="setShowNotice" />

    <Divider size="small">页签设置</Divider>
    <SettingItem title="开启多页签栏" :modelValue="tabsNav" @on-change="setTabsNav" />
    <SettingItem
      :disabled="!tabsNav"
      :tipTitle="`需开启 [ 开启多页签栏 ]`"
      title="页签显示风格"
      :modelValue="tabsNavStyle"
      :radio-action="true"
      :radio-option="[
        { label: `dot`, value: `经典` },
        { label: `card`, value: `卡片` }
      ]"
      @on-change="setTabsNavStyle"
    />
    <SettingItem
      title="页签显示图标"
      :modelValue="tabsNavIcon"
      :disabled="!tabsNav || tabsNavStyle !== `card`"
      :tipTitle="!tabsNav ? `需开启 [ 开启多页签栏 ]` : tabsNavStyle !== `card` ? `仅卡片风格有效` : ``"
      @on-change="setTabsNavIcon"
    />
    <SettingItem
      title="固定多页签栏"
      :modelValue="tabsNavFix"
      :disabled="!tabsNav"
      :tipTitle="`需开启 [ 开启多页签栏 ]`"
      @on-change="setTabsNavFix"
    />
    <SettingItem
      title="双击页签重载"
      :modelValue="tabsReload"
      :disabled="!tabsNav"
      :tipTitle="`需开启 [ 开启多页签栏 ]`"
      @on-change="setTabsReload"
    />
    <SettingItem
      title="拖拽页签排序"
      :modelValue="tabSortable"
      :disabled="!tabsNav"
      :tipTitle="`需开启 [ 开启多页签栏 ]`"
      @on-change="setTabSortable"
    />

    <Divider size="small">页头设置</Divider>
    <SettingItem title="面包屑导航" :modelValue="pageBreadcrumb" @on-change="setPageBreadcrumb" />
    <SettingItem title="显示页面描述" :modelValue="pageDescription" @on-change="setPageDescription" />

    <Divider size="small">其它设置</Divider>
    <SettingItem title="路由切换动画">
      <template #action>
        <Select :modelValue="routeAnimate" @on-change="setRouteAnimate" size="small" style="width: 90px">
          <Option value="null">禁用动画</Option>
          <Option value="slider-right">滑动消退</Option>
          <Option value="slide-bottom">底部消退</Option>
          <Option value="zoom-in">放大渐变</Option>
          <Option value="zoom-out">缩小渐变</Option>
          <Option value="fade">淡入淡出</Option>
        </Select>
      </template>
    </SettingItem>
    <SettingItem title="全局表单风格">
      <template #action>
        <Select :modelValue="dialogType" @on-change="setDialogType" size="small" v-width="90">
          <Option value="drawer">抽屉</Option>
          <Option value="modal">模态框</Option>
        </Select>
      </template>
    </SettingItem>
    <SettingItem title="移动端响应式" :modelValue="responsiveLayout" @on-change="setResponsiveLayout" />
    <SettingItem
      title="菜单排他展开"
      :modelValue="menuAccordion"
      :disabled="!isSideMenu"
      :extraTip="`侧边菜单手风琴模式展开`"
      @on-change="setMenuAccordion"
    />
    <SettingItem title="开启动态标题" :modelValue="dynamicTitle" @on-change="setDynamicTitle" />
    <SettingItem title="开启底部版权" :modelValue="copyright" @on-change="setCopyright" />
    <SettingItem title="开启全局水印" :modelValue="watermark" @on-change="setWatermark" />
    <SettingItem title="开启色弱模式" :modelValue="colorWeak" @on-change="setColorWeak" />
    <SettingItem title="开启灰色模式" :modelValue="colorGrey" @on-change="setColorGrey" />

    <Divider />
    <div :style="{ marginBottom: '24px' }">
      <Button long @click="resetSetting">重置</Button>
      <Alert type="warning" :style="{ marginTop: '24px' }">
        <template #desc>该功能可实时预览各种布局效果，修改后会缓存在本地，下次打开会记忆主题配置。</template>
      </Alert>
    </div>
  </Drawer>
</template>

<script>
  import startup from '@/config/startup'
  import SettingItem from './SettingItem'
  import ThemeBlock from '@/components/ThemeBlock'
  import { mixinStore, mixinDevice } from '@/utils/mixin'

  export default {
    components: {
      ThemeBlock,
      SettingItem
    },
    mixins: [mixinStore, mixinDevice],
    data() {
      return {
        prefixCls: `${this.$config.cssPrefix}setting`,
        visible: false,
        siderStyles: [
          {
            type: 'siderTheme',
            title: '暗色侧边栏',
            theme: 'dark',
            block: 'sider-dark'
          },
          {
            type: 'siderTheme',
            title: '亮色侧边栏',
            theme: 'light'
          }
        ],
        headerStyles: [
          {
            type: 'headerTheme',
            title: '暗色顶栏',
            theme: 'dark',
            block: 'header-dark'
          },
          {
            type: 'headerTheme',
            title: '亮色顶栏',
            theme: 'light'
          },
          {
            type: 'headerTheme',
            title: '主色顶栏',
            theme: 'primary',
            block: 'header-primary'
          }
        ],
        navModeList: [
          {
            type: 'sideMenu',
            title: '侧边菜单导航',
            block: 'sider-dark'
          },
          {
            type: 'topMenu',
            title: '顶部菜单导航',
            block: 'top-nav'
          },
          {
            type: 'mixMenu',
            title: '混合菜单导航',
            block: 'mix-nav'
          }
        ]
      }
    },
    computed: {
      themeClasses() {
        const { prefixCls } = this
        return (type, theme) => [
          `${prefixCls}-radio`,
          {
            [`${prefixCls}-radio-checked`]: this.settingEqual(type, theme)
          }
        ]
      },
      navModeClasses() {
        const { prefixCls } = this
        return type => [
          `${prefixCls}-radio`,
          {
            [`${prefixCls}-radio-checked`]: type === this.layoutMode
          }
        ]
      }
    },
    methods: {
      resetSetting() {
        startup(true)
      }
    }
  }
</script>
