<template>
  <span :class="`${BasicLayout.prefixCls}-header-tool-item`" @click="handleReload">
    <Icon custom="i-icon i-icon-refresh" :size="iconSize" />
  </span>
</template>

<script>
  export default {
    name: 'HeaderReload',
    inject: ['BasicLayout'],
    props: {
      iconSize: {
        type: [String, Number],
        default: 18
      }
    },
    methods: {
      handleReload() {
        this.BasicLayout.handleReload()
      }
    }
  }
</script>
