<template>
  <div :class="`${prefixCls}-head-menu-item`">
    <MenuItem :class="menuItemClass" :name="menu.path" :to="menu.path" :target="menu.meta.target">
      <MenuHeadTitle :menu="menu" :selected="routeMatched" />
    </MenuItem>
  </div>
</template>

<script>
  import mixins from '../MenuSide/mixins'
  import MenuHeadTitle from './MenuHeadTitle'

  export default {
    name: 'MenuHeadItem',
    mixins: [mixins],
    inject: ['prefixCls'],
    components: { MenuHeadTitle },
    props: {
      menu: {
        type: Object,
        default: () => {}
      }
    },
    computed: {
      menuItemClass() {
        return {
          'ivu-menu-item-active ivu-menu-item-selected': this.routeMatched
        }
      }
    }
  }
</script>
