export default {
  data() {
    return {
      prefixCls: `${this.$config.cssPrefix}menu`
    }
  },
  computed: {
    badgeData() {
      const menuBadge = this.$store.state.app.menuBadge
      return menuBadge.find(item => item.path === this.menu.path)
    },
    routeMatched() {
      return this.$route.matched
        .map(e => e.path)
        .filter(e => !!e)
        .some(e => e === this.menu.path)
    }
  },
  methods: {
    linkUrl(path) {
      const type = typeof path
      if (type !== 'string') {
        return null
      }
      if (path.includes('//')) {
        /* Absolute URL, we do not need to route this */
        return path
      }
      const router = this.$router
      if (router) {
        const current = this.$route
        const route = router.resolve(path, current, false)
        return route ? route.href : path
      }
      return path
    }
  }
}
