<template>
  <div ref="iconSelect" :class="classes">
    <Dropdown
      trigger="custom"
      :visible="visible"
      :transfer="transfer"
      :placement="placement"
      @on-visible-change="handleVisibleChange"
      @on-clickoutside="handleClickOutside"
    >
      <div :class="`${prefixCls}-rel`" @click.prevent.stop="handleToggleOpen">
        <input type="hidden" :name="name" :value="currentValue" />
        <Input ref="input" :elementId="elementId" readonly :disabled="itemDisabled" :modelValue="currentValue" :size="size" :placeholder="placeholder">
          <template #prepend>
            <span v-if="currentItem">
              <Icon v-if="currentItem.custom" :custom="currentItem.custom" />
              <Icon v-if="currentItem.name" :type="currentItem.name" />
            </span>
          </template>
        </Input>
        <Icon :class="`${prefixCls}-arrow`" type="ios-close-circle" v-show="showCloseIcon" @click.stop="clearSelect" />
        <Icon :class="`${prefixCls}-arrow`" type="ios-arrow-down" />
      </div>
      <template #list>
        <DropdownMenu>
          <div :class="`${prefixCls}-drop`">
            <div :class="`${prefixCls}-search`">
              <Input :placeholder="searchPlaceholder" v-model="query" />
              <div :class="`${prefixCls}-list`">
                <div
                  v-for="item in filteredIconList"
                  :class="iconClass(item)"
                  :title="showTitle && item.name"
                  :key="item.name"
                  @click="handleClickItem(item)"
                >
                  <Icon v-if="item.custom" :custom="item.custom" />
                  <Icon v-else :type="item.name" />
                  <p v-if="iconName">{{ item.name }}</p>
                </div>
              </div>
            </div>
          </div>
        </DropdownMenu>
      </template>
    </Dropdown>
  </div>
</template>

<script>
  import emitter from 'view-ui-plus/src/mixins/emitter'
  import mixinsForm from 'view-ui-plus/src/mixins/form'
  import iViewIcons from './iview-icons'

  export default {
    name: 'IconSelect',
    emits: ['update:modelValue', 'on-change'],
    mixins: [emitter, mixinsForm],
    props: {
      modelValue: {
        type: String,
        default: ''
      },
      disabled: {
        type: Boolean,
        default: false
      },
      clearable: {
        type: Boolean,
        default: false
      },
      placeholder: {
        type: String,
        default: '请选择'
      },
      searchPlaceholder: {
        type: String,
        default: '搜索...'
      },
      placement: {
        validator: value => ['top', 'bottom', 'top-start', 'bottom-start', 'top-end', 'bottom-end'].includes(value),
        default: 'bottom-start'
      },
      size: {
        validator: value => ['small', 'large', 'default'].includes(value),
        default: 'default'
      },
      transfer: {
        type: Boolean,
        default: false
      },
      name: {
        type: String
      },
      elementId: {
        type: String
      },
      search: {
        type: Boolean,
        default: false
      },
      iconList: {
        type: Array,
        default: () => iViewIcons
      },
      iconName: {
        type: Boolean,
        default: false
      },
      showTitle: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        prefixCls: `${this.$config.cssPrefix}icon-select`,
        currentValue: this.modelValue,
        visible: false,
        query: ''
      }
    },
    computed: {
      classes() {
        const { prefixCls } = this
        return {
          [`${prefixCls}`]: true,
          [`${prefixCls}-visible`]: this.visible,
          [`${prefixCls}-disabled`]: this.disabled,
          [`${prefixCls}-show-clear`]: this.showCloseIcon,
          [`${prefixCls}-size-${this.size}`]: !!this.size
        }
      },
      iconClass() {
        return item => {
          const { prefixCls } = this
          return [
            `${prefixCls}-item`,
            {
              [`${prefixCls}-item-disabled`]: item.disabled,
              [`${prefixCls}-item-selected`]: item.name === this.currentValue
            }
          ]
        }
      },
      showCloseIcon() {
        return this.currentValue && this.clearable && !this.itemDisabled
      },
      currentItem() {
        return this.currentValue ? this.iconList.find(item => item.name === this.currentValue) : null
      },
      filteredIconList() {
        return this.query ? this.iconList.filter(item => item.name.indexOf(this.query) > -1) : this.iconList
      }
    },
    watch: {
      modelValue(newValue) {
        this.currentValue = newValue
        this.handleChangeValue(newValue)
      }
    },
    methods: {
      handleVisibleChange(visible) {
        this.visible = visible
      },
      handleClickOutside(event) {
        this.$refs.iconSelect.contains(event.target) || (this.visible = false)
      },
      handleToggleOpen() {
        if (this.itemDisabled) {
          return false
        }
        this.visible = !this.visible
      },
      handleClose() {
        this.visible = false
      },
      clearSelect() {
        if (this.itemDisabled) return false
        this.handleChangeValue('')
        this.$emit('on-clear')
      },
      handleChangeValue(iconName) {
        if (iconName !== this.currentValue) {
          this.currentValue = iconName
        }
        this.handleClose()
        this.$emit('update:modelValue', iconName)
        this.$emit('on-change', iconName)
        this.dispatch('FormItem', 'on-form-change', iconName)
        this.query = ''
      },
      handleClickItem(icon) {
        if (icon.disabled) {
          return false
        }
        this.handleChangeValue(icon.name)
      }
    }
  }
</script>
