import { Notice, Message, LoadingBar } from 'view-ui-plus'
import store from '@/store'
import config from '@/config'
import router from '@/router'
import { storage } from '@/utils/storage'
import { ACCESS_TOKEN, setCurrentTitle } from '@/store/mutation-types'

const whiteList = ['login', 'register', 'registerResult']

router.beforeEach(async (to, from, next) => {
  config.showProgressBar && LoadingBar.start()
  to.meta && store.commit(setCurrentTitle, to.meta.title)

  if (storage.get(ACCESS_TOKEN)) {
    if (to.path === '/login') {
      next({ name: config.homeRouteName })
    } else if (!store.state.user.userRole.length) {
      // try {
      console.log('store.state:', store.state.user.userRole.length)
      const data = await store.dispatch('getUserInfo')
      console.log('data:', data)
      const routes = await store.dispatch('generateRoutes', data)

      // resetRouter() // 重置路由 防止退出重新登录或者token过期后页面未刷新，导致的路由重复添加

      // VueRouter@3.5.0+ New API [router.addRoute]
      routes.forEach(r => router.addRoute(r))

      // 请求带有 redirect 重定向时，登录自动重定向到该地址
      const redirect = decodeURIComponent(from.query.redirect || to.path)
      if (to.path === redirect) {
        // hack方法 确保addRoutes已完成 ,set the replace: true so the navigation will not leave a history record
        next({ ...to, replace: true })
      } else {
        // 跳转到目的路由
        next({ path: redirect })
      }
      // } catch (error) {
      //   storage.remove(ACCESS_TOKEN)
      //   Notice.error({ title: '系统提示', desc: '用户信息获取失败' })
      //   next({ path: '/login', query: { redirect: to.fullPath } })
      // }
    } else {
      next()
    }
  } else if (whiteList.includes(to.name)) {
    next()
  } else {
    next({ path: '/login', query: { redirect: to.fullPath } })
    Message.warning('登录已失效，请重新登录')
  }
})

router.afterEach(() => {
  config.showProgressBar && LoadingBar.finish()
})
