<template>
  <div ref="chartLine" v-height="height"></div>
</template>

<script>
  import { shallowRef } from 'vue'

  export default {
    name: 'ChartLine',
    props: {
      height: {
        type: Number,
        required: true
      },
      options: {
        type: Object,
        required: true,
        validator: value => !!value.xData && !!value.y1Data && !!value.y2Data
      }
    },
    data() {
      return {
        ChartLine: null
      }
    },
    watch: {
      options() {
        this.handleSetChartLine()
      }
    },
    async mounted() {
      this.handleSetChartLine()
    },
    methods: {
      async handleSetChartLine() {
        const $echarts = await import(/* webpackChunkName: "async-echarts" */ 'echarts')
        this.ChartLine = shallowRef(this.ChartLine ?? $echarts.init(this.$refs.chartLine))
        this.ChartLine.setOption({
          xAxis: {
            type: 'category',
            axisLine: { lineStyle: { color: '#D7DDE4' } },
            axisTick: { alignWithLabel: true, lineStyle: { color: '#D7DDE4' } },
            axisLabel: { textStyle: { color: '#7F8B9C' } },
            splitLine: { show: false, lineStyle: { color: '#F5F7F9' } },
            data: this.options.xData,
            boundaryGap: false
          },
          yAxis: {
            axisLine: { show: false },
            axisTick: { show: false },
            axisLabel: { textStyle: { color: '#7F8B9C' } },
            splitLine: { show: true, lineStyle: { color: '#F5F7F9' } },
            type: 'value'
          },
          legend: { data: ['浏览量（PV）', '访客数（UV）'], x: 'right' },
          series: [
            {
              data: this.options.y1Data,
              name: '浏览量（PV）',
              type: 'line',
              tooltip: true,
              smooth: true,
              symbol: 'none',
              areaStyle: { normal: { opacity: 0.2 } }
            },
            {
              data: this.options.y2Data,
              name: '访客数（UV）',
              type: 'line',
              tooltip: true,
              smooth: true,
              symbol: 'none',
              areaStyle: { normal: { opacity: 0.2 } }
            }
          ],
          color: ['#1495EB', '#00CC66', '#F9D249', '#ff9900', '#9860DF'],
          grid: { left: 20, right: 20, bottom: 20, top: 40, containLabel: true },
          tooltip: { trigger: 'axis' }
        })
      },
      handleResize() {
        this.ChartLine && this.ChartLine.resize()
      }
    },
    beforeDestroy() {
      this.ChartLine && this.ChartLine.dispose()
      this.ChartLine = null
    }
  }
</script>
