import dayjs from 'dayjs'
import { saveLogToServer } from '@/api/log'
import { pushLog, cleanLog } from '@/store/mutation-types'

export default {
  state: {
    logList: []
  },
  mutations: {
    [pushLog](state, payload) {
      state.logList.push(payload)
    },
    [cleanLog](state) {
      state.logList = []
    }
  },
  actions: {
    pushLog({ commit, rootState }, payload) {
      const { user } = rootState
      const data = {
        message: payload.message,
        sources: payload.sources,
        type: payload.type || 'info',
        time: dayjs().format('YYYY-MM-DD HH:mm:ss'),
        meta: {
          ...payload.meta,
          token: user.token,
          user: user.info,
          url: window.location.href
        }
      }
      // FIXME: 提交服务端保存日志
      saveLogToServer(data).then(() => commit(pushLog, data))
    }
  }
}
