<template>
  <div :class="prefixCls">
    <PageHeader :title="pageTitle" :hiddenBreadcrumb="!pageBreadcrumb" v-bind="$attrs">
      <template #content>
        <span v-if="showHeaderContent" v-html="content"></span>
        <slot v-else name="headerContent"></slot>
      </template>
      <template #breadcrumb v-if="pageBreadcrumb">
        <HeaderBreadcrumb />
      </template>
      <template v-for="slotName in slotsKeys" #[slotName]>
        <slot :name="slotName"></slot>
      </template>
    </PageHeader>
    <slot></slot>
  </div>
</template>

<script>
  import { mapState } from 'vuex'
  import { PageHeader } from 'view-ui-plus'
  import HeaderBreadcrumb from '../../layouts/components/HeaderBreadcrumb'

  export default {
    name: 'PageView',
    components: { HeaderBreadcrumb, PageHeader },
    props: {
      title: {
        type: String,
        default: undefined
      },
      content: {
        type: String,
        default: undefined
      }
    },
    data() {
      return {
        prefixCls: `${this.$config.cssPrefix}page-view`,
        pageTitle: undefined,
        usedSlots: ['headerContent']
      }
    },
    computed: {
      ...mapState({
        pageBreadcrumb: state => state.app.pageBreadcrumb,
        pageDescription: state => state.app.pageDescription
      }),
      slotsKeys() {
        return Object.keys(this.$slots).filter(key => !this.usedSlots.includes(key))
      },
      showHeaderContent() {
        return !this.$slots.headerContent && this.content && this.pageDescription
      },
      homeRoute() {
        return this.$store.getters.homeRoute
      },
      breadcrumbList() {
        const routeMatched = this.$route.matched.filter(item => !item.meta.hideInBread)
        return [this.homeRoute, ...routeMatched].map(item => ({
          title: item.meta.title || '未命名',
          to: item.path
        }))
      }
    },
    mounted() {
      this.getPageMeta()
    },
    updated() {
      this.getPageMeta()
    },
    methods: {
      getPageMeta() {
        this.pageTitle = this.title ?? this.$route.meta.title
      }
    }
  }
</script>
