import { mapState, mapGetters, mapMutations } from 'vuex'
import {
  setMenuCollapse,
  setDevice,
  setSiderTheme,
  setHeaderTheme,
  setLayoutMode,
  setColorWeak,
  setColorGrey,
  setHeaderFix,
  setHeaderStick,
  setSiderFix,
  setHideHeader,
  setHeaderWidth,
  setContentWidth,
  setTabsNav,
  setTabsNavStyle,
  setTabsNavList,
  setTabsNavIcon,
  setTabSortable,
  setTabsNavFix,
  setTabsReload,
  setDynamicTitle,
  setMenuAccordion,
  setFullContent,
  setCopyright,
  setWatermark,
  setResponsiveLayout,
  setShowSiderCollapse,
  setShowReload,
  setBreadcrumbNav,
  setBreadcrumbNavIcon,
  setShowSearch,
  setRouteAnimate,
  setShowLog,
  setShowFullscreen,
  setShowNotice,
  setDialogType,
  setPageBreadcrumb,
  setPageDescription
} from '@/store/mutation-types'
import { deviceEnquire } from '@/utils/device'
import { LAYOUT_MODE, DEVICE_TYPE } from '@/utils/constants'

const mixinState = {
  computed: {
    ...mapState({
      layoutMode: state => state.app.layoutMode,
      siderTheme: state => state.app.siderTheme,
      siderFix: state => state.app.siderFix,
      headerWidth: state => state.app.headerWidth,
      contentWidth: state => state.app.contentWidth,
      headerTheme: state => state.app.headerTheme,
      headerStick: state => state.app.headerStick,
      headerFix: state => state.app.headerFix,
      headerHide: state => state.app.headerHide,
      menuCollapse: state => state.app.menuCollapse,

      tabsNav: state => state.app.tabsNav,
      tabsNavFix: state => state.app.tabsNavFix,
      tabsReload: state => state.app.tabsReload,
      tabsNavIcon: state => state.app.tabsNavIcon,
      tabSortable: state => state.app.tabSortable,
      tabsNavList: state => state.app.tabsNavList,
      tabsNavStyle: state => state.app.tabsNavStyle,
      cacheNavList: state => state.app.cacheNavList,

      showSiderCollapse: state => state.app.showSiderCollapse,
      showReload: state => state.app.showReload,
      showBreadcrumb: state => state.app.showBreadcrumb,
      showBreadcrumbIcon: state => state.app.showBreadcrumbIcon,
      showSearch: state => state.app.showSearch,
      showLog: state => state.app.showLog,
      showFullscreen: state => state.app.showFullscreen,
      showNotice: state => state.app.showNotice,

      siderExpandedWidth: state => state.app.siderExpandedWidth,
      siderCollapseWidth: state => state.app.siderCollapseWidth,
      sameRouteForceUpdate: state => state.app.sameRouteForceUpdate,

      pageBreadcrumb: state => state.app.pageBreadcrumb,
      pageDescription: state => state.app.pageDescription,

      responsiveLayout: state => state.app.responsiveLayout,
      menuAccordion: state => state.app.menuAccordion,
      fullContent: state => state.app.fullContent,
      routeAnimate: state => state.app.routeAnimate,
      dynamicTitle: state => state.app.dynamicTitle,
      dialogType: state => state.app.dialogType,
      watermark: state => state.app.watermark,
      copyright: state => state.app.copyright,
      colorWeak: state => state.app.colorWeak,
      colorGrey: state => state.app.colorGrey
    }),
    scrollListener() {
      return !this.headerFix && this.tabsNavFix
    },
    isSideMenu() {
      return this.layoutMode === LAYOUT_MODE.sideMenu
    },
    isTopMenu() {
      return this.layoutMode === LAYOUT_MODE.topMenu
    },
    isMixMenu() {
      return this.layoutMode === LAYOUT_MODE.mixMenu
    }
  }
}

const mixinStore = {
  computed: {
    ...mixinState.computed,
    ...mapGetters(['settingEqual'])
  },
  methods: {
    ...mapMutations([
      setMenuCollapse,
      setDevice,
      setSiderTheme,
      setHeaderTheme,
      setLayoutMode,
      setColorWeak,
      setColorGrey,
      setHeaderFix,
      setHeaderStick,
      setSiderFix,
      setHideHeader,
      setHeaderWidth,
      setContentWidth,
      setTabsNav,
      setTabsNavStyle,
      setTabsNavList,
      setTabsNavIcon,
      setTabSortable,
      setTabsNavFix,
      setTabsReload,
      setDynamicTitle,
      setMenuAccordion,
      setFullContent,
      setCopyright,
      setWatermark,
      setResponsiveLayout,

      setShowSiderCollapse,
      setShowReload,
      setBreadcrumbNav,
      setBreadcrumbNavIcon,
      setShowSearch,
      setRouteAnimate,
      setShowLog,
      setShowFullscreen,
      setShowNotice,
      setDialogType,
      setPageBreadcrumb,
      setPageDescription
    ])
  }
}

const mixinDevice = {
  computed: {
    ...mapState({
      device: state => state.app.device
    }),
    isMobile() {
      return this.device === DEVICE_TYPE.mobile
    },
    isDesktop() {
      return this.device === DEVICE_TYPE.desktop
    },
    isTablet() {
      return this.device === DEVICE_TYPE.tablet
    }
  }
}

const AppDeviceEnquire = {
  mounted() {
    const { $store } = this
    deviceEnquire(device => {
      switch (device) {
        case DEVICE_TYPE.desktop:
          $store.commit(setDevice, device)
          break
        case DEVICE_TYPE.tablet:
          $store.commit(setDevice, device)
          $store.commit(setMenuCollapse, false)
          break
        case DEVICE_TYPE.mobile:
        default:
          $store.commit(setDevice, device)
          $store.commit(setMenuCollapse, false)
          break
      }
    })
  }
}

export { mixinState, mixinStore, AppDeviceEnquire, mixinDevice }
