import dayjs from 'dayjs'

const formatNumber = value => {
  if (!value) return '0'
  // 将整数部分逢三一断
  return value.toString().replace(/(\d)(?=(?:\d{3})+$)/g, '$1,')
}

const formatDate = (dataStr, pattern = 'YYYY-MM-DD HH:mm:ss') => dayjs(dataStr).format(pattern)

export default {
  install(app) {
    app.config.globalProperties.$filter = {
      formatNumber,
      formatDate
    }
  }
}
