<template>
  <div :class="prefixCls">
    <template v-if="!isMobile">
      <span :class="`${BasicLayout.prefixCls}-header-tool-item`" @click="handleSearch">
        <Icon type="ios-search-outline" class="ivu-font-bold" size="18" />
      </span>
      <Select :class="[showSearch && 'show-search', `ivu-search-input`]" clearable filterable placeholder="搜索菜单..." @on-change="searchMethods">
        <Option v-for="item in menuOptions" :key="item.name" :value="item.name">
          {{ item.meta.title }}
        </Option>
      </Select>
    </template>
    <template v-else>
      <Dropdown :trigger="`custom`" :visible="dropVisible" class="ivu-mobile-dropdown" @on-clickoutside="handleDropMenu">
        <span :class="`${BasicLayout.prefixCls}-header-tool-item`" @click="handleDropMenu">
          <Icon type="ios-search-outline" class="ivu-font-bold" size="16" />
        </span>
        <template #list>
          <DropdownMenu>
            <div class="ivu-search-drop-main">
              <div class="ivu-search-select">
                <Select clearable filterable prefix="ios-search" placeholder="搜索菜单..." @on-change="searchMethods">
                  <Option v-for="item in menuOptions" :key="item.name" :value="item.name">
                    {{ item.meta.title }}
                  </Option>
                </Select>
              </div>
              <div class="ivu-search-cancel">
                <span @click="handleDropMenu">取消</span>
              </div>
            </div>
          </DropdownMenu>
        </template>
      </Dropdown>
    </template>
  </div>
</template>

<script>
  export default {
    name: 'HeaderSearch',
    inject: ['BasicLayout'],
    props: {
      isMobile: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        prefixCls: `${this.$config.cssPrefix}header-search`,
        showSearch: false,
        dropVisible: false,
        menuOptions: []
      }
    },
    created() {
      const menuList = this.$store.getters.menuList
      this.searchMenus(this.menuOptions, menuList)
    },
    methods: {
      handleSearch() {
        this.showSearch = !this.showSearch
      },
      searchMenus(arr, menus) {
        for (const menu of menus) {
          !menu.children && arr.push(menu)
          menu.children && menu.children.length && this.searchMenus(arr, menu.children)
        }
      },
      searchMethods(value) {
        if (!value) return
        const route = this.menuOptions.find(item => item.name === value)
        if (route.meta.target === '_blank') {
          window.open(route.path, '_blank')
        } else {
          this.$router.push({ name: value })
        }
        if (this.mobile) this.handleDropMenu()
      },
      handleDropMenu() {
        this.dropVisible = !this.dropVisible
      }
    }
  }
</script>
