<template>
  <div :class="wrapperClasses" @click="handleClick">
    <SvgLogo v-if="false" />
    <img :src="mainLogo" />
    <span v-if="textLogo">{{ $config.titleSuffix }}</span>
  </div>
</template>

<script>
  import { SvgLogo } from '@/components'
  import minLogo from '@/assets/logo/logo-small.png'
  import maxLogoDark from '@/assets/logo/logo-dark-2.png'
  import maxLogoLight from '@/assets/logo/logo-light-2.png'

  export default {
    name: 'HeaderLogo',
    components: { SvgLogo },
    props: {
      mode: {
        validator: value => ['sider', 'header'].includes(value),
        required: true
      },
      theme: {
        validator: value => ['light', 'dark', 'primary'].includes(value),
        required: true
      },
      stick: {
        type: Boolean,
        default: false
      },
      showTitle: {
        type: Boolean,
        default: true
      },
      collapsed: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        prefixCls: `${this.$config.cssPrefix}logo`,
        minLogo,
        maxLogoDark,
        maxLogoLight
      }
    },
    computed: {
      darkTheme() {
        return this.theme === 'dark'
      },
      siderMode() {
        return this.mode === 'sider'
      },
      textLogo() {
        return this.$config.showTextLogo && this.showTitle
      },
      mainLogo() {
        if (this.collapsed || this.textLogo || !this.showTitle) return minLogo
        return this.darkTheme ? maxLogoDark : maxLogoLight
      },
      wrapperClasses() {
        const { prefixCls } = this
        if (this.textLogo) {
          return [
            `${prefixCls}-text`,
            `${prefixCls}-text-${this.mode}`,
            {
              [`${prefixCls}-text-collapse`]: this.collapsed,
              [`${prefixCls}-text-${this.mode}-dark`]: this.darkTheme
            }
          ]
        }
        return [
          prefixCls,
          `${prefixCls}-${this.mode}`,
          {
            [`${prefixCls}-${this.mode}-dark`]: this.darkTheme,
            [`${prefixCls}-${this.mode}-stick`]: !this.siderMode && this.stick
          }
        ]
      }
    },
    methods: {
      handleClick() {
        this.$router.push({
          name: this.$config.homeRouteName
        })
      }
    }
  }
</script>
