<template>
  <span :class="`${BasicLayout.prefixCls}-header-tool-item`" @click="handleSetting">
    <Tooltip :disabled="!$config.settingTip || toolTip" always placement="bottom-end">
      <template #content>
        <div style="width: 160px; white-space: pre-wrap">
          <span>点击这里查看</span> <span style="color: #fadb14">主题风格</span>、<span style="color: #fadb14">切换导航模式</span>
          等更多配置
        </div>
      </template>
      <Icon type="md-more" :size="iconSize" />
      <Setting ref="setting" />
    </Tooltip>
  </span>
</template>

<script>
  import Setting from '../Setting'

  export default {
    name: 'HeaderSetting',
    inject: ['BasicLayout'],
    components: { Setting },
    props: {
      classes: {
        type: [String, Array],
        default: () => []
      },
      iconSize: {
        type: [String, Number],
        default: 18
      }
    },
    data() {
      return {
        toolTip: false
      }
    },
    methods: {
      handleSetting() {
        this.toolTip = true
        this.$refs.setting.visible = true
      }
    }
  }
</script>
