<template>
  <div :class="prefixCls">
    <iframe v-if="ready" :src="src" :class="`${prefixCls}-iframe`"></iframe>
  </div>
</template>

<script>
  import { cssPrefix } from '@/config'

  const prefixCls = `${cssPrefix}frame`
  const layoutCls = `${cssPrefix}layout`
  const noMargin = `${layoutCls}-content-main-no-margin`
  const noCopyright = `${cssPrefix}copyright-hidden`
  const contentFlex = `${layoutCls}-content-flex`
  const contentMainFlex = `${layoutCls}-content-main-flex`

  export default {
    name: 'XIframe',
    props: {
      src: {
        type: String,
        required: true
      },
      margin: {
        type: Boolean,
        default: false
      },
      copyright: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        ready: false,
        prefixCls,
        contentEl: null,
        copyrightEl: null,
        layoutContentEl: null,
        layoutContentMainEl: null
      }
    },
    computed: {
      contentWidth() {
        return this.$store.state.app.contentWidth
      },
      layoutMode() {
        return this.$store.state.app.layoutMode
      }
    },
    watch: {
      margin: {
        immediate: true,
        handler(newBool) {
          this.$nextTick(() => {
            this.contentEl && (newBool ? this.contentEl.classList.remove(noMargin) : this.contentEl.classList.add(noMargin))
          })
        }
      },
      copyright: {
        immediate: true,
        handler(newBool) {
          this.$nextTick(() => {
            this.copyrightEl && (newBool ? this.copyrightEl.classList.remove(noCopyright) : this.copyrightEl.classList.add(noCopyright))
          })
        }
      },
      contentWidth() {
        this.destroyAction()
        this.mountedAction()
      },
      layoutMode() {
        this.destroyAction()
        this.mountedAction()
      }
    },
    mounted() {
      this.mountedAction()
    },
    methods: {
      mountedAction() {
        this.contentEl = document.querySelector(`.${layoutCls}-content-main`)
        this.copyrightEl = document.querySelector(`.${cssPrefix}copyright`)

        this.layoutContentEl = document.querySelector(`.${layoutCls}-content`)
        this.layoutContentEl && this.layoutContentEl.classList.add(contentFlex)
        this.layoutContentMainEl = document.querySelector(`.${layoutCls}-content-main`)
        this.layoutContentMainEl && this.layoutContentMainEl.classList.add(contentMainFlex)
        this.$nextTick(() => (this.ready = true))
      },
      destroyAction() {
        this.contentEl && this.contentEl.classList.remove(noMargin)
        this.copyrightEl && this.copyrightEl.classList.remove(noCopyright)
        this.layoutContentEl && this.layoutContentEl.classList.remove(contentFlex)
        this.layoutContentMainEl && this.layoutContentMainEl.classList.remove(contentMainFlex)
        this.contentEl = null
        this.copyrightEl = null
        this.layoutContentEl = null
        this.layoutContentMainEl = null
      }
    },
    beforeUnmount() {
      this.destroyAction()
    }
  }
</script>
