<template>
  <div :class="prefixCls">
    <div :class="`${prefixCls}-image`">
      <component :is="imageNode" />
    </div>
    <p :class="`${prefixCls}-desc`">
      <slot name="desc">{{ desc }}</slot>
    </p>
  </div>
</template>

<script>
  import SvgImage from './SvgImage'
  import SvgImageColor from './SvgImageColor'

  export default {
    name: 'Empty',
    components: { SvgImage, SvgImageColor },
    props: {
      desc: {
        type: String,
        default: '暂无数据'
      },
      imgColor: {
        type: Boolean,
        default: true
      }
    },
    data() {
      return {
        prefixCls: `${this.$config.cssPrefix}empty`
      }
    },
    computed: {
      imageNode() {
        return this.imgColor ? SvgImageColor : SvgImage
      }
    }
  }
</script>
