import store from '@/store'
import {
  ACCESS_TOKEN,
  setMenuCollapse,
  setSiderTheme,
  setHeaderTheme,
  setLayoutMode,
  setColorWeak,
  setColorGrey,
  setHeaderFix,
  setHeaderStick,
  setSiderFix,
  setHeaderWidth,
  setContentWidth,
  setHideHeader,
  setTabsNav,
  setTabsNavStyle,
  setTabsNavList,
  setTabsNavIcon,
  setTabSortable,
  setTabsNavFix,
  setTabsReload,
  setDynamicTitle,
  setMenuAccordion,
  setFullContent,
  setCopyright,
  setWatermark,
  setResponsiveLayout,
  setRouteAnimate,
  setShowSiderCollapse,
  setShowReload,
  setBreadcrumbNav,
  setBreadcrumbNavIcon,
  setShowSearch,
  setShowLog,
  setShowFullscreen,
  setShowNotice,
  setDialogType,
  setPageBreadcrumb,
  setPageDescription
} from '@/store/mutation-types'
import config from '@/config'
import $console from '@/utils/console'
import { storage } from '@/utils/storage'

export default function startup(reset) {
  $console.capsule(config.titleSuffix, `version：${config.adminVersion}`)

  store.commit(ACCESS_TOKEN, storage.get(ACCESS_TOKEN))
  store.commit(setMenuCollapse, storage.get(setMenuCollapse, false))
  store.commit(setSiderTheme, reset ? config.siderTheme : storage.get(setSiderTheme, config.siderTheme))
  store.commit(setHeaderTheme, reset ? config.headerTheme : storage.get(setHeaderTheme, config.headerTheme))
  store.commit(setLayoutMode, reset ? config.layoutMode : storage.get(setLayoutMode, config.layoutMode))
  store.commit(setHeaderFix, reset ? config.headerFix : storage.get(setHeaderFix, config.headerFix))
  store.commit(setHeaderStick, reset ? config.headerStick : storage.get(setHeaderStick, config.headerStick))
  store.commit(setSiderFix, reset ? config.siderFix : storage.get(setSiderFix, config.siderFix))
  store.commit(setHeaderWidth, (reset && config.headerWidth) || storage.get(setHeaderWidth, config.headerWidth))
  store.commit(setContentWidth, (reset && config.contentWidth) || storage.get(setContentWidth, config.contentWidth))
  store.commit(setHideHeader, reset ? config.headerHide : storage.get(setHideHeader, config.headerHide))
  store.commit(setColorWeak, reset ? config.colorWeak : storage.get(setColorWeak, config.colorWeak))
  store.commit(setColorGrey, reset ? config.colorGrey : storage.get(setColorGrey, config.colorGrey))
  store.commit(setTabsNav, reset ? config.tabsNav : storage.get(setTabsNav, config.tabsNav))
  store.commit(setTabsNavStyle, reset ? config.tabsNavStyle : storage.get(setTabsNavStyle, config.tabsNavStyle))
  store.commit(setTabsNavList, storage.get(setTabsNavList))
  store.commit(setTabsNavIcon, reset ? config.tabsNavIcon : storage.get(setTabsNavIcon, config.tabsNavIcon))
  store.commit(setTabSortable, reset ? config.tabSortable : storage.get(setTabSortable, config.tabSortable))
  store.commit(setTabsNavFix, reset ? config.tabsNavFix : storage.get(setTabsNavFix, config.tabsNavFix))
  store.commit(setTabsReload, reset ? config.tabsReload : storage.get(setTabsReload, config.tabsReload))
  store.commit(setDynamicTitle, reset ? config.dynamicTitle : storage.get(setDynamicTitle, config.dynamicTitle))
  store.commit(setShowReload, reset ? config.showReload : storage.get(setShowReload, config.showReload))
  store.commit(setBreadcrumbNav, reset ? config.showBreadcrumb : storage.get(setBreadcrumbNav, config.showBreadcrumb))
  store.commit(setBreadcrumbNavIcon, reset ? config.showBreadcrumbIcon : storage.get(setBreadcrumbNavIcon, config.showBreadcrumbIcon))
  store.commit(setMenuAccordion, reset ? config.menuAccordion : storage.get(setMenuAccordion, config.menuAccordion))
  store.commit(setFullContent, storage.get(setFullContent))
  store.commit(setCopyright, reset ? config.copyright : storage.get(setCopyright, config.copyright))
  store.commit(setWatermark, reset ? config.watermark : storage.get(setWatermark, config.watermark))
  store.commit(setRouteAnimate, reset ? config.routeAnimate : storage.get(setRouteAnimate, config.routeAnimate))
  store.commit(setShowSiderCollapse, reset ? config.showSiderCollapse : storage.get(setShowSiderCollapse, config.showSiderCollapse))
  store.commit(setResponsiveLayout, reset ? config.responsiveLayout : storage.get(setResponsiveLayout, config.responsiveLayout))
  store.commit(setShowSearch, reset ? config.showSearch : storage.get(setShowSearch, config.showSearch))
  store.commit(setShowLog, reset ? config.showLog : storage.get(setShowLog, config.showLog))
  store.commit(setShowFullscreen, reset ? config.showFullscreen : storage.get(setShowFullscreen, config.showFullscreen))
  store.commit(setShowNotice, reset ? config.showNotice : storage.get(setShowNotice, config.showNotice))
  store.commit(setDialogType, reset ? config.dialogType : storage.get(setDialogType, config.dialogType))
  store.commit(setPageBreadcrumb, reset ? config.pageBreadcrumb : storage.get(setPageBreadcrumb, config.pageBreadcrumb))
  store.commit(setPageDescription, reset ? config.pageDescription : storage.get(setPageDescription, config.pageDescription))
}
