<template>
  <div :class="`${prefixCls}-item`">
    <slot>
      <span :class="titleClasses"
        >{{ title }}
        <Tooltip transfer :max-width="200" placement="top" :content="tipTitle || extraTip">
          <Icon v-if="disabled || extraTip" type="ios-help-circle-outline" />
        </Tooltip>
      </span>
    </slot>
    <span :class="`${prefixCls}-item-action`" v-if="!$slots.default && title">
      <slot name="action">
        <RadioGroup v-if="radioAction" size="small" type="button" button-style="solid" :modelValue="modelValue" @on-change="handleChange">
          <Radio v-for="item in radioOption" :key="item.label" :disabled="disabled" :label="item.label">
            {{ item.value }}
          </Radio>
        </RadioGroup>
        <i-switch v-else size="small" :modelValue="modelValue" :disabled="disabled" @on-change="handleChange" />
      </slot>
    </span>
  </div>
</template>

<script>
  export default {
    name: 'SettingItem',
    emits: ['update:modelValue', 'on-change'],
    props: {
      title: {
        type: String,
        default: undefined
      },
      modelValue: {
        type: [Boolean, String, Number],
        default: undefined
      },
      disabled: {
        type: Boolean,
        default: false
      },
      tipTitle: {
        type: String,
        default: undefined
      },
      extraTip: {
        type: String,
        default: undefined
      },
      radioAction: {
        type: Boolean,
        default: false
      },
      radioOption: {
        type: Array,
        default: () => []
      }
    },
    data() {
      return {
        prefixCls: `${this.$config.cssPrefix}setting`
      }
    },
    computed: {
      titleClasses() {
        const { prefixCls } = this
        return [
          `${prefixCls}-item-title`,
          {
            [`${prefixCls}-item-title-disabled`]: this.disabled
          }
        ]
      }
    },
    methods: {
      handleChange(checked) {
        this.$emit('update:modelValue', checked)
        this.$emit('on-change', checked)
      }
    }
  }
</script>
