import event from './event'
import TabsNav from './TabsNav'

const api = {
  open(fullPath) {
    event.$emit('open', fullPath)
  },
  rename(fullPath, tabTitle) {
    event.$emit('rename', { fullPath, tabTitle })
  },
  close(fullPath) {
    event.$emit('close', fullPath)
  }
}

TabsNav.install = app => {
  if (app.config.globalProperties.$tabsNav) return
  api.instance = event
  app.config.globalProperties.$tabsNav = api
  app.component(TabsNav.name, TabsNav)
}

export default TabsNav
