<template>
  <GlobalFooter v-if="copyright" :links="links" :copyright="iCopyright" />
</template>

<script>
  import { GlobalFooter } from 'view-ui-plus'

  export default {
    name: 'Copyright',
    components: { GlobalFooter },
    data() {
      return {
        links: []
      }
    },
    computed: {
      copyright() {
        return this.$store.state.app.copyright
      },
      currentYear() {
        return new Date().getFullYear()
      },
      iCopyright() {
        const { titleSuffix, adminVersion } = this.$config
        return `Copyright © ${this.currentYear} ${titleSuffix} ${adminVersion} Made By View UI Plus`
      }
    }
  }
</script>
