<template>
  <span :class="`${BasicLayout.prefixCls}-header-tool-item`" @click="handleFullscreen">
    <Icon :custom="`i-icon i-icon-${isFullscreen ? 'exit-full-screen' : 'full-screen'}`" :size="iconSize" />
  </span>
</template>

<script>
  import screenFull from 'screenfull'

  export default {
    name: 'HeaderFullScreen',
    inject: ['BasicLayout'],
    props: {
      iconSize: {
        type: [String, Number],
        default: 18
      }
    },
    data() {
      return {
        isFullscreen: false
      }
    },
    methods: {
      handleFullscreen() {
        if (!screenFull.isEnabled) {
          this.$Message.error('您的浏览器不支持全屏模式')
          return
        }
        screenFull.toggle()
        this.isFullscreen = !screenFull.isFullscreen
      }
    }
  }
</script>
