<template>
  <div :class="wrapperClasses"></div>
</template>

<script>
  export default {
    name: 'ThemeBlock',
    props: {
      theme: {
        validator: value => ['sider-dark', 'header-dark', 'real-dark', 'top-nav', 'mix-nav', 'sider-primary', 'header-primary'].includes(value)
      }
    },
    data() {
      return {
        prefixCls: `${this.$config.cssPrefix}theme`
      }
    },
    computed: {
      wrapperClasses() {
        const { prefixCls } = this
        return [
          prefixCls,
          {
            [`${prefixCls}-${this.theme}`]: !!this.theme
          }
        ]
      }
    }
  }
</script>
