<template>
  <div ref="region" :class="classes">
    <Dropdown
      trigger="custom"
      :visible="visible"
      :transfer="transfer"
      :placement="placement"
      @on-visible-change="handleVisibleChange"
      @on-clickoutside="handleClickOutside"
    >
      <div :class="`${prefixCls}-rel`" @click.prevent.stop="handleToggleOpen">
        <input type="hidden" :name="name" :value="currentValue" />
        <Input
          ref="input"
          :elementId="elementId"
          readonly
          :disabled="itemDisabled"
          :modelValue="displayInputRender"
          :size="size"
          :placeholder="placeholder"
        />
        <Icon :class="`${prefixCls}-arrow`" type="ios-close-circle" v-show="showCloseIcon" @click.stop="clearSelect" />
        <Icon :class="`${prefixCls}-arrow`" type="ios-arrow-down" />
      </div>
      <template #list>
        <DropdownMenu>
          <div :class="`${prefixCls}-drop`">
            <Tabs :animated="false" :name="`${prefixCls}-tabs`" v-model="currentType">
              <TabPane :tab="`${prefixCls}-tabs`" name="province" :label="typeLabel[0]" />
              <TabPane
                v-if="type === 'street' || type === 'district' || type === 'city'"
                :tab="`${prefixCls}-tabs`"
                name="city"
                :disabled="!provinceCode"
                :label="typeLabel[1]"
              />
              <TabPane
                v-if="type === 'street' || type === 'district'"
                :tab="`${prefixCls}-tabs`"
                name="district"
                :disabled="!cityCode"
                :label="typeLabel[2]"
              />
              <TabPane :tab="`${prefixCls}-tabs`" name="street" :disabled="!districtCode" :label="typeLabel[3]" />
              <div :class="`${prefixCls}-drop-main`">
                <div v-show="currentType === 'province'">
                  <div
                    :class="dropItemClass(code, provinceCode)"
                    v-for="(name, code) in regionData[countryCode]"
                    :key="code"
                    @click="handleSelectProvince(code)"
                  >
                    <span>{{ name }}</span>
                  </div>
                </div>
                <div v-show="currentType === 'city'">
                  <div
                    :class="dropItemClass(code, cityCode)"
                    v-for="(name, code) in regionData[provinceCode]"
                    :key="code"
                    @click="handleSelectCity(code)"
                  >
                    <span>{{ name }}</span>
                  </div>
                </div>
                <div v-show="currentType === 'district'">
                  <div
                    :class="dropItemClass(code, districtCode)"
                    v-for="(name, code) in regionData[cityCode]"
                    :key="code"
                    @click="handleSelectDistrict(code)"
                  >
                    <span>{{ name }}</span>
                  </div>
                </div>
                <div v-show="currentType === 'street'">
                  <div
                    :class="dropItemClass(code, streetCode)"
                    v-for="(name, code) in regionData[districtCode]"
                    :key="code"
                    @click="handleSelectStreet(code)"
                  >
                    <span>{{ name }}</span>
                  </div>
                </div>
              </div>
            </Tabs>
          </div>
        </DropdownMenu>
      </template>
    </Dropdown>
  </div>
</template>

<script>
  import emitter from 'view-ui-plus/src/mixins/emitter'
  import mixinsForm from 'view-ui-plus/src/mixins/form'
  import regionData from './regionData'

  const r = array => {
    if (Array.isArray(array)) {
      const result = Array(array.length)
      for (let i = 0; i < array.length; i++) {
        result[i] = array[i]
      }
      return result
    }
    return Array.from(array)
  }

  const o = e => {
    const t = []
    for (const n in e) {
      const i = e[n]
      for (const r in i) {
        t.push({ code: r, name: i[r], pid: n })
      }
    }
    return t
  }

  const s = e => {
    const t = []
    if (!Array.isArray(e)) {
      return t
    }
    e.forEach(e => delete e.children)
    const n = {}
    e.forEach(e => (n[e.code] = e))
    e.forEach(e => {
      const i = n[e.pid]
      i ? (i.children || (i.children = [])).push(e) : t.push(e)
    })
    return t
  }

  export default {
    name: 'Region',
    emits: ['update:modelValue'],
    mixins: [emitter, mixinsForm],
    props: {
      modelValue: {
        type: Array,
        default: () => []
      },
      regionData: {
        type: Object,
        default: () => regionData
      },
      countryCode: {
        type: [Number, String],
        default: 86
      },
      disabled: {
        type: Boolean,
        default: false
      },
      clearable: {
        type: Boolean,
        default: false
      },
      placeholder: {
        type: String,
        default: '请选择'
      },
      type: {
        validator: value => ['province', 'city', 'district', 'street'].includes(value),
        default: 'street'
      },
      typeText: {
        type: Array,
        default: () => ['省/直辖市', '市', '区', '街道']
      },
      dataType: {
        validator: value => ['all', 'code', 'name'].includes(value),
        default: 'all'
      },
      placement: {
        validator: value => ['top', 'bottom', 'top-start', 'bottom-start', 'top-end', 'bottom-end'].includes(value),
        default: 'bottom-start'
      },
      size: {
        validator: value => ['small', 'large', 'default'].includes(value),
        default: 'default'
      },
      transfer: {
        type: Boolean,
        default: false
      },
      name: {
        type: String
      },
      elementId: {
        type: String
      }
    },
    data() {
      const value = this.handleSetValue()
      let provinceCode = ''
      let cityCode = ''
      let districtCode = ''
      let streetCode = ''
      value.length >= 1 && (provinceCode = value[0].code)
      value.length >= 2 && (cityCode = value[1].code)
      value.length >= 3 && (districtCode = value[2].code)
      value.length >= 4 && (streetCode = value[3].code)
      return {
        prefixCls: `${this.$config.cssPrefix}region`,
        currentValue: value,
        visible: false,
        currentType: 'province',
        provinceCode,
        cityCode,
        districtCode,
        streetCode
      }
    },
    watch: {
      modelValue(newValue) {
        const value = this.handleSetValue(newValue)
        this.handleInitData()
        this.handleSetCode(value)
        this.currentValue = value
        this.dispatch('FormItem', 'on-form-change', this.currentValue)
      }
    },
    computed: {
      classes() {
        const { prefixCls } = this
        return {
          [`${prefixCls}`]: true,
          [`${prefixCls}-visible`]: this.visible,
          [`${prefixCls}-disabled`]: this.disabled,
          [`${prefixCls}-show-clear`]: this.showCloseIcon,
          [`${prefixCls}-size-${this.size}`]: !!this.size
        }
      },
      dropItemClass() {
        const { prefixCls } = this
        return (itemCode, selectedCode) => [
          `${prefixCls}-drop-main-item`,
          {
            [`${prefixCls}-drop-main-item-selected`]: itemCode === selectedCode
          }
        ]
      },
      showCloseIcon() {
        return this.currentValue && this.currentValue.length && this.clearable && !this.itemDisabled
      },
      typeLabel() {
        const e = [].concat(r(this.typeText))
        this.provinceCode && (e[0] = this.regionData[this.countryCode][this.provinceCode])
        this.cityCode && (e[1] = this.regionData[this.provinceCode][this.cityCode])
        this.districtCode && (e[2] = this.regionData[this.cityCode][this.districtCode])
        this.streetCode && (e[3] = this.regionData[this.districtCode][this.streetCode])
        return e
      },
      displayInputRender() {
        let e = ''
        const t = [].concat(r(this.currentValue))
        t.length >= 1 && (e += t[0].name)
        t.length >= 2 && (e += `/${t[1].name}`)
        t.length >= 3 && (e += `/${t[2].name}`)
        t.length >= 4 && (e += `/${t[3].name}`)
        return e
      }
    },
    methods: {
      handleSetValue(value) {
        const e = value ?? this.modelValue
        const t = o(this.regionData)
        const n = s(t)
        let i = []
        if (this.dataType === 'all') {
          i = e
        } else if (this.dataType === 'code' || this.dataType === 'name') {
          const r = this.dataType
          let a = void 0
          let c = void 0
          let l = void 0
          let u = void 0
          if (e.length >= 1) {
            a = n.find(item => item[r] === e[0])
            i.push({ code: a.code, name: a.name })
          }
          if (e.length >= 2) {
            c = a.children.find(item => item[r] === e[1])
            i.push({ code: c.code, name: c.name })
          }
          if (e.length >= 3) {
            l = c.children.find(item => item[r] === e[2])
            i.push({ code: l.code, name: l.name })
          }
          if (e.length >= 4) {
            u = l.children.find(item => item[r] === e[3])
            i.push({ code: u.code, name: u.name })
          }
        }
        return i
      },
      handleSetCode(e) {
        e.length >= 1 && (this.provinceCode = e[0].code)
        e.length >= 2 && (this.cityCode = e[1].code)
        e.length >= 3 && (this.districtCode = e[2].code)
        e.length >= 4 && (this.streetCode = e[3].code)
      },
      handleVisibleChange(e) {
        this.visible = e
      },
      handleClickOutside(e) {
        this.$refs.region.contains(e.target) || (this.visible = false)
      },
      handleToggleOpen() {
        if (this.itemDisabled) {
          return false
        }
        this.visible = !this.visible
      },
      clearSelect() {
        if (this.itemDisabled) {
          return false
        }
        this.handleChangeValue([])
        this.handleInitData()
        this.$emit('on-clear')
      },
      handleInitData() {
        this.provinceCode = ''
        this.cityCode = ''
        this.districtCode = ''
        this.streetCode = ''
        this.currentType = 'province'
      },
      handleClose() {
        this.visible = false
      },
      handleSelectProvince(code) {
        this.provinceCode = code
        this.cityCode = ''
        this.districtCode = ''
        this.streetCode = ''
        this.type === 'province' ? this.handleFinishSelect() : (this.currentType = 'city')
      },
      handleSelectCity(code) {
        this.cityCode = code
        this.districtCode = ''
        this.streetCode = ''
        this.type === 'city' ? this.handleFinishSelect() : (this.currentType = 'district')
      },
      handleSelectDistrict(code) {
        this.districtCode = code
        this.streetCode = ''
        this.type === 'district'
          ? this.handleFinishSelect()
          : this.regionData[this.districtCode]
          ? (this.currentType = 'street')
          : this.handleFinishSelect()
      },
      handleSelectStreet(code) {
        this.streetCode = code
        this.handleFinishSelect()
      },
      handleFinishSelect() {
        const e = []
        this.provinceCode &&
          e.push({
            code: this.provinceCode,
            name: this.regionData[this.countryCode][this.provinceCode]
          })
        this.cityCode &&
          e.push({
            code: this.cityCode,
            name: this.regionData[this.provinceCode][this.cityCode]
          })
        this.districtCode &&
          e.push({
            code: this.districtCode,
            name: this.regionData[this.cityCode][this.districtCode]
          })
        this.streetCode &&
          e.push({
            code: this.streetCode,
            name: this.regionData[this.districtCode][this.streetCode]
          })
        this.handleChangeValue(e)
      },
      handleChangeValue(e) {
        this.currentValue = e
        this.handleClose()
        let t = void 0
        this.dataType === 'all' ? (t = e) : this.dataType === 'code' ? (t = e.map(e => e.code)) : this.dataType === 'name' && (t = e.map(e => e.name))
        this.$emit('update:modelValue', t)
        this.$emit('on-change', e)
        this.dispatch('FormItem', 'on-form-change', e)
      }
    }
  }
</script>
