<template>
  <Modal
    v-if="isModal"
    ref="modal"
    :transfer="true"
    v-model="currentValue"
    v-bind="$attrs"
    class-name="ivu-modal-vertical-center"
    @on-close="handleCancel"
  >
    <template v-for="(value, name) in $slots" #[name]>
      <slot :name="name" />
    </template>
  </Modal>
  <Drawer v-else v-model="currentValue" :transfer="false" :styles="drawerStyle" v-bind="$attrs" @on-close="handleCancel">
    <template v-for="(value, name) in $slots" #[name]>
      <slot :name="name" />
      <div class="ivu-dialog-container-footer" :key="name">
        <Space>
          <slot name="footer">
            <Button @click="handleCancel">取消</Button>
            <Button type="primary" @click="handleOk">确定</Button>
          </slot>
        </Space>
      </div>
    </template>
  </Drawer>
</template>

<script>
  import { Button, Drawer, Modal } from 'view-ui-plus'
  import Space from '../Space'

  const DialogContainerType = {
    DRAWER: 'drawer',
    MODAL: 'modal'
  }
  export default {
    name: 'DialogContainer',
    emits: ['update:modelValue', 'on-ok', 'on-cancel'],
    components: {
      Space,
      Button,
      Drawer,
      Modal
    },
    props: {
      type: {
        type: String,
        validator: value => ['drawer', 'modal'].includes(value),
        default: undefined
      },
      modelValue: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        clientHeight: document.body.clientHeight,
        currentValue: this.modelValue
      }
    },
    watch: {
      modelValue(newVal) {
        this.currentValue = newVal
        if (newVal) {
          this.setModalBodyStyle()
        }
      }
    },
    computed: {
      drawerStyle() {
        const hasTitle = !!this.$attrs.title
        return {
          position: 'static',
          height: 'calc(100% - 55px)',
          overflow: 'auto',
          paddingBottom: hasTitle ? '77px' : '22px'
        }
      },
      isModal() {
        const dialogType = this.type || this.$store.state.app.dialogType
        return DialogContainerType.MODAL === dialogType
      }
    },
    mounted() {
      window.addEventListener('resize', () => this.getClientHeight())
    },
    beforeUnmount() {
      window.removeEventListener('resize', this.getClientHeight())
    },
    methods: {
      getClientHeight() {
        this.clientHeight = document.body.clientHeight
      },
      setModalBodyStyle() {
        if (this.isModal) {
          const modalBody = this.$refs.modal.$el.querySelector('.ivu-modal-body')
          modalBody.style.height = `${this.clientHeight - 200}px`
          modalBody.style.overflow = 'auto'
        }
      },
      handleCancel() {
        this.$emit('update:modelValue', false)
        this.$emit('on-cancel')
      },
      handleOk() {
        this.$emit('on-ok')
      }
    }
  }
</script>
<style lang="less">
  .ivu-dialog-container-footer {
    width: 100%;
    position: absolute;
    left: 0;
    bottom: 0;
    border-top: 1px solid #e8e8e8;
    padding: 10px 16px;
    text-align: right;
    background: #fff;
  }

  .ivu-modal-vertical-center {
    display: flex;
    align-items: center;
    justify-content: center;

    .ivu-modal {
      top: 0;
    }
  }
</style>
