<template>
  <div :class="wrapClasses" :style="styles">
    <Tabs
      type="card"
      :animated="false"
      :modelValue="activePath"
      :before-remove="beforeRemoveTab"
      @on-click="handleClickTab"
      @on-tab-remove="handleRemoveTab"
      @on-contextmenu="handleContextMenu"
    >
      <TabPane
        v-for="(item, index) in tabsNavList"
        :key="`${item.fullPath}-${index}`"
        :name="item.fullPath"
        :label="h => tabPaneLabel(h, item)"
        :closable="item.meta && item.meta.closable !== false"
        :context-menu="true"
      />
      <template #contextMenu>
        <DropdownItem @click="clickContextMenu(`fullscreen`)">
          <Icon :type="fullContent ? `md-contract` : `md-expand`" />
          <span class="ivu-ml-4">{{ fullContent ? `退出全屏` : `内容全屏` }}</span>
        </DropdownItem>
        <DropdownItem @click="clickContextMenu(`reloadTabs`)" v-if="contextTab && contextTab.name === activePath">
          <Icon type="md-refresh" />
          <span class="ivu-ml-4">刷新页面</span>
        </DropdownItem>
        <DropdownItem @click="clickContextMenu(`closeLeft`)">
          <Icon type="md-arrow-back" />
          <span class="ivu-ml-4">关闭左侧</span>
        </DropdownItem>
        <DropdownItem @click="clickContextMenu(`closeRight`)">
          <Icon type="md-arrow-forward" />
          <span class="ivu-ml-4">关闭右侧</span>
        </DropdownItem>
        <DropdownItem @click="clickContextMenu(`closeOther`)">
          <Icon type="md-close" />
          <span class="ivu-ml-4">关闭其它</span>
        </DropdownItem>
        <DropdownItem @click="clickContextMenu(`closeAll`)">
          <Icon type="md-close-circle" />
          <span class="ivu-ml-4">全部关闭</span>
        </DropdownItem>
      </template>
    </Tabs>
    <Dropdown :class="`${prefixCls}-close`" placement="bottom-end" @on-click="clickMenuItem">
      <div :class="`${prefixCls}-close-main`">
        <Icon type="ios-arrow-down" />
      </div>
      <template #list>
        <DropdownMenu>
          <DropdownItem name="fullscreen">
            <Icon :type="fullContent ? `md-contract` : `md-expand`" />
            <span class="ivu-ml-4">{{ fullContent ? `退出全屏` : `内容全屏` }}</span>
          </DropdownItem>
          <DropdownItem name="reloadTabs">
            <Icon type="md-refresh" />
            <span class="ivu-ml-4">刷新页面</span>
          </DropdownItem>
          <DropdownItem name="closeLeft">
            <Icon type="md-arrow-back" />
            <span class="ivu-ml-4">关闭左侧</span>
          </DropdownItem>
          <DropdownItem name="closeRight">
            <Icon type="md-arrow-forward" />
            <span class="ivu-ml-4">关闭右侧</span>
          </DropdownItem>
          <DropdownItem name="closeOther">
            <Icon type="md-close" />
            <span class="ivu-ml-4">关闭其它</span>
          </DropdownItem>
          <DropdownItem name="closeAll">
            <Icon type="md-close-circle" />
            <span class="ivu-ml-4">全部关闭</span>
          </DropdownItem>
        </DropdownMenu>
      </template>
    </Dropdown>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'
  import Sortable from 'sortablejs'
  import event from './event'
  import { mixinState, mixinDevice } from '@/utils/mixin'
  import { setTabsNavList, setFullContent } from '@/store/mutation-types'

  export default {
    name: 'TabsNav',
    inject: ['BasicLayout'],
    mixins: [mixinState, mixinDevice],
    props: {
      hideSider: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        prefixCls: `${this.$config.cssPrefix}tabs`,
        scrollArea: document.getElementById('app'),
        tabPathList: [],
        scrollTop: 0,
        activePath: undefined,
        contextTab: undefined,
        sortTabs: undefined
      }
    },
    watch: {
      $route(newRoute) {
        this.activePath = newRoute.fullPath
        if (!this.tabPathList.includes(newRoute.fullPath)) {
          this.setTabNavList([...this.tabsNavList, newRoute])
        }
      },
      activePath(newPath) {
        if (this.tabPathList.includes(newPath)) {
          const { name, path, query, params } = this.tabsNavList.find(item => item.fullPath === newPath)
          this.$router.push({
            name,
            path,
            query,
            params
          })
        } else {
          this.$router.push({ path: newPath || this.homeRoute.path })
        }
      },
      tabsNavList(newNavList) {
        this.tabPathList = newNavList.map(item => item.fullPath)
      },
      updateScroll(newBool) {
        if (newBool) {
          this.scrollArea.addEventListener('scroll', this.handleScroll, {
            passive: true
          })
        } else {
          this.scrollArea.removeEventListener('scroll', this.handleScroll)
        }
      },
      tabSortable(newValue) {
        if (newValue) this.bindDragSort()
        else this.unbindDragSort()
      }
    },
    computed: {
      ...mapGetters(['openTabs', 'homeRoute']),
      updateScroll() {
        return this.tabsNavFix && !this.headerFix
      },
      wrapClasses() {
        const { prefixCls } = this
        return [
          prefixCls,
          {
            [`${prefixCls}-fix`]: this.tabsNavFix,
            [`${prefixCls}-dot`]: this.tabsNavStyle === 'dot',
            [`${prefixCls}-fix-expanded`]: !this.isMobile && !this.isTopMenu && !this.fullContent && this.tabsNavFix && !this.menuCollapse,
            [`${prefixCls}-fix-collapse`]: !this.isMobile && !this.isTopMenu && !this.fullContent && this.tabsNavFix && this.menuCollapse,
            [`${prefixCls}-fix-hide-sider`]: this.tabsNavFix && this.hideSider,
            [`${prefixCls}-${this.contentWidth}`]: this.isDesktop && this.isTopMenu && !this.fullContent
          }
        ]
      },
      styles() {
        const style = {}
        const { layoutHeaderHeight } = this.$config
        this.tabsNavFix && !this.headerFix && (style.top = `${layoutHeaderHeight - this.scrollTop}px`)
        return style
      }
    },
    mounted() {
      // this.bindEvent()
      this.bindDragSort()
      this.activePath = this.$route.fullPath
      if (this.tabsNavList.length) {
        this.tabPathList = this.tabsNavList.map(item => item.fullPath)
      } else {
        this.setTabNavList([...this.tabsNavList, this.$route])
      }
    },
    methods: {
      bindEvent() {
        event
          .$on('open', fullPath => {
            if (fullPath) this.activePath = fullPath
            else this.$Message.error('请输入标签页fullPath')
          })
          .$on('close', fullPath => {
            this.closeThat(fullPath || this.activePath)
          })
          .$on('rename', ({ fullPath, tabTitle }) => {
            const item = this.tabsNavList.find(item => item.fullPath === fullPath)
            item.meta.customTitle = tabTitle
            this.setTabNavList(this.tabsNavList)
          })
      },
      tabPaneLabel(h, item) {
        const renderDot = h('span', { class: 'ivu-tag-dot-inner' })
        const renderIcon = h('XIcon', {
          props: {
            type: (this.tabsNavIcon && item.meta && item.meta.icon) || ''
          }
        })
        const renderSpan = h('span', item.meta ? item.meta.customTitle || item.meta.title : '未命名')
        const renderNode = this.tabsNavStyle === 'dot' ? renderDot : renderIcon
        return h('span', {}, [renderNode, renderSpan])
      },
      beforeRemoveTab(index) {
        const currentTab = this.tabsNavList[index]
        if (currentTab && currentTab.meta && currentTab.meta.beforeFn) {
          const beforeFn = currentTab.meta.beforeFn
          return new Promise(beforeFn)
        }
      },
      handleContextMenu(data, event, position) {
        this.contextTab = data
      },
      clickMenuItem(key) {
        this[key](this.activePath)
      },
      clickContextMenu(key) {
        this[key](this.contextTab.name)
      },
      fullscreen(e) {
        this.$store.commit(setFullContent, !this.fullContent)
      },
      setTabNavList(tabsNavList) {
        this.$store.dispatch(setTabsNavList, tabsNavList)
      },
      handleClickTab(activePath) {
        if (this.activePath === activePath) {
          this.tabsReload && this.reloadTabs()
        } else {
          this.activePath = activePath
        }
      },
      handleRemoveTab(targetPath) {
        const newPaneList = this.tabsNavList.filter(item => item.fullPath !== targetPath)
        const activeIndex = this.tabPathList.findIndex(item => item === targetPath)
        this.setTabNavList(newPaneList)
        if (!newPaneList.length) this.$router.push({ path: this.homeRoute.path })
        this.tabPathList = this.tabPathList.filter(path => path !== targetPath)
        if (!this.tabPathList.includes(this.activePath)) {
          const nextPathIndex = this.tabPathList.length <= activeIndex ? this.tabPathList.length - 1 : activeIndex
          this.activePath = this.tabPathList[nextPathIndex]
        }
      },
      isClosableTab(fullPath) {
        return !this.openTabs.some(tab => tab.fullPath === fullPath)
      },
      closeThat(fullPath) {
        if (this.isClosableTab(fullPath)) {
          this.handleRemoveTab(fullPath)
          return
        }
        const currentTab = this.openTabs.find(item => item.fullPath === fullPath)
        this.$Message.info(`${currentTab.meta.title}不能关闭`)
      },
      closeLeft(tabPath) {
        const currentIndex = this.tabPathList.indexOf(tabPath)
        const hasRemoveTab = this.tabPathList.filter((item, index) => index < currentIndex && this.isClosableTab(item))
        if (!hasRemoveTab.length) {
          this.$Message.info('左侧没有可关闭的页签')
          return
        }
        hasRemoveTab.forEach(item => this.handleRemoveTab(item))
      },
      closeRight(tabPath) {
        const currentIndex = this.tabPathList.indexOf(tabPath)
        const hasRemoveTab = this.tabPathList.filter((item, index) => index > currentIndex && this.isClosableTab(item))
        if (!hasRemoveTab.length) {
          this.$Message.info('右侧没有可关闭的页签')
          return
        }
        hasRemoveTab.forEach(item => this.handleRemoveTab(item))
      },
      closeOther(tabPath) {
        const hasRemoveTab = this.tabPathList.filter(item => item !== tabPath && this.isClosableTab(item))
        if (!hasRemoveTab.length) {
          this.$Message.info('没有可关闭的页签')
          return
        }
        hasRemoveTab.forEach(item => this.handleRemoveTab(item))
      },
      closeAll() {
        const removeItem = this.tabPathList.filter(item => this.isClosableTab(item))
        if (!removeItem.length) {
          this.$Message.info('没有可关闭的页签')
          return
        }
        removeItem.forEach(item => this.handleRemoveTab(item))
      },
      reloadTabs() {
        this.BasicLayout.handleReload()
      },
      handleScroll() {
        if (this.updateScroll) {
          const height = this.scrollArea.scrollTop
          const { layoutHeaderHeight } = this.$config
          this.scrollTop = height > layoutHeaderHeight ? layoutHeaderHeight : height
        }
      },
      bindDragSort() {
        this.unbindDragSort()
        if (this.tabSortable) {
          const el = this.$el.querySelector('.ivu-tabs-nav')
          this.sortTabs = new Sortable(el, {
            animation: 300,
            draggable: '.ivu-tabs-tab',
            onUpdate: ({ oldDraggableIndex, newDraggableIndex }) => {
              if (typeof oldDraggableIndex === 'number' && typeof newDraggableIndex === 'number') {
                const newTabs = [...this.tabsNavList]
                newTabs.splice(newDraggableIndex, 0, newTabs.splice(oldDraggableIndex, 1)[0])
              }
            },
            setData: () => {}
          })
        }
      },
      unbindDragSort() {
        this.sortTabs && this.sortTabs.destroy()
        this.sortTabs = null
      }
    },
    beforeUnmount() {
      this.unbindDragSort()
    }
  }
</script>
