<template>
  <div class="vxe-loading is--visible vxe-table--loading">
    <div class="vxe-loading--chunk">
      <i class="vxe-icon-spinner roll vxe-loading--default-icon"></i>
      <div class="vxe-loading--text">{{ text }}</div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'VxeLoading',
    props: {
      text: {
        type: String,
        default: '加载中...'
      }
    }
  }
</script>

<style lang="less" scoped>
  @import 'vxe-table.css';
  .vxe-loading {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 99;
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
    background-color: hsla(0, 0%, 100%, 0.5);

    & > .vxe-loading--chunk {
      width: 100%;
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      text-align: center;
      color: #409eff;
    }

    & > .vxe-loading--default-icon {
      font-size: 1.4em;
    }

    .roll {
      display: inline-block;
      animation: rollCircle 1s linear infinite;
    }

    & > .vxe-loading--text {
      padding: 0.4em 0;
    }
  }
</style>
